import type {CSSProperties} from 'react'

import type {CalendarEventItem} from '@bpinc/cal-request-urls'
import type {DragInfo} from '@bpinc/cal-state-types'
import {MS_IN_DAY, MS_IN_HOUR, MS_IN_MINUTE} from '@bpinc/lib-helpers'

import {getRangeInDays, MINUTES_IN_CELL, MS_IN_CELL} from './dates'


interface GetHeaderEventStyles extends GetEventStyles {
    viewEnd: number
}

interface GetEventStyles {
    viewStart: number
    viewEnd: number
    cellSize: [number, number]
    start: number
    end: number
    line: number
    lines: number
}1 | 7

// 24 hours x 2 event cells per hour
export const CELLS_IN_DAY = 24 * 2

const EVENT_PADDING = 4
const EVENT_RIGHT_MARGIN = 16
const CELL_HEIGHT = 24

export const getWeekHeaderCellIndex = (timestamp: number, startOfWeek = 0) => {
    return Math.floor((timestamp - startOfWeek) / MS_IN_DAY)
}

export const getWeekCellIndices = (timestamp: number, startOfWeek = 0) => {
    const dayIndex = Math.floor((timestamp - startOfWeek) / MS_IN_DAY)
    const startDate = new Date(timestamp)
    const h = startDate.getHours()
    const m = startDate.getMinutes()
    const cellIndex = h * 2 + Math.floor(m / MINUTES_IN_CELL)
    return [dayIndex, cellIndex]
}

export function getEventStyles(options: GetEventStyles) {
    const {
        viewStart,
        viewEnd,
        cellSize: [_, cellHeight],
        start,
        end,
        line,
        lines,
    } = options
    const dateToHeight = (t: number) => {
        const [, cellIndex] = getWeekCellIndices(t)
        return cellIndex * cellHeight
    }

    const styles: CSSProperties = {}
    styles.width = `calc((100% - ${EVENT_RIGHT_MARGIN}px) / ${lines})`
    styles.left = `calc(${styles.width} * ${line} + ${line}px)`
    styles.top = start <= viewStart ? 0 : dateToHeight(start)
    const height = (end >= viewEnd ? cellHeight * 24 * 2 : dateToHeight(end) - 1) - styles.top
    // min height event - 30-minutes event
    styles.height = height >= CELL_HEIGHT ? height : CELL_HEIGHT - 1
    return styles
}

export function getHeaderEventStyles(options: GetHeaderEventStyles) {
    const {
        viewStart,
        viewEnd,
        cellSize: [cellWidth, cellHeight],
        start,
        end,
        line,
        lines,
    } = options
    const styles: CSSProperties = {}
    const beforeViewStart = Math.min(start, end) < viewStart

    const days = getRangeInDays(
        new Date(Math.max(start, viewStart)),
        new Date(Math.min(viewEnd, end)),
    )

    const daysFromWeekStart = getRangeInDays(new Date(viewStart), new Date(start)) - 1

    styles.bottom = EVENT_PADDING / 2 + (lines ? CELL_HEIGHT * (lines - line - 1) : 0)

    styles.height = cellHeight - EVENT_PADDING

    styles.width = days * cellWidth - EVENT_RIGHT_MARGIN
    styles.left = beforeViewStart ? EVENT_PADDING / 2 : cellWidth * daysFromWeekStart

    styles.backgroundColor = 'var(--c-calendar-day-event-bg)'
    styles.borderLeftColor = 'var(--c-calendar-day-event-border)'

    return styles
}

export const getDragItemShift = (dragItemStart: number, dragInfo: DragInfo, startOfWeek: number) => {
    const [dayIndex, cellIndex] = getWeekCellIndices(dragItemStart, startOfWeek)
    const [dragDayIndex, dragCellIndex] = dragInfo.cell
    const shiftHours = (dragDayIndex - dayIndex) * MS_IN_DAY
    const shiftMinutes = (dragCellIndex - cellIndex) * MS_IN_CELL
    return shiftHours + shiftMinutes
}

export const getDateFromCell = (cell: Element, start: number) => {
    const x = cell.getAttribute('data-x')
    const y = cell.getAttribute('data-y')
    if (x && y) {
        const dayIndex = Number(x)
        const cellIndex = Number(y)
        return getDateByCellIndex(start, dayIndex, cellIndex)
    }
}

export const getDateByCellIndex = (
    start: number,
    dayIndex: number,
    cellIndex: number,
) => {
    const startOfDay = start + MS_IN_DAY * dayIndex
    const hours = Math.floor(cellIndex / 2)
    const minutes = cellIndex % 2 ? MINUTES_IN_CELL : 0

    return new Date(startOfDay + hours * MS_IN_HOUR + minutes * MS_IN_MINUTE)
}

export const dragCalendarEvent = (
    event: CalendarEventItem,
    shift: number,
): CalendarEventItem => {
    return {
        ...event,
        start: event.start + shift,
        end: event.end + shift,
    }
}

export const resizeCalendarEvent = (
    event: CalendarEventItem,
    shift: number,
): CalendarEventItem => {
    let [eventStart, eventEnd] = [event.start, event.end + shift]

    if (eventStart > eventEnd) [eventStart, eventEnd] = [eventEnd, eventStart]
    if (eventStart === eventEnd) eventEnd += MS_IN_CELL

    return {
        ...event,
        start: eventStart,
        end: eventEnd,
    }
}
