import type {SyntheticEvent} from 'react'
import React from 'react'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {useCalendarState} from '@bpinc/cal-state-context'
import {View} from '@bpinc/cal-state-types'
import type {SmMenuItemProps} from '@bpinc/lib-external-ui-elements'
import {SmMenu} from '@bpinc/lib-external-ui-elements'
import {
    IconCalendarViewDay,
    IconCalendarViewMonth,
    IconCalendarViewSchedule,
    IconCalendarViewWeek,
} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {SVGRComponent} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useHandleViewChange} from '../hooks/useHandleViewChange'


export const CalendarViewSelect: React.FC = () => {
    const {t} = useLocalizationState()
    const view = useCalendarState('view')
    const handleViewChange = useHandleViewChange()
    const isCommunicator = useIsCommunicator()

    return (
        <SmMenu compact className="min-h-0 shadow-none">
            <ViewButton
                view={View.MONTH}
                active={view === View.MONTH}
                onClick={handleViewChange}
                Icon={IconCalendarViewMonth}
                title={t.CALENDAR.month()}
                isCommunicator={isCommunicator}
            />

            <ViewButton
                view={View.WEEK}
                active={view === View.WEEK}
                onClick={handleViewChange}
                Icon={IconCalendarViewWeek}
                title={t.CALENDAR.week()}
                isCommunicator={isCommunicator}
            />

            <ViewButton
                view={View.DAY}
                active={view === View.DAY}
                onClick={handleViewChange}
                Icon={IconCalendarViewDay}
                title={t.CALENDAR.day()}
                isCommunicator={isCommunicator}
            />

            <ViewButton
                view={View.SCHEDULE}
                active={view === View.SCHEDULE}
                onClick={handleViewChange}
                Icon={IconCalendarViewSchedule}
                title={t.CALENDAR.schedule()}
                isCommunicator={isCommunicator}
            />
        </SmMenu>
    )
}

type ViewButtonProps = {
    view: View
    active: boolean
    onClick: (e: SyntheticEvent, data: SmMenuItemProps) => void
    Icon: SVGRComponent
    title: string
    isCommunicator: boolean
}

const ViewButton: React.FC<ViewButtonProps> = ({
    view,
    active,
    onClick,
    Icon,
    title,
    isCommunicator,
}) => {
    return (
        <SmMenu.Item
            as="button"
            view={view}
            active={active}
            title={title}
            onClick={onClick}
            className={tcn('px-4 py-[9px]', {
                'h-8 w-8 px-[7px]': isCommunicator,
            })}
        >
            {isCommunicator ?
                <Icon className="[&>svg]:h-3.5 [&>svg]:w-4.5" /> :
                title
            }
        </SmMenu.Item>
    )
}
