import {useEffect} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'


export function useEventDetails(eventId: string) {
    const {calendarEventDetails, calendarEventDetailsLoading} = useCalendarState(
        ['calendarEventDetails', 'calendarEventDetailsLoading'],
    )
    const {getCalendarEventDetails} = useCalendarStateMethods()

    const eventDetails = calendarEventDetails[eventId]
    const loading = !!calendarEventDetailsLoading[eventId]

    useEffect(() => {
        if (!eventDetails) {
            getCalendarEventDetails(eventId)
        }
    }, [eventDetails, eventId, getCalendarEventDetails])

    return {eventDetails, loading}
}
