import React from 'react'
import {DndProvider} from 'react-dnd'
import {HTML5Backend} from 'react-dnd-html5-backend'

import {useCalendarState} from '@bpinc/cal-state-context'
import {View} from '@bpinc/cal-state-types'

import {useEventsLoader} from '../../hooks/useEventsLoader'
import {useRequestsLoader} from '../../hooks/useRequestsLoader'
import {useShiftsLoader} from '../../hooks/useShiftsLoader'
import {CalendarBodyLoader} from './CalendarBodyLoader'
import {ViewColumns} from './ViewColumns'
import {ViewMonth} from './ViewMonth'
import {ViewSchedule} from './ViewSchedule'


export const CalendarBody = React.memo(() => {
    useEventsLoader()
    useRequestsLoader()
    useShiftsLoader()

    const {view, calendarEventsLoading: isLoading} = useCalendarState([
        'view',
        'calendarEventsLoading',
    ])

    return (
        <DndProvider backend={HTML5Backend}>
            <div className="relative flex flex-1 flex-col">
                {view === View.MONTH && <ViewMonth />}

                {(view === View.WEEK || view === View.DAY) && <ViewColumns />}

                {view === View.SCHEDULE && <ViewSchedule />}

                {isLoading && <CalendarBodyLoader />}
            </div>
        </DndProvider>
    )
})
