import type {FC} from 'react'
import React, {useCallback} from 'react'

import {intlFormatters} from '@bpinc/cal-event-ui/src/utils'
import {MS_IN_DAY, MS_IN_HOUR} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useTimezoneDifference} from '../../../../hooks/useTimezoneDifference'


type DayTimeColumnProps = {
    isCommunicator: boolean
}

export const DayTimeColumn: FC<DayTimeColumnProps> = React.memo(({
    isCommunicator,
}) => {
    const timezoneDifferenceInMS = useTimezoneDifference()
    const {language: locale} = useLocalizationState()
    const {h, hhmm, normalize} = intlFormatters({locale, timeZone: 'UTC'})

    const getTimeByIndex = useCallback((index: number, offsetInMS = 0) => {
        if (index === 0 || index % 2) {
            return ''
        }

        const ms = (MS_IN_HOUR * index) / 2
        const date = new Date(MS_IN_DAY + ms - offsetInMS)
        const hFormat = h.format(date)
        return new RegExp('AM|PM', 'i').test(hFormat) ? hFormat.toUpperCase() : normalize(hhmm.format(date))
    }, [h, hhmm, normalize])

    return (
        <div className={tcn('w-[52px]', {'w-[57px]': isCommunicator})}>
            {Array.from({length: 24 * 2}, (_, index) => {
                const first = index === 0
                const localTime = getTimeByIndex(index)
                const secondaryTime =
                    timezoneDifferenceInMS &&
                    getTimeByIndex(index, timezoneDifferenceInMS)

                return (
                    <div
                        key={index}
                        className={tcn(
                            'relative h-6 select-none border border-l-0 border-solid border-cal-border',
                            {'border-b-0 border-t': first},
                            {'odd:border-y-0 even:border-t-0': !first},
                        )}
                    >
                        <div
                            className={tcn(
                                'box-border whitespace-nowrap pr-1 text-right text-sm/[16px]',
                                '-translate-x-2 -translate-y-1/2 bg-white uppercase',
                            )}
                        >
                            {localTime}
                            {!!secondaryTime && (
                                <div className="uppercase text-main-secondary">
                                    {secondaryTime}
                                </div>
                            )}
                        </div>
                    </div>
                )
            })}
        </div>
    )
})
