import React, {useEffect, useMemo, useState} from 'react'
import type {Control} from 'react-hook-form'
import {useController} from 'react-hook-form'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {useGetContactPictureUrl} from '@bpinc/ad-contacts-state-hooks'
import type {ContactPhoneType} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import type {CalendarEventExistingContact, CalendarEventFields} from '@bpinc/cal-state-types'
import {AvatarLegacy} from '@bpinc/lib-avatar-ui'
import type {PropsAvatar} from '@bpinc/lib-contact-list-item-ui'
import {HiddenField, SelectFieldRaw} from '@bpinc/lib-forms-ui'

import {ContactPhoneLabel} from './ContactPhoneLabel'


type ExistingContactProps = {
    index: number
    contact: CalendarEventExistingContact
    control: Control<CalendarEventFields>
}

export function ExistingContact({
    contact,
    index,
    control,
}: ExistingContactProps) {
    const {getContact} = useContactsStateMethods()
    const getContactPictureUrl = useGetContactPictureUrl()
    const {formatFullName} = useFormatting()
    const [primaryPhoneId, setPrimaryPhoneId] = useState(buildPhoneId(contact.primaryPhone))
    const [contactPhones, setContactPhones] = useState(() => contact.primaryPhone ? [contact.primaryPhone] : [])
    const [isContactLoading, setContactLoading] = useState(true)

    useEffect(() => {
        getContact({id: contact.id, force: false}).then(loadedContact => {
            if (loadedContact) {
                setContactPhones(loadedContact.phones || [])
                const primaryPhone = (loadedContact.phones || []).find(phone => {
                    return contact.primaryPhone?.phone === phone.phone && contact.primaryPhone.type == phone.type
                }, [])
                setPrimaryPhoneId(buildPhoneId(primaryPhone))

                setContactLoading(false)
            }
        })
    }, [contact.id, contact.primaryPhone, getContact])

    const options = useMemo(() => buildPhoneSelectOptions(contactPhones), [contactPhones])

    const avatar = useMemo<PropsAvatar>(() => (
        {firstName: contact.firstName || '', lastName: contact.lastName || '', url: getContactPictureUrl(contact.id), type: 'contact'}
    ), [contact.firstName, contact.id, contact.lastName, getContactPictureUrl])

    const {field: phoneIdField} = useController({
        control,
        name: `contacts.${index}.primaryPhone.id`,
        defaultValue: buildPhoneId(contact.primaryPhone),
    })
    const {field: {onChange: onChangePhone}} = useController({
        control,
        name: `contacts.${index}.primaryPhone`,
    })

    const {value: phoneId, onChange: onChangePrimaryPhoneId} = phoneIdField

    useEffect(() => {
        const contactPhone = contactPhones.find(phone => buildPhoneId(phone) === phoneId)
        onChangePhone(contactPhone)
    }, [contactPhones, onChangePhone, phoneId])

    useEffect(() => {
        onChangePrimaryPhoneId(primaryPhoneId)
    }, [onChangePrimaryPhoneId, primaryPhoneId])

    return (
        <>
            <HiddenField
                control={control}
                name={`contacts.${index}.id`}
                defaultValue={contact.id}
            />
            <HiddenField
                control={control}
                name={`contacts.${index}.firstName`}
                defaultValue={contact.firstName}
            />
            <HiddenField
                control={control}
                name={`contacts.${index}.lastName`}
                defaultValue={contact.lastName}
            />
            <div className="flex flex-row items-center">
                <AvatarLegacy size={32} {...avatar} />
                <div className="ml-2 min-w-0 flex-initial">
                    <div className="truncate text-lg/[23px] font-bold">
                        {formatFullName(contact.firstName, contact.lastName)}
                    </div>
                    {contact.primaryPhone && (
                        <SelectFieldRaw
                            field={phoneIdField}
                            inline={false}
                            options={options}
                            selection={false}
                            loading={isContactLoading}
                        />
                    )}
                </div>
            </div>
        </>

    )
}

function buildPhoneSelectOptions(phones: Pick<ContactPhoneType, 'type' | 'phone'>[]) {
    return phones.map(phone => {
        const id = buildPhoneId(phone)
        return {
            key: id,
            text: <ContactPhoneLabel phone={phone.phone} type={phone.type} />,
            value: id,
        }
    })
}

function buildPhoneId(phone?: Pick<ContactPhoneType, 'type' | 'phone'>) {
    if (!phone) {
        return undefined
    }
    return phone.type + phone.phone
}

