import React from 'react'

import type {NotEmptyEventMapItem} from '@bpinc/cal-state-types'

import type {ViewMonthEventProps} from './ViewMonthEvent'
import {ViewMonthEventBlock} from './ViewMonthEventBlock'


type ViewMonthRequestProps = Omit<ViewMonthEventProps, 'item'> & {
    item: NotEmptyEventMapItem
}

export const ViewMonthRequest: React.FC<ViewMonthRequestProps> = props => {
    return (
        <ViewMonthEventBlock
            title={props.item.data.title}
            className="border-l-cal-request-border bg-cal-request-bg"
            {...props}
        />
    )
}
