export enum RequestType {
    VACATION = 'Vacation',
    PTO = 'PTO',
}

export enum RequestStatus {
    PENDING = 'pending',
    WAITLISTED = 'waitlisted',
    APPROVED = 'approved',
    DENIED = 'denied',
}

export enum ApprovalType {
    SYSTEM = 'system',
    SUPERVISOR = 'supervisor',
}

type RequestCommon = {
    id: string
    type: RequestType
    reqTimeStart: number
    reqTimeEnd: number
    submissionDate: number
    comment?: string
}

type PendingRequest = RequestCommon & {
    status: RequestStatus.PENDING
}

type WaitlistedRequest = RequestCommon & {
    status: RequestStatus.WAITLISTED
    approvalDate: number
}

export type DeniedRequest = RequestCommon & {
    status: RequestStatus.DENIED
    approvalDate: number
    approvalType: ApprovalType
    approvedBy?: string
    decisionDetails?: string
}

export type ApprovedRequest = RequestCommon & {
    status: RequestStatus.APPROVED
    approvalDate: number
    approvalType: ApprovalType
    approvedBy?: string
    decisionDetails?: string
}

export type Request = PendingRequest | WaitlistedRequest | DeniedRequest | ApprovedRequest
