import {useCallback} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'

import {useSetNewSelectedCalendarEvent} from './useSetNewSelectedCalendarEvent'
import {useSetSelectedCalendarEvent} from './useSetSelectedCalendarEvent'


export type SelectEventProps =
 | {id: string}
 | {startTime: number; endTime: number}

 /**
  * Selects a calendar event by id or creates new one. Sends event to calendarEventMachine
  */
export const useSelectEvent = () => {
    const {
        calendarEvents,
        calendarEventDetails,
    } = useCalendarState([
        'calendarEvents',
        'calendarEventDetails',
    ])
    const {
        getCalendarEventDetails,
        selectEvent,
    } = useCalendarStateMethods()
    const setNewSelectedEvent = useSetNewSelectedCalendarEvent()
    const setSelectedEvent = useSetSelectedCalendarEvent()

    return useCallback((args: SelectEventProps) => {
        if ('id' in args) {
            findOrFetchEvent(args.id)
        } else {
            setNewSelectedEvent(args.startTime, args.endTime)
        }

        function findOrFetchEvent(eventId: string) {
            const foundEventDetails = calendarEventDetails[eventId]
            if (foundEventDetails) {
                selectEvent(foundEventDetails)
                return
            }

            const event = calendarEvents.find(({id}) => id === eventId)
            if (event) {
                setSelectedEvent(event)
            }

            getCalendarEventDetails(eventId)
                .then(fetchedEventDetails => {
                    if (fetchedEventDetails) {
                        selectEvent(fetchedEventDetails)
                    }
                })
        }
    }, [
        calendarEvents,
        selectEvent,
        setNewSelectedEvent,
        setSelectedEvent,
        calendarEventDetails,
        getCalendarEventDetails,
    ])
}
