import React, {useMemo} from 'react'
import type {Control} from 'react-hook-form'

import {useTimezonesState} from '@bpinc/ad-timezones-state-context'
import type {CalendarEventFields} from '@bpinc/cal-state-types'
import {SelectField} from '@bpinc/lib-forms-ui'


type TimezoneSelectProps = {
    control: Control<CalendarEventFields>
}

export const TimezoneSelect: React.FC<TimezoneSelectProps> = ({control}) => {
    const {allTimezones} = useTimezonesState()

    const timezoneOptions = useMemo(
        () => allTimezones.map(({id, fullName}) => ({
            key: id,
            text: fullName,
            value: id,
            className: 'max-w-[270px]',
            title: fullName,
        })),
        [allTimezones],
    )

    return (
        <SelectField
            tabIndex={0}
            control={control}
            name="timezone"
            options={timezoneOptions}
            search
            selection={false}
        />
    )
}
