import React, {
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from 'react'
import {isEqual} from 'lodash-es'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {useSelectEvent} from '@bpinc/cal-hooks'
import type {CalendarEventItem} from '@bpinc/cal-request-urls'
import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import type {DragInfo, ResizeInfo} from '@bpinc/cal-state-types'
import {DefaultCalendars, EventType} from '@bpinc/cal-state-types'
import {
    dragCalendarEvent,
    eventDetailsToCalendarEvent,
    eventsToEventMap,
    getDragItemShift,
    getEventEndByStart,
    getRangeFromView,
    getSortedSelectionRange,
    requestsToEventMap,
    resizeCalendarEvent,
    shiftsToEventMap,
} from '@bpinc/cal-utils'
import {DAYS_IN_WEEKEND, useAsRef, useEventListener, usePrevious} from '@bpinc/lib-helpers'

import {useAllDayEventsByLine, useBodyEventsList} from '../../../hooks/useEventsList'
import {useIsStartFromMonday} from '../../../hooks/useIsStartFromMonday'
import {useSelectionRange} from '../../../hooks/useSelectionRange'
import {useShowWeekends} from '../../../hooks/useShowWeekends'
import {HeaderEvents} from './Header/HeaderEvents'
import {HeaderTable} from './Header/HeaderTable'
import {DayEvents} from './DayEvents'
import {DaysColumns} from './DaysColumns'


export type WeekDropResult = {
    onEnd: () => void
}

const INITIAL_CELL_SIZE: [number, number] = [0, 0]
const HEADER_CELL_HEIGHT = 24

export const ViewColumns: React.FC = () => {
    const [dragInfo, setDragInfo] = useState<DragInfo>()
    const [resizeInfo, setResizeInfo] = useState<ResizeInfo>()
    const dragInfoRef = useAsRef(dragInfo)
    const resizeInfoRef = useAsRef(resizeInfo)

    const {
        calendarFilters,
        selectedEvent,
        calendarEvents,
        timeoffRequests,
        shifts,
        view,
        viewDate,
        viewDaysAmount,
    } = useCalendarState([
        'calendarFilters',
        'selectedEvent',
        'calendarEvents',
        'timeoffRequests',
        'shifts',
        'view',
        'viewDate',
        'viewDaysAmount',
    ])
    const {closeEventForm} = useCalendarStateMethods()

    const isCommunicator = useIsCommunicator()
    const isStartFromMonday = useIsStartFromMonday()
    const showWeekends = useShowWeekends()

    const [cellSize, setCellSize] = useState<[number, number]>(INITIAL_CELL_SIZE)
    const cellSizeInitialized = cellSize[0] !== INITIAL_CELL_SIZE[0]
        || cellSize[1] !== INITIAL_CELL_SIZE[1]
    // start, end of week / day view
    const [start, end] = useMemo(() =>
        getRangeFromView(viewDate, view,
            {
                weekStartsFromMonday: isStartFromMonday,
                withoutWeekend: !showWeekends,
            })
    , [view, viewDate, isStartFromMonday, showWeekends])
    const selectEvent = useSelectEvent()
    const {
        moveCalendarEvent,
        stretchCalendarEvent,
        updateSelectedEvent,
    } = useCalendarStateMethods()
    const [selectionRange, setSelectionRange] = useSelectionRange()
    const tableWrapperRef = useRef<HTMLDivElement>(null)

    const prevSelectedEvent = usePrevious(selectedEvent)
    const isSelectedEventsEqual = useMemo(() =>
        isEqual(selectedEvent, prevSelectedEvent)
    , [selectedEvent, prevSelectedEvent])

    // We need to reset dnd info only when selected event updating is finished
    useEffect(() => {
        if ((dragInfo || resizeInfo) && !isSelectedEventsEqual) {
            setDragInfo(undefined)
            setResizeInfo(undefined)
        }
    }, [dragInfo, resizeInfo, isSelectedEventsEqual])

    const eventsByDate = useMemo(() => {
        let eventMap = undefined
        let selectedCalendarEvent =
            selectedEvent && eventDetailsToCalendarEvent(selectedEvent)

        if (dragInfo) {
            const {
                type: dragType,
                data: dragItem,
            } = dragInfo.item

            let events: CalendarEventItem[] = calendarEvents
            if (dragType === EventType.NEW_EVENT && selectedCalendarEvent) {
                const shift = getDragItemShift(dragItem.start, dragInfo, start)
                selectedCalendarEvent = dragCalendarEvent(dragItem, shift)
            } else {
                events = calendarEvents.map(item => {
                    if (item.id === dragItem.id) {
                        const shift = getDragItemShift(item.start, dragInfo, start)
                        return dragCalendarEvent(item, shift)
                    }
                    return item
                })
            }
            eventMap = eventsToEventMap(events, selectedCalendarEvent, selectionRange)
        }

        if (resizeInfo) {
            const {
                item: {
                    type: resizeType,
                    data: resizeItem,
                },
                shift,
            } = resizeInfo

            let events: CalendarEventItem[] = calendarEvents
            if (resizeType === EventType.NEW_EVENT && selectedCalendarEvent) {
                selectedCalendarEvent = resizeCalendarEvent(resizeItem, shift)
            } else {
                events = calendarEvents.map(item => {
                    if (item.id === resizeItem.id) {
                        return resizeCalendarEvent(item, shift)
                    }
                    return item
                })
            }
            eventMap = eventsToEventMap(events, selectedCalendarEvent, selectionRange)
        }

        eventMap = eventMap || eventsToEventMap(calendarEvents, selectedCalendarEvent, selectionRange)
        if (calendarFilters[DefaultCalendars.REQUESTS]) {
            eventMap = requestsToEventMap(eventMap, timeoffRequests)
        }
        if (calendarFilters[DefaultCalendars.SHIFTS]) {
            eventMap = shiftsToEventMap(eventMap, shifts)
        }
        return eventMap
    }, [
        calendarFilters,
        calendarEvents,
        timeoffRequests,
        shifts,
        dragInfo,
        resizeInfo,
        start,
        selectedEvent,
        selectionRange,
    ])

    const handleEventDrop = useCallback(() => {
        if (!dragInfoRef.current || !dragInfoRef.current.item) {
            return
        }
        const dragItem = dragInfoRef.current.item
        if (dragItem.type === EventType.EVENT) {
            const {id} = dragItem.data
            const calendarEvent = calendarEvents.find(item => item.id === id)

            if (calendarEvent) {
                const delta = getDragItemShift(calendarEvent.start, dragInfoRef.current, start)
                moveCalendarEvent(id, delta)
            }
            setDragInfo(undefined)
        }
        if (dragItem.type === EventType.NEW_EVENT) {
            const delta = getDragItemShift(dragItem.data.start, dragInfoRef.current, start)
            updateSelectedEvent({
                startTime: dragItem.data.start + delta,
                endTime: dragItem.data.end + delta,
            })
        }
    }, [calendarEvents, dragInfoRef, moveCalendarEvent, start, updateSelectedEvent])

    const handleEventResize = useCallback(() => {
        if (!resizeInfoRef.current || !resizeInfoRef.current.item) {
            return
        }
        const resizeItem = resizeInfoRef.current.item
        const shift = resizeInfoRef.current.shift
        if (resizeItem.type === EventType.EVENT) {
            stretchCalendarEvent(resizeItem.data.id, shift)
            setResizeInfo(undefined)
        }
        if (resizeItem.type === EventType.NEW_EVENT) {
            const [initialStart, initialEnd] = [resizeItem.data.start, resizeItem.data.end]
            const duration = initialEnd - initialStart
            const isReverse = shift < 0 && Math.abs(shift) > duration
            const [nextStart, nextEnd] = isReverse
                ? [initialEnd + shift, initialStart]
                : [initialStart, initialEnd + shift]
            updateSelectedEvent({startTime: nextStart, endTime: nextEnd})
        }
    }, [resizeInfoRef, stretchCalendarEvent, updateSelectedEvent])

    const handleCellMouseDown = useCallback((_: React.MouseEvent, handlingDate: Date) => {
        if (selectedEvent) {
            closeEventForm()
            return
        }

        // handlingDate is the nearest to user click time multiple of 30 minutes
        setSelectionRange([handlingDate, handlingDate])
    }, [setSelectionRange, selectedEvent, closeEventForm])

    const handleCellMouseOver = useCallback((_: React.MouseEvent, handlingDate: Date) => {
        setSelectionRange(prev => {
            if (!prev) {
                return prev
            }

            return [prev[0], handlingDate]
        })
    }, [setSelectionRange])

    const handleCellMouseUp = useCallback(() => {
        if (!selectionRange) {
            return
        }

        const [selectionStart, selectionEnd] =
            getSortedSelectionRange(selectionRange)

        const endDate = selectionStart !== selectionEnd ?
            selectionEnd : getEventEndByStart(selectionStart)
        selectEvent({startTime: Number(selectionStart), endTime: Number(endDate)})
    }, [selectEvent, selectionRange])

    useEventListener('mouseup', handleCellMouseUp)

    useEffect(() => {
        // scroll to 6am
        if (cellSizeInitialized) {
            const el = tableWrapperRef.current
            const height = cellSize[1] * 12
            if (el) el.scrollTop = height
        }
        // on init
        // eslint-disable-next-line
    }, [cellSizeInitialized])

    const headerCellSize: [number, number] = useMemo(() => {
        return [cellSize[0], HEADER_CELL_HEIGHT]
    }, [cellSize])

    const daysAmount = useMemo(() => {
        if (viewDaysAmount === 7 && !showWeekends) {
            return viewDaysAmount - DAYS_IN_WEEKEND
        }
        return viewDaysAmount
    }, [viewDaysAmount, showWeekends])

    const [allDayEventsByLine, headerLinesAmount] = useAllDayEventsByLine({
        start,
        events: eventsByDate,
        daysAmount,
    })

    const dayEvents = useBodyEventsList({
        start,
        events: eventsByDate,
        daysAmount,
    }, {withinOneDay: true})

    return (
        <div className="flex flex-1 flex-col">
            <div className="relative flex-none pr-2.5">
                <HeaderTable
                    start={start}
                    dragInfo={dragInfo}
                    daysAmount={daysAmount}
                    selectionRange={selectionRange}
                    setSelectionRange={setSelectionRange}
                    cellSize={headerCellSize}
                    linesAmount={headerLinesAmount}
                    isCommunicator={isCommunicator}
                    isStartFromMonday={isStartFromMonday}
                    showWeekends={showWeekends}
                    onDrop={handleEventDrop}
                    onDropCellHover={setDragInfo}
                />
                <HeaderEvents
                    viewStart={start}
                    viewEnd={end}
                    cellSize={headerCellSize}
                    selectedEvent={selectedEvent}
                    isSelectionInProgress={Boolean(selectionRange)}
                    eventsByLine={allDayEventsByLine}
                    isCommunicator={isCommunicator}
                />
            </div>

            <div className="relative flex-1">
                <div
                    className="absolute left-0 top-0 h-full w-full overflow-y-auto overflow-x-hidden"
                    ref={tableWrapperRef}
                >
                    <DaysColumns
                        start={start}
                        daysAmount={daysAmount}
                        headerLinesAmount={headerLinesAmount}
                        cellSize={cellSize}
                        isCommunicator={isCommunicator}
                        selectionRange={selectionRange}
                        onCellSizeChange={setCellSize}
                        onCellMouseDown={handleCellMouseDown}
                        onCellMouseOver={
                            selectionRange ? handleCellMouseOver : undefined
                        }
                        dragInfo={dragInfo}
                        resizeInfo={resizeInfo}
                        onEventDrop={handleEventDrop}
                        onEventResize={handleEventResize}
                        onDropCellHover={setDragInfo}
                        onEventResizeMove={setResizeInfo}
                    />
                    <DayEvents
                        start={start}
                        cellSize={cellSize}
                        events={dayEvents}
                        selectedEvent={selectedEvent}
                        isSelectionInProgress={Boolean(selectionRange)}
                        isCommunicator={isCommunicator}
                    />
                </div>
            </div>
        </div>
    )
}
