import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'


type Props = {
    children: React.ReactNode
    className?: string
}

export const Cell: React.FC<Props> = ({
    children,
    className,
}) => {
    return (
        <div
            className={tcn(
                'block w-full truncate text-base font-normal text-cal-dark-text',
                className,
            )}
        >
            {children}
        </div>
    )
}
