
import {useMemo} from 'react'

import {useLocalizationState} from '@bpinc/state-localization-context'


export type GetMonthsListOptions = {
    short?: boolean
}

export function useMonthsList({short}: GetMonthsListOptions = {}) {
    const {t: {MONTHS: messages}} = useLocalizationState()

    const fullArr = useMemo(() => [
        messages.january(),
        messages.february(),
        messages.march(),
        messages.april(),
        messages.may(),
        messages.june(),
        messages.july(),
        messages.august(),
        messages.september(),
        messages.october(),
        messages.november(),
        messages.december(),
    ], [messages])

    const shortArr = useMemo(() => [
        messages.januaryShort(),
        messages.februaryShort(),
        messages.marchShort(),
        messages.aprilShort(),
        messages.mayShort(),
        messages.juneShort(),
        messages.julyShort(),
        messages.augustShort(),
        messages.septemberShort(),
        messages.octoberShort(),
        messages.novemberShort(),
        messages.decemberShort(),
    ], [messages])

    return short ? shortArr : fullArr
}
