import React from 'react'

import {Page} from '@bpinc/cal-state-types'
import {useEventListener} from '@bpinc/lib-helpers'

import {useCalendarPage} from '../hooks/useCalendarPage'
import {useHandlerMoveKeysCalendar} from '../hooks/useHandlerMoveKeysCalendar'
import {Calendar} from './Calendar'
import {CalendarPageHeader} from './CalendarPageHeader'
import {EventFormSidebar} from './EventFormSidebar'
import {Requests} from './Requests'


export const CalendarPage: React.FC = () => {
    const [page, setPage] = useCalendarPage()
    const handlerCalendarKeys = useHandlerMoveKeysCalendar()
    useEventListener('keydown', handlerCalendarKeys)

    if (!page) {
        return null
    }

    return (
        <>
            <CalendarPageHeader
                activePage={page}
                onPageChange={setPage}
            />
            <div
                data-testid="calendar-page"
                className="flex h-full flex-row items-stretch overflow-hidden pb-2 pl-4 pr-0 pt-3"
            >
                {page === Page.MY_CALENDAR && <Calendar />}
                {page === Page.MY_REQUESTS && <Requests />}
                <EventFormSidebar />
            </div>
        </>
    )
}
