import React, {useCallback, useMemo} from 'react'

import {useDayOfWeek, useDaysOfWeek, useSelectEvent} from '@bpinc/cal-hooks'
import type {DragInfo} from '@bpinc/cal-state-types'
import {type SelectionRange} from '@bpinc/cal-state-types'
import {TimezoneDropdownToggle} from '@bpinc/cal-timezone-dropdown'
import {getNextDayTime} from '@bpinc/cal-utils'
import {MS_IN_DAY, useOnKeyPressHandler} from '@bpinc/lib-helpers'
import {isHTMLElement} from '@bpinc/lib-html-utils'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {StateSetter} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useOpenDayView} from '../../../../hooks/useOpenDayView'
import {HeaderDropContainer} from './HeaderDropContainer'


type HeaderTableProps = {
    start: number
    dragInfo?: DragInfo
    daysAmount: number
    linesAmount: number
    cellSize: [number, number]
    selectionRange: SelectionRange | undefined
    setSelectionRange: StateSetter<SelectionRange | undefined>
    isCommunicator: boolean
    isStartFromMonday: boolean
    showWeekends: boolean
    onDrop: () => void
    onDropCellHover: StateSetter<DragInfo | undefined>
}

export const HeaderTable: React.FC<HeaderTableProps> = ({
    start,
    dragInfo,
    daysAmount,
    selectionRange,
    cellSize,
    linesAmount,
    setSelectionRange,
    isCommunicator,
    isStartFromMonday,
    showWeekends,
    onDrop,
    onDropCellHover,
}) => {
    const {t} = useLocalizationState()
    const selectEvent = useSelectEvent()

    const openDayView = useOpenDayView()
    const daysOfWeek = useDaysOfWeek({
        short: true,
        startsFromMonday: isStartFromMonday,
        withoutWeekend: !showWeekends,
    })
    const dayOfWeek = useDayOfWeek(new Date(start))
    const days = useMemo(() =>
        daysAmount === 1 ? [dayOfWeek] : daysOfWeek
    , [daysAmount, dayOfWeek, daysOfWeek])

    const getStartOfDay = useCallback((dayIndex: number) => {
        return start + MS_IN_DAY * dayIndex
    }, [start])

    const isToday = useCallback(
        (i: number) => {
            const startOfDay = getStartOfDay(i)
            const now = Date.now()
            return startOfDay < now && now - startOfDay < MS_IN_DAY
        },
        [getStartOfDay],
    )

    const getWeekDayDate = useCallback(
        (i: number) => new Date(start + MS_IN_DAY * i),
        [start],
    )

    const getDateFromMouseEvent = useCallback(
        (e: React.MouseEvent) => {
            const i = e.currentTarget.getAttribute('data-i')
            if (i) return new Date(getStartOfDay(Number(i)))
        },
        [getStartOfDay],
    )

    const handleCellMouseDown = useCallback(
        (e: React.MouseEvent) => {
            const day = getDateFromMouseEvent(e)
            if (day) {
                setSelectionRange([day, new Date(getNextDayTime(day))])
            }
        },
        [getDateFromMouseEvent, setSelectionRange],
    )

    const handleCellMouseOver = useCallback(
        (e: React.MouseEvent) => {
            const day = getDateFromMouseEvent(e)
            setSelectionRange(currentSelectionRange => {
                if (!day || !currentSelectionRange) {
                    return currentSelectionRange
                }

                const [selectionStart, selectionEnd] = currentSelectionRange

                const dayInMs = day.getTime()
                const nextDayInMs = getNextDayTime(day)
                const startInMs = selectionStart.getTime()
                const endInMs = selectionEnd.getTime()

                if (dayInMs < startInMs && dayInMs < endInMs) {
                    return [new Date(Math.max(startInMs, endInMs)), day]
                }

                if (nextDayInMs > startInMs && nextDayInMs > endInMs) {
                    return [new Date(Math.min(startInMs, endInMs)), new Date(nextDayInMs)]
                }

                if (startInMs < nextDayInMs) {
                    return [new Date(selectionStart), new Date(nextDayInMs)]
                }

                return [new Date(selectionStart), day]
            })
        },
        [getDateFromMouseEvent, setSelectionRange],
    )

    const handlerOpenDayView = useCallback(
        (columnIndex: string) => {
            openDayView(Number(columnIndex))
        },
        [openDayView],
    )

    const handlerPressAllDayEvent = useCallback(
        (startDayTime: string) => {
            const nextDayTime = getNextDayTime(Number(startDayTime))
            selectEvent({startTime: Number(startDayTime), endTime: Number(nextDayTime)})
        },
        [selectEvent],
    )

    const handleDayHeaderClick = useCallback(
        (e: React.MouseEvent) => {
            if (!isHTMLElement(e.currentTarget) || !e.currentTarget.dataset.column) {
                return
            }
            handlerOpenDayView(e.currentTarget.dataset.column)
        },
        [handlerOpenDayView],
    )

    const handlePressKeyForElement = useCallback(
        (e: React.KeyboardEvent) => {
            if (!isHTMLElement(e.target)) {
                return
            }
            if (e.target.dataset.day) {
                handlerPressAllDayEvent(e.target.dataset.day)
                return
            }
            if (e.target.dataset.column) {
                handlerOpenDayView(e.target.dataset.column)
                return
            }
        },
        [handlerPressAllDayEvent, handlerOpenDayView],
    )

    const onKeyPressHandler = useOnKeyPressHandler(handlePressKeyForElement)

    const cellStyle = useMemo(
        () => ({
            width: cellSize[0],
            height: cellSize[1],
        }),
        [cellSize],
    )

    const tdStyles = tcn(
        'relative border border-solid border-cal-border p-0',
        'text-center text-sm uppercase text-cal-header-text no-underline',
        'cursor-pointer select-none first:border-none first:normal-case',
    )

    return (
        <table className="-mt-px h-full w-full table-fixed border-collapse border-spacing-0">
            <thead>
                <tr>
                    <th
                        className={tcn('w-[52px]', {
                            'w-[57px]': isCommunicator,
                        })}
                    />
                    {days.map(name => {
                        return (
                            <th
                                key={name}
                                className="text-sm uppercase text-cal-header-text"
                            >
                                {name}
                            </th>
                        )
                    })}
                </tr>
                <tr id="row-header-days">
                    <td className="h-6 border-none" />
                    {days.map((name, i) => (
                        <td
                            key={name}
                            className="h-6 border-none"
                            tabIndex={0}
                            data-column={i}
                            data-element="day-header-cell"
                            onClick={handleDayHeaderClick}
                            onKeyDown={onKeyPressHandler}
                        >
                            <div className="flex cursor-pointer justify-center">
                                <div
                                    className={tcn(
                                        'flex h-5 w-5 select-none items-center justify-center text-sm text-primary-legacy',
                                        {
                                            'rounded-full bg-primary-legacy text-primary-contrast no-underline':
                                                isToday(i),
                                        },
                                    )}
                                >
                                    {getWeekDayDate(i).getDate()}
                                </div>
                            </div>
                        </td>
                    ))}
                </tr>
            </thead>
            <HeaderDropContainer
                className="overflow-auto"
                start={start}
                dragInfo={dragInfo}
                cellSize={cellSize}
                onHover={onDropCellHover}
                onDrop={onDrop}
            >
                <tr id="row-all-day-cells">
                    <td className={tdStyles}>
                        {linesAmount <= 1 && <TimezoneDropdownToggle />}
                    </td>
                    {days.map((name, i) => (
                        <td
                            key={name}
                            tabIndex={0}
                            data-element="all-day-cell"
                            data-day={getStartOfDay(i)}
                            className={tdStyles}
                            style={cellStyle}
                            data-i={i}
                            onMouseDown={handleCellMouseDown}
                            onKeyDown={onKeyPressHandler}
                            onMouseOver={
                                selectionRange ? handleCellMouseOver : undefined
                            }
                        >
                            {t.CALENDAR.all_day()}
                        </td>
                    ))}
                </tr>
                {linesAmount > 1 && (
                    <EmptyLinesForEvents lines={linesAmount - 1} />
                )}
            </HeaderDropContainer>
        </table>
    )
}

const EmptyLinesForEvents: React.FC<{lines: number}> = ({lines}) => {
    return (
        <>
            {Array.from({length: lines}).map((_, index, arr) => {
                return (
                    <tr key={index} className="h-6">
                        {index === arr.length - 1 && (
                            <td className="border-none text-center">
                                <TimezoneDropdownToggle />
                            </td>
                        )}
                    </tr>
                )
            })}
        </>
    )
}
