import React, {
    useCallback,
    useEffect,
    useLayoutEffect,
    useRef,
    useState,
} from 'react'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {usePrevious, useResizeObserver} from '@bpinc/lib-helpers'

import {useIsStartFromMonday} from '../../../hooks/useIsStartFromMonday'
import {useShowWeekends} from '../../../hooks/useShowWeekends'
import {ViewMonthContent} from './ViewMonthContent'
import {ViewMonthHeader} from './ViewMonthHeader'


export const ViewMonth: React.FC = React.memo(() => {
    const viewRef = useRef<HTMLDivElement>(null)
    const [itemsInCell, setItemsInCell] = useState(1)

    const isStartFromMonday = useIsStartFromMonday()
    const showWeekends = useShowWeekends()
    const isCommunicator = useIsCommunicator()
    const viewObserverEntry = useResizeObserver(viewRef)
    const viewHeight = viewObserverEntry?.contentRect.height
    const prevViewHeight = usePrevious(viewHeight)

    const calculateItemsInCell = useCallback(() => {
        const eventContainer = document.getElementById('month-events-container')
        if (eventContainer) {
            const itemHeight = 18
            const itemMargin = 2
            const totalItemHeight = itemHeight + 2 * itemMargin
            const height = eventContainer.offsetHeight
            const itemsCanFit = Math.floor(height / totalItemHeight)

            setItemsInCell(itemsCanFit)
        }
    }, [])

    useLayoutEffect(() => {
        calculateItemsInCell()
    }, [calculateItemsInCell])

    useEffect(() => {
        if (viewHeight !== prevViewHeight) {
            calculateItemsInCell()
        }
    }, [viewHeight, prevViewHeight, calculateItemsInCell])

    return (
        <div className="flex flex-1 flex-col" ref={viewRef}>
            <ViewMonthHeader
                isCommunicator={isCommunicator}
                isStartFromMonday={isStartFromMonday}
                showWeekends={showWeekends}
            />
            <ViewMonthContent
                itemsInCell={itemsInCell}
                isStartFromMonday={isStartFromMonday}
                showWeekends={showWeekends}
            />
        </div>
    )
})
