import type {Dispatch, SetStateAction} from 'react'
import {useEffect, useState} from 'react'

import {Page} from '@bpinc/cal-state-types'
import {parseQueryString, setQueryParams} from '@bpinc/lib-helpers'


type Result = [Page | null, Dispatch<SetStateAction<Page | null>>]

export function useCalendarPage(): Result {
    const [activePage, setActivePage] = useState<Page | null>(null)

    useEffect(() => {
        const queryParams = parseQueryString()
        const tab = queryParams.tab

        if (tab) {
            setActivePage(tab as Page)
        } else {
            setQueryParams({tab: Page.MY_CALENDAR})
            setActivePage(Page.MY_CALENDAR)
        }

        return () => setQueryParams({})
    }, [])

    useEffect(() => {
        setQueryParams({tab: activePage})
    }, [activePage])

    return [activePage, setActivePage]
}
