import React from 'react'
import {useForm} from 'react-hook-form'

import {useCalendarState} from '@bpinc/cal-state-context'
import type {CalendarEventFields} from '@bpinc/cal-state-types'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {CalendarEventForm} from './CalendarEventForm'


export const EventFormSidebar: React.FC = React.memo(() => {
    const form = useForm<CalendarEventFields>()
    const selectedEvent = useCalendarState('selectedEvent')

    return (
        <div
            className={tcn(
                'relative ml-[15px] w-0 overflow-hidden border-l border-solid border-l-transparent',
                'ease transition-[width,border-left-color] duration-300',
                {'w-[308px] border-l-[#c6c8c9]': selectedEvent},
            )}
        >
            {selectedEvent && <CalendarEventForm form={form} event={selectedEvent} />}
        </div>
    )
})
