import type {HTMLAttributes} from 'react'
import React from 'react'
import {useDrop} from 'react-dnd'

import type {DragInfo, NotEmptyEventMapItem} from '@bpinc/cal-state-types'
import {DragItemTypes} from '@bpinc/cal-state-types'
import {getWeekHeaderCellIndex} from '@bpinc/cal-utils'

import type {WeekDropResult} from '../ViewColumns'


interface HeaderDropContainerProps extends HTMLAttributes<HTMLDivElement> {
    start: number
    dragInfo?: DragInfo
    onDrop: () => void
    onHover: (dragInfo: DragInfo) => void
    cellSize: [number, number]
}

export const HeaderDropContainer: React.FC<HeaderDropContainerProps> = ({
    children,
    start,
    dragInfo,
    onDrop,
    onHover,
    cellSize,
    ...rest
}) => {
    const [, drop] = useDrop<NotEmptyEventMapItem, WeekDropResult, null>(
        () => ({
            accept: [DragItemTypes.HEADER_EVENT],
            hover: (item, monitor) => {
                const diffCoords = monitor.getDifferenceFromInitialOffset()

                if (!diffCoords) {
                    return
                }

                const dayIndex = getWeekHeaderCellIndex(item.data.start, start)

                if (dragInfo) {
                    const {x: diffX} = diffCoords
                    const [cellWidth] = cellSize
                    const newDayIndex = dayIndex + Math.round(diffX / cellWidth)
                    if (newDayIndex !== dragInfo.cell[0]) {
                        onHover({
                            item: dragInfo.item,
                            cell: [newDayIndex, 0],
                        })
                    }
                } else {
                    onHover({
                        item,
                        cell: [dayIndex, 0],
                    })
                }
            },
            drop: () => ({onEnd: onDrop}),
        }),
        [start, dragInfo, cellSize],
    )

    return (
        <tbody
            ref={drop}
            {...rest}
        >
            {children}
        </tbody>
    )
}
