import type {FC} from 'react'
import React from 'react'

import type {RequestResult} from '@bpinc/cal-state-types'
import {SmMessage} from '@bpinc/lib-external-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


type EventErrorsProps = {
    saveResult?: RequestResult
    deleteResult?: RequestResult
    clearSaveResult: () => void
    clearDeleteResult: () => void
}

export const EventErrors: FC<EventErrorsProps> = ({saveResult, deleteResult, clearSaveResult, clearDeleteResult}) => {
    const {t: {CALENDAR_EVENT_FORM: message}} = useLocalizationState()

    return (
        <>
            {saveResult === 'error' && (
                <div className="mb-4">
                    <SmMessage
                        error
                        onDismiss={clearSaveResult}
                        header={message.error_save_event_header()}
                        content={message.error_save_event_text()}
                    />
                </div>
            )}
            {deleteResult === 'error' && (
                <div className="mb-4">
                    <SmMessage
                        error
                        onDismiss={clearDeleteResult}
                        header={message.error_delete_event_header()}
                        content={message.error_delete_event_text()}
                    />
                </div>
            )}
        </>
    )
}
