import {useCallback} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import {useCalendarStateMethods} from '@bpinc/cal-state-context'
import {isWithinOneDay} from '@bpinc/cal-utils'
import {uuid} from '@bpinc/lib-uuid'

import {useCalendarOwner} from './useCalendarOwner'
import {useLocalTimezone} from './useLocalTimezone'


export const useSetNewSelectedCalendarEvent = () => {
    const {selectEvent} = useCalendarStateMethods()

    const timezone = useLocalTimezone()
    const calendarOwner = useCalendarOwner()

    const {formatFullName} = useFormatting()

    return useCallback((startTime: number, endTime: number) => {
        const userId = calendarOwner.userId
        const userName = formatFullName(calendarOwner.firstName, calendarOwner.lastName)
        const isAllDay = !isWithinOneDay(startTime, endTime)

        const eventDetails: CalendarEventDetails = {
            _temporaryId: uuid(),
            allDay: isAllDay,
            assignees: [
                {
                    userId,
                    displayText: userName,
                    type: 'user',
                },
            ],
            completed: false,
            contacts: [],
            remindIn: 0,
            remindInUnit: 'MINUTES',
            screenpopSize: 0,
            startTime,
            endTime,
            timezone,
            title: '',
            userId,
            userName,
        }

        selectEvent(eventDetails)
    }, [
        selectEvent,
        calendarOwner,
        timezone,
        formatFullName,
    ])
}
