import type {FC, LazyExoticComponent} from 'react'
import React, {Suspense} from 'react'

import type {AppType} from '@bpinc/state-lookup'

import {CommonOpenEventButton} from './CommonOpenEventButton'


const AdOpenEventButton = React.lazy(() => import('./AdOpenEventButton').then(module => ({default: module.AdOpenEventButton})))
const AdcOpenEventButton = React.lazy(() => import('./AdcOpenEventButton').then(module => ({default: module.AdcOpenEventButton})))

const OpenEventButtons: Partial<Record<AppType, LazyExoticComponent<FC<OpenEventButtonCommonProps>>>> = {
    ad: AdOpenEventButton,
    adc: AdcOpenEventButton,
}

export type OpenEventButtonCommonProps = {
    eventId: string
    onClick: () => void
}

type OpenEventButtonProps = OpenEventButtonCommonProps & {
    type: AppType
}

export const OpenEventButton: FC<OpenEventButtonProps> = ({type, ...restProps}) => {
    const Component = OpenEventButtons[type]

    if (!Component) {
        return null
    }

    return (
        <Suspense fallback={<CommonOpenEventButton disabled /> }>
            <Component {...restProps} />
        </Suspense>
    )
}
