import type {FC} from 'react'
import React from 'react'
import type {UseFormReturn} from 'react-hook-form'

import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import type {CalendarEventFields} from '@bpinc/cal-state-types'
import {getDefaultValues} from '@bpinc/cal-utils'
import {SmForm, SmHeader} from '@bpinc/lib-external-ui-elements'
import {CheckboxField} from '@bpinc/lib-forms-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {formatEventTimes} from '../../utils'
import {AssigneesComboBox} from '../AssigneesComboBox'
import {EventDetails} from '../EventDetails'
import {EventContactDetails} from './EventContactDetails'


type EventPreviewProps = {
    form: UseFormReturn<CalendarEventFields>
    event: CalendarEventDetails
    showTitle: boolean
}

export const EventPreview: FC<EventPreviewProps> = ({
    form,
    event,
    showTitle,
}) => {
    const {t: {CALENDAR_EVENT_FORM: message}, language} = useLocalizationState()

    const {control} = form

    const defaultValues = getDefaultValues(event)

    const {
        allDay,
        endTime,
        notes,
        remindIn,
        remindInUnit,
        screenpopSize,
        startTime,
        title,
        contacts,
    } = defaultValues

    const {from, to} = formatEventTimes({
        allDay,
        allDayMessage: message.text_all_day_event(),
        startTime,
        endTime,
        locale: language,
    })

    return (
        <div className="overflow-y-auto overflow-x-hidden">
            {showTitle && <SmHeader as="h3">{title}</SmHeader>}

            <CheckboxField
                className="mb-4"
                control={control}
                name="completed"
                label={message.field_completed_label()}
            />

            <p className="mb-3.5">
                <span className="mr-3 inline-block">{from.date}</span>
                <span className="mr-3 inline-block">{from.time}</span>
                {to && (
                    <>
                        <span className="mr-3 inline-block">{'  ' + to.date}</span>
                        <span className="mr-3 inline-block">{to.time}</span>
                    </>
                )}
            </p>

            {remindIn
                ? (
                    <p>
                        {message.text_reminde_before(
                            remindInUnit === 'MINUTES'
                                ? message.format_minutes(remindIn)
                                : remindInUnit === 'HOURS'
                                    ? message.format_hours(remindIn)
                                    : message.format_days(remindIn),
                        )}
                    </p>
                )
                : null
            }

            {/* TBD: Replace the placeholder with a real component */}
            <SmForm.Field>
                <AssigneesComboBox
                    control={control}
                    label={message.field_assignee_label()}
                    name="assignees"
                    disabled
                />
            </SmForm.Field>
            <SmForm.Field>
                <label>{message.field_summary_label()}</label>
                <p>{notes || message.field_details_none()}</p>
            </SmForm.Field>

            <SmForm.Field>
                <EventDetails form={form} staticValue={screenpopSize} />
            </SmForm.Field>
            {contacts.map((contact, index) => (
                <EventContactDetails
                    key={index}
                    contact={contact}
                />
            ))}

            <div className="h-14">
                <div />
            </div>
        </div>
    )
}
