import type {CSSProperties} from 'react'
import React, {useCallback} from 'react'
import {useDrag} from 'react-dnd'

import {useSelectEvent} from '@bpinc/cal-hooks'
import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import type {DayOfWeek, DragInfo, NotEmptyEventMapItem} from '@bpinc/cal-state-types'
import {DragItemTypes, EventCustomId, EventType} from '@bpinc/cal-state-types'
import {getDragStartDate, getLasts} from '@bpinc/cal-utils'
import {useOnKeyPressHandler} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'


export type ViewMonthEventBlockProps = {
    item: NotEmptyEventMapItem
    title: React.ReactNode | string
    dayOfWeek: DayOfWeek
    className?: string
    classNameAllDay?: string
    dragInfo?: DragInfo
    onDragStart: (date: Date) => void
    showOccupied?: boolean
    selectedEvent?: CalendarEventDetails
    isSelectionInProgress?: boolean
    showInModal?: boolean
}

export const ViewMonthEventBlock: React.FC<ViewMonthEventBlockProps> = ({
    item,
    title,
    dayOfWeek,
    className,
    classNameAllDay,
    dragInfo,
    onDragStart,
    showOccupied = false,
    selectedEvent,
    isSelectionInProgress = false,
    showInModal = false,
}) => {
    const selectEvent = useSelectEvent()

    const [{isDragging}, drag] = useDrag(
        () => ({
            type: DragItemTypes.EVENT,
            item,
            collect: monitor => ({
                isDragging: monitor.isDragging(),
            }),
        }),
        [item],
    )

    const handleClick = useCallback(() => {
        if (
            (item.type === EventType.EVENT || item.type === EventType.OCCUPIED)
            && item.data.id !== EventCustomId.SELECTION
        ) {
            selectEvent({id: item.data.id})
        }
    }, [item, selectEvent])

    const handleMouseDown = useCallback((e: React.MouseEvent) => {
        // click within event item should not pop to the cell
        e.stopPropagation()

        const startDragDate = showInModal ?
            new Date((item as NotEmptyEventMapItem).data.start) :
            getDragStartDate(e.pageX, e.pageY)

        if (!startDragDate) {
            return
        }
        onDragStart(startDragDate)
    }, [item, onDragStart, showInModal])

    const onKeyPressHandler = useOnKeyPressHandler(handleClick)

    const {data, lastsDays} = item
    const lasts = showOccupied ? 1 : getLasts(lastsDays, dayOfWeek)
    const isNoEnd = showOccupied ? lastsDays > 1 : lasts < lastsDays
    const style: CSSProperties = {width: showInModal || isNoEnd ? `calc(100% * ${lasts})` : `calc(100% * ${lasts} - 16px + ${lasts - 1}px)`}
    const dragItem = dragInfo && 'data' in dragInfo.item ? dragInfo?.item.data : null
    const isAllDay = data.allDay || lastsDays > 1
    const isSelected = selectedEvent?.id === data.id || data.id === EventType.NEW_EVENT
    const isOccupied = item.type === EventType.OCCUPIED && !showInModal
    const eventClassName = tcn(
        'relative z-[2] box-border cursor-pointer self-start overflow-hidden whitespace-nowrap text-sm',
        'mx-0 my-0.5 border-l-4 border-solid text-cal-event-text',
        'ease transition-all duration-300',
        className,
        {
            [classNameAllDay || '']: isAllDay || isOccupied,
            '!border-l-0': isOccupied,
            'shadow': isSelected || data.id === EventCustomId.SELECTION,
            'invisible': isDragging || data.id === dragItem?.id,
            'pointer-events-none':
                dragInfo || data.id === EventCustomId.SELECTION && isSelectionInProgress || selectedEvent && !isSelected,
        },
    )

    return (
        <div
            // user can hover existed event while selection
            ref={isSelectionInProgress ? undefined : drag}
            tabIndex={0}
            data-event-id={item.data.id}
            className={eventClassName}
            style={style}
            onClick={handleClick}
            onMouseDown={handleMouseDown}
            onKeyDown={onKeyPressHandler}
        >
            <div className="h-4.5 select-none truncate px-[3px] py-0 leading-[18px]">
                {title}
            </div>
        </div>
    )
}
