import type {ChangeEvent, FC, MouseEvent} from 'react'
import React, {
    memo,
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from 'react'

import type {Timezone} from '@bpinc/ad-transport-types'
import {SmDropdown} from '@bpinc/lib-external-ui-elements'
import {stopPropagation, useBoolean, useEscapeManager, useOutsideClick} from '@bpinc/lib-helpers'
import {isHTMLElement} from '@bpinc/lib-html-utils'
import {IconSearch} from '@bpinc/lib-icons'
import type {DropdownItemProps} from '@bpinc/lib-ui-elements'
import {highlighted, SearchInput} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {getTimezoneId} from '../utils/helpers'


type TimezoneDropdownProps = {
    as?: string
    localTimezoneId: string
    ariaLabel?: string
    className?: string
    text: string
    onSelectTimezone(timezoneId: string): void
    recentTimezoneIds: string[]
    allTimezones: Timezone[]
}

export const TimezoneDropdown: FC<TimezoneDropdownProps> = memo(({
    localTimezoneId,
    onSelectTimezone,
    recentTimezoneIds,
    allTimezones,
    className,
    text,
    ariaLabel,
}) => {
    const {value: show, setTrue, setFalse, toggle} = useBoolean()
    const [search, setSearch] = useState('')
    const {t: {CALENDAR}} = useLocalizationState()
    const inputRef = useRef<HTMLInputElement>(null)
    const dropdownRef = useRef(null)

    const handleClick = useCallback((e: MouseEvent<HTMLElement>, data: DropdownItemProps) => {
        stopPropagation(e)
        if (typeof data.value === 'string') {
            onSelectTimezone(data.value)
        }
        setFalse()
    }, [onSelectTimezone, setFalse])

    const handleSearch = useCallback((e: ChangeEvent<HTMLInputElement>) => {
        setSearch(e.currentTarget.value)
    }, [])

    const localTimezone = useMemo(() => {
        return allTimezones.find(timezone => timezone.id === localTimezoneId)
    }, [localTimezoneId, allTimezones])

    const recentTimezones: Timezone[] = useMemo(() => {
        return recentTimezoneIds.reduce<Timezone[]>((acc, timezoneId) => {
            const timezone = allTimezones.find(timezoneItem => timezoneItem.id === timezoneId)
            return timezone
                ? acc.concat(timezone)
                : acc
        }, [])
    }, [recentTimezoneIds, allTimezones])

    const filteredTimezones = useMemo(() => {
        if (search) {
            const s = search.toLowerCase()
            return allTimezones.filter(
                ({fullName}) => fullName.toLowerCase().includes(s),
            )
        }
        return allTimezones
    }, [allTimezones, search])

    const handleKeyDown = useCallback((e: React.KeyboardEvent) => {
        const timezoneId = getTimezoneId(e.target)
        if (timezoneId) {
            onSelectTimezone(timezoneId)
        }
    }, [onSelectTimezone])

    useOutsideClick({
        ref: dropdownRef,
        handler: () => setFalse(),
    })

    useEffect(() => {
        if (show) {
            inputRef.current?.focus()
        }
    }, [show])

    const handlePressKey = useCallback((e: React.KeyboardEvent) => {
        if (!isHTMLElement(e.target)) {
            return
        }
        if (e.key === ' ' || e.key === 'Enter') {
            e.preventDefault()
            e.stopPropagation()
            handleKeyDown(e)
            toggle()
            return
        }
        if (e.shiftKey && e.key === 'Tab' && e.target === inputRef.current) {
            setFalse()
            return
        }
    }, [toggle, setFalse, handleKeyDown])

    useEscapeManager(setFalse, {enabled: show})

    return (
        <div ref={dropdownRef}>
            <SmDropdown
                as="a"
                href="#"
                tabIndex={0}
                open={show}
                icon={false}
                closeOnBlur={false}
                openOnFocus={false}
                className={className}
                onKeyDown={handlePressKey}
                onOpen={setTrue}
                onClose={setFalse}
                text={text}
                aria-label={ariaLabel}
                data-testid="timezone-dropdown"
            >
                <SmDropdown.Menu
                    id="timezone-content"
                    data-testid="timezone-content"
                    className="w-[340px]"
                    open={show}
                    onClick={stopPropagation}
                >
                    <SearchInput
                        fluid
                        clearable
                        value={search}
                        inputRef={inputRef}
                        className="min-h-9.5 max-w-[324px] text-base"
                        aria-label={CALENDAR.aria_search_label()}
                        icon={
                            <div className="absolute right-0 top-0 m-0 h-full w-9.5">
                                <IconSearch className="h-full w-6" />
                            </div>
                        }
                        onChange={handleSearch}
                        placeholder={CALENDAR.timezone_search_placeholder()}
                        onClick={stopPropagation}
                    />
                    <SmDropdown.Menu
                        scrolling
                        data-root-navigation-by-keys
                        className="max-h-90"
                    >
                        {localTimezone && !search && (
                            <>
                                <Header label={CALENDAR.timezone_local_header()} />
                                {localTimezone && (
                                    <TimezoneItem
                                        id={localTimezone.id}
                                        fullName={localTimezone.fullName}
                                        highlight={search}
                                        onClick={handleClick}
                                        onKeyDown={handlePressKey}
                                    />
                                )}
                            </>
                        )}
                        {recentTimezones.length > 0 && !search && (
                            <>
                                <Header label={CALENDAR.timezone_recently_header()} />
                                {recentTimezones.map(({id, fullName}) => (
                                    <TimezoneItem
                                        key={id}
                                        id={id}
                                        fullName={fullName}
                                        highlight={search}
                                        onClick={handleClick}
                                        onKeyDown={handlePressKey}
                                    />
                                ))}
                            </>
                        )}
                        {filteredTimezones.length > 0 && (
                            <>
                                {!search && <Header label={CALENDAR.timezone_all_header()} />}
                                {filteredTimezones.map(({id, fullName}) => (
                                    <TimezoneItem
                                        key={id}
                                        id={id}
                                        fullName={fullName}
                                        highlight={search}
                                        onClick={handleClick}
                                        onKeyDown={handlePressKey}
                                    />
                                ))}
                            </>
                        )}
                        {search && !filteredTimezones.length && <Header label={CALENDAR.no_results_label()} />}
                    </SmDropdown.Menu>
                </SmDropdown.Menu>
            </SmDropdown>
        </div>
    )
})

type TimezoneItemProps = Pick<Timezone, 'id' | 'fullName'> & {
    highlight: string
    onClick: (e: MouseEvent<HTMLElement>, data: DropdownItemProps) => void
    onKeyDown: (e: React.KeyboardEvent) => void
}

const TimezoneItem: FC<TimezoneItemProps> = ({
    id,
    fullName,
    onClick,
    onKeyDown,
    highlight,
}) => {
    return (
        <SmDropdown.Item
            key={id}
            value={id}
            data-id={id}
            data-testid={id}
            aria-label={id}
            tabIndex={0}
            data-element="timezone-item"
            className="cursor-pointer text-base leading-[28px]"
            onClick={onClick}
            onKeyDown={onKeyDown}
        >
            {highlighted({highlight, text: fullName})}
        </SmDropdown.Item>
    )
}

type HeaderProps = {
    label: string
}

const Header: FC<HeaderProps> = ({label}) => {
    return (
        <SmDropdown.Header
            className="align-middle text-base normal-case leading-[28px]"
        >
            {label}
        </SmDropdown.Header>
    )
}
