import React, {useCallback, useMemo, useState} from 'react'

import {SmTransition} from '@bpinc/lib-external-ui-elements'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import type {CalendarSidebarFiltersProps} from './CalendarSidebarFilters'
import {CalendarSidebarFilters} from './CalendarSidebarFilters'
import {CalendarSidebarMonths} from './CalendarSidebarMonths'
import type {CalendarSidebarPaginationProps} from './CalendarSidebarPagination'
import {CalendarSidebarPagination} from './CalendarSidebarPagination'


type CalendarSidebarProps = {
    show: boolean
    viewDate: Date
    eventsDatesMap: Record<string, unknown>
    filters: CalendarSidebarFiltersProps
    pagination?: Pick<CalendarSidebarPaginationProps, 'nextTooltip' | 'previousTooltip'>
}

const MONTHS_PER_PAGE = 2

export const CalendarSidebar: React.FC<CalendarSidebarProps> = React.memo(({
    show,
    viewDate,
    eventsDatesMap,
    filters,
    pagination,
}) => {
    const [currentDate, setCurrentDate] = useState<Date>(viewDate)

    const switchPage = useCallback(({forward}: {forward: boolean}) =>
        () => setCurrentDate(prev => {
            const sample = new Date(prev)
            sample.setMonth(sample.getMonth() + (forward ? MONTHS_PER_PAGE : -MONTHS_PER_PAGE))
            return sample
        },
        ), [])

    const goNextPage = useMemo(() => switchPage({forward: true}), [switchPage])
    const goPrevPage = useMemo(() => switchPage({forward: false}), [switchPage])

    return (
        <div
            className={tcn('ease w-0 transition-[width] duration-300', {
                'w-[258px]': show,
            })}
        >
            <SmTransition
                visible={show}
                duration="300"
                animation="fade right"
                unmountOnHide
            >
                <div
                    className={tcn(
                        'relative box-border flex h-full w-[258px] flex-col',
                        'overflow-y-auto overflow-x-hidden py-4 pl-6 pr-2',
                    )}
                >
                    <CalendarSidebarMonths
                        currentDate={currentDate}
                        viewDate={viewDate}
                        eventsDatesMap={eventsDatesMap}
                    />
                    <CalendarSidebarPagination
                        goNextPage={goNextPage}
                        goPreviousPage={goPrevPage}
                        nextTooltip={pagination?.nextTooltip}
                        previousTooltip={pagination?.previousTooltip}
                    />
                    <CalendarSidebarFilters {...filters} />
                </div>
            </SmTransition>
        </div>
    )
})
