/* eslint-disable react-perf/jsx-no-new-function-as-prop */
import React, {useMemo} from 'react'

import {useDaysOfWeek} from '@bpinc/cal-hooks'
import {getRangeFromView} from '@bpinc/cal-utils'
import {getNextMonthDate} from '@bpinc/lib-helpers'

import {CalendarSidebarMonth} from './CalendarSidebarMonth'


type CalendarSidebarMonthsProps = {
    currentDate: Date
    viewDate: Date
    eventsDatesMap: Record<string, unknown>
}

export const CalendarSidebarMonths: React.FC<CalendarSidebarMonthsProps> = React.memo(({
    currentDate,
    viewDate,
    eventsDatesMap,
}) => {
    const [start, end] = getRangeFromView(viewDate.getTime(), 'month', {expand: true})
        .map(d => new Date(d))
    const nextMonthDate = useMemo(() => getNextMonthDate(currentDate), [currentDate])
    const daysOfWeek = useDaysOfWeek({oneLetter: true})

    return (
        <div>
            <CalendarSidebarMonth
                monthDate={currentDate}
                viewStart={start}
                viewEnd={end}
                eventsMap={eventsDatesMap}
                daysOfWeek={daysOfWeek}
                hideNextMonth
            />
            <CalendarSidebarMonth
                monthDate={nextMonthDate}
                viewStart={start}
                viewEnd={end}
                eventsMap={eventsDatesMap}
                daysOfWeek={daysOfWeek}
                hidePreviousMonth
            />
        </div>
    )
})
