
import type {CalendarEventItem} from '@bpinc/cal-request-urls'
import {EventType as EventBlockType} from '@bpinc/cal-request-urls'
import {
    type EventMap,
    type EventMapItem,
    type NewEvent,
    type RegularEvent,
    type SelectionRange,
    EventCustomId,
    EventType,
} from '@bpinc/cal-state-types'
import type {Shift, TimeoffRequest} from '@bpinc/cal-wfm-request-urls'
import {MS_IN_DAY} from '@bpinc/lib-helpers'

import {selectionToCalendarEvent} from './mappers/event'
import {getRangeInDays, getWeeksInRange, isWithinOneDay} from './dates'
import {getSortedSelectionRange} from './events-common'


export const getEventMapKey = (date: Date) => {
    const y = date.getFullYear()
    const m = date.getMonth() + 1
    const d = date.getDate()
    return `${y}-${m}-${d}`
}

export const eventsToEventMap = (
    calendarEvents: CalendarEventItem[],
    selectedEvent?: CalendarEventItem,
    selectionRange?: SelectionRange,
) => {
    const eventMap: EventMap = {}
    const sortedEvents =
        calendarEvents.slice(0).sort((a, b) => a.start - b.start)

    sortedEvents.forEach(event => {
        if (event.id !== selectedEvent?.id) {
            addEventToEventMap(eventMap, event)
        }
    })

    if (selectedEvent && selectedEvent.id !== EventCustomId.SELECTION) {
        addEditedEventToMap(eventMap, selectedEvent)
    } else if (selectionRange) {
        addSelectionToEventMap(eventMap, selectionRange)
    }

    return eventMap
}

export const requestsToEventMap = (
    eventMap: EventMap,
    requests: TimeoffRequest[],
) => {
    const requestEvents = requests.map(r => requestToCalendarEvent(r))
    const sorted = requestEvents.slice(0).sort((a, b) => a.start - b.start)

    sorted.forEach(event => {
        addEventToEventMap(eventMap, event)
    })

    return eventMap
}

const requestToCalendarEvent = (request: TimeoffRequest): CalendarEventItem => {
    const [start, end] = [request.startDate, request.endDate].map(
        d => new Date(d).getTime(),
    )

    return {
        type: EventBlockType.REQUEST,
        id: request.id,
        allDay: !isWithinOneDay(start, end),
        start,
        end,
        title: request.status,
    }
}

export const shiftsToEventMap = (
    eventMap: EventMap,
    shifts: Shift[],
) => {
    const shiftEvents = shifts.map(s => shiftToCalendarEvent(s))
    const sorted = shiftEvents.slice(0).sort((a, b) => a.start - b.start)

    sorted.forEach(event => {
        addEventToEventMap(eventMap, event)
    })

    return eventMap
}

const shiftToCalendarEvent = (shift: Shift): CalendarEventItem => {
    const [start, end] = [shift.startTime, shift.endTime].map(
        d => new Date(d).getTime(),
    )

    return {
        type: EventBlockType.SHIFT,
        id: shift.id,
        allDay: !isWithinOneDay(start, end),
        start,
        end,
        title: 'Shift',
    }
}

const addEventToEventMap = (eventMap: EventMap, event: CalendarEventItem): void => {
    const startDate = new Date(event.start)
    const endDate = new Date(event.end)
    const mapKey: string = getEventMapKey(startDate) // YYYY-MM-DD

    if (!eventMap[mapKey]) {
        eventMap[mapKey] = []
    }

    const lastsDays = getRangeInDays(startDate, endDate)

    const mapEventItem: RegularEvent = {
        type: EventType.EVENT,
        data: event,
        lastsDays,
    }

    let emptySlotIndex = eventMap[mapKey].findIndex(item => item.type === EventType.EMPTY)
    emptySlotIndex = emptySlotIndex === -1 ? eventMap[mapKey].length : emptySlotIndex
    eventMap[mapKey][emptySlotIndex] = mapEventItem

    if (lastsDays > 1) {
        addOccupiedItemsToEventMap(
            eventMap,
            mapEventItem,
            startDate,
            lastsDays,
            emptySlotIndex,
        )
    }
}

const addOccupiedItemsToEventMap = (
    eventMap: EventMap,
    eventMapItem: RegularEvent | NewEvent,
    eventStartDate: Date,
    eventLastsDays: number,
    eventSlotIndex: number,
): void => {
    const insertionDate = new Date(eventStartDate)

    for (let lastsCount = 1; lastsCount < eventLastsDays; lastsCount++) {
        insertionDate.setHours(24) // increment day

        const nextDayKey = getEventMapKey(insertionDate)

        if (!eventMap[nextDayKey]) {
            eventMap[nextDayKey] = []
        }

        const dayOfWeek = insertionDate.getDay()
        // lastsDay > 1, so if we increment 1 day and get day === 0 then it's next week
        const isNextWeek = dayOfWeek === 0

        if (!isNextWeek) {
            while (eventMap[nextDayKey].length < eventSlotIndex) {
                eventMap[nextDayKey].push({
                    type: EventType.EMPTY,
                })
            }
        }

        const occupiedItem: EventMapItem = {
            type: EventType.OCCUPIED,
            data: eventMapItem.data,
            lastsDays: eventLastsDays - lastsCount,
        }

        if (isNextWeek || eventMap[nextDayKey].length === eventSlotIndex) {
            eventMap[nextDayKey].push(occupiedItem)
        } else {
            // occupy an empty spot
            eventMap[nextDayKey][eventSlotIndex] = occupiedItem
        }
    }
}

const addSelectionToEventMap = (
    eventMap: EventMap,
    selection: SelectionRange,
): void => {
    const range = getSortedSelectionRange(selection)
    const selectionEvent = selectionToCalendarEvent(range)
    addEditedEventToMap(eventMap, selectionEvent)
}

const addEditedEventToMap = (eventMap: EventMap, event: CalendarEventItem) => {
    const [start, end] = [new Date(event.start), new Date(event.end)]

    const daysCount = getRangeInDays(start, end)

    for (let i = 0; i < daysCount; i++) {
        const nextDate = new Date(new Date(start).setHours(24 * i))
        const mapKey = getEventMapKey(nextDate)

        if (!eventMap[mapKey]) {
            eventMap[mapKey] = []
        }

        eventMap[mapKey].unshift({
            type: i === 0 ? EventType.NEW_EVENT : EventType.OCCUPIED,
            lastsDays: daysCount - i,
            data: event,
        })
    }

    addEmptyEventsToEventMap(eventMap, start, end)
}

const addEmptyEventsToEventMap = (
    eventMap: EventMap,
    eventStart: Date,
    eventEnd: Date,
): void => {
    const weeksInRange = getWeeksInRange([eventStart, eventEnd])

    weeksInRange.forEach(weekStart => {
        for (let i = 0; i < 7; i++) {
            const day = new Date(new Date(weekStart).setHours(24 * i))

            if (dayIsWithinEvent(day, eventStart, eventEnd)) {
                continue
            }

            const dateKey = getEventMapKey(day)

            if (!eventMap[dateKey]) {
                eventMap[dateKey] = []
            }

            eventMap[dateKey].unshift({
                type: EventType.EMPTY,
            })
        }
    })
}

const dayIsWithinEvent = (
    startOfDay: Date,
    eventStart: Date,
    eventEnd: Date,
): boolean => {
    const dayStartTs = Number(startOfDay)
    const dayEndTs = Number(startOfDay) + MS_IN_DAY
    const eventStartTs = Number(eventStart)
    const eventEndTs = Number(eventEnd)

    const isEventStartInDay = dayStartTs <= eventStartTs && eventStartTs < dayEndTs
    const isEventMiddleInDay = eventStartTs < dayStartTs && dayEndTs < eventEndTs
    const isEventEndInDay = dayStartTs <= eventEndTs && eventEndTs < dayEndTs

    return isEventStartInDay || isEventMiddleInDay || isEventEndInDay
}
