import React, {useCallback, useEffect, useMemo, useState} from 'react'
import {isEqual} from 'lodash-es'

import {useSelectEvent} from '@bpinc/cal-hooks'
import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import type {DragInfo} from '@bpinc/cal-state-types'
import {DefaultCalendars, EventCustomId, View} from '@bpinc/cal-state-types'
import {
    eventDetailsToCalendarEvent,
    eventsToEventMap,
    getDateByMonthViewIndexes,
    getDaysOfWeekIndices,
    getDefaultTimeForHourEvent,
    getMonthEventSelectionRange,
    getRangeFromView,
    getRangeInDays,
    getWeeksInMonth,
    requestsToEventMap,
    shiftsToEventMap,
} from '@bpinc/cal-utils'
import {areSameMonths, MS_IN_DAY, useEventListener, usePrevious} from '@bpinc/lib-helpers'

import {useSelectionRange} from '../../../hooks/useSelectionRange'
import {ViewMonthEvents} from './ViewMonthEvents'


type ViewMonthContentProps = {
    itemsInCell: number
    isStartFromMonday: boolean
    showWeekends: boolean
}

export const ViewMonthContent: React.FC<ViewMonthContentProps> = React.memo(({
    itemsInCell,
    isStartFromMonday,
    showWeekends,
}) => {
    const [dragInfo, setDragInfo] = useState<DragInfo | undefined>()
    const [dragStartDate, setDragStartDate] = useState<Date | undefined>()
    const {
        viewDate,
        calendarFilters,
        calendarEvents,
        selectedEvent,
        timeoffRequests,
        shifts,
    } = useCalendarState([
        'viewDate',
        'calendarFilters',
        'calendarEvents',
        'selectedEvent',
        'timeoffRequests',
        'shifts',
    ])
    const {
        moveCalendarEvent,
        resetEventForm,
        closeEventForm,
        updateSelectedEvent,
    } = useCalendarStateMethods()
    const selectEvent = useSelectEvent()
    const [selectionRange, setSelectionRange] = useSelectionRange()

    const [start] = getRangeFromView(
        viewDate,
        View.MONTH,
        {expand: true, weekStartsFromMonday: isStartFromMonday},
    ).map(d => new Date(d))

    const weeksInMonth = useMemo(() => getWeeksInMonth(viewDate), [viewDate])

    const weekDaysIndices = useMemo(() =>
        getDaysOfWeekIndices({startsFromMonday: isStartFromMonday, withoutWeekend: !showWeekends})
    , [isStartFromMonday, showWeekends])

    const eventsByDate = useMemo(
        () => {
            const selectedCalendarEvent = selectedEvent && eventDetailsToCalendarEvent(selectedEvent)
            const monthEventRange = selectionRange && getMonthEventSelectionRange(selectionRange)
            let eventMap = eventsToEventMap(calendarEvents, selectedCalendarEvent, monthEventRange)
            if (calendarFilters[DefaultCalendars.REQUESTS]) {
                eventMap = requestsToEventMap(eventMap, timeoffRequests)
            }
            if (calendarFilters[DefaultCalendars.SHIFTS]) {
                eventMap = shiftsToEventMap(eventMap, shifts)
            }
            return eventMap
        },
        [calendarEvents, timeoffRequests, shifts, selectedEvent, selectionRange, calendarFilters],
    )

    const prevSelectedEvent = usePrevious(selectedEvent)
    const isSelectedEventsEqual = useMemo(() =>
        isEqual(selectedEvent, prevSelectedEvent)
    , [selectedEvent, prevSelectedEvent])

    // We need to reset dragInfo only when selected event updating is finished
    useEffect(() => {
        if (dragInfo && !isSelectedEventsEqual) {
            setDragInfo(undefined)
            setDragStartDate(undefined)
        }
    }, [dragInfo, isSelectedEventsEqual])

    const handleDropCellDrop = useCallback(() => {
        if (!dragInfo || !dragStartDate) {
            return
        }

        const {id: itemId, start: itemStart, end: itemEnd} = dragInfo.item.data

        const dropCellDate = getDateByMonthViewIndexes(start, dragInfo.cell[0], dragInfo.cell[1])
        const diffDays = getRangeInDays(dragStartDate, dropCellDate) - 1
        const delta = diffDays * MS_IN_DAY * (dragStartDate < dropCellDate ? 1 : -1)

        if (itemId === EventCustomId.NEW_EVENT) {
            updateSelectedEvent({
                startTime: new Date(itemStart + delta).getTime(),
                endTime: new Date(itemEnd + delta).getTime(),
            })
        } else {
            moveCalendarEvent(itemId, delta)
            setDragInfo(undefined)
            setDragStartDate(undefined)
        }
    }, [
        dragInfo,
        dragStartDate,
        start,
        moveCalendarEvent,
        updateSelectedEvent,
    ])

    const handleCellMouseDown = useCallback((_: React.MouseEvent, day: Date) => {
        if (selectedEvent) {
            closeEventForm()
            return
        }
        setSelectionRange([day, day])
    }, [selectedEvent, setSelectionRange, closeEventForm])

    const handleCellMouseOver = useCallback((_: React.MouseEvent, day: Date) => {
        setSelectionRange(prev => {
            if (!prev) {
                return prev
            }
            return [prev[0], day]
        })
    }, [setSelectionRange])

    const handleCellMouseUp = useCallback((_: MouseEvent) => {
        if (!selectionRange) {
            return
        }

        const [startDate, endDate] = getMonthEventSelectionRange(selectionRange, eventsByDate)
        selectEvent({
            startTime: Number(startDate),
            endTime: Number(endDate),
        })
    }, [selectEvent, selectionRange, eventsByDate])

    useEventListener('mouseup', handleCellMouseUp)

    const handleCellKeyPressEvent = useCallback((day: Date) => {
        selectEvent(getDefaultTimeForHourEvent(day))
    }, [selectEvent])

    return (
        <div className="relative flex-1">
            <div className="absolute left-0 top-0 h-full w-full overflow-y-auto overflow-x-hidden">
                <table className="-mt-px h-full w-full table-fixed border-collapse border-spacing-0">
                    <tbody id="month-content" data-root-navigation-by-keys className="overflow-auto">
                        {Array.from(
                            {length: weeksInMonth},
                            (_, calendarWeekIndex) => (
                                <tr key={calendarWeekIndex}>
                                    {weekDaysIndices.map(weekDayIndex => {
                                        const day = getDateByMonthViewIndexes(
                                            start,
                                            calendarWeekIndex,
                                            weekDayIndex,
                                        )

                                        return (
                                            <td
                                                key={weekDayIndex}
                                                className="relative border border-solid border-cal-border p-0"
                                            >
                                                <ViewMonthEvents
                                                    day={day}
                                                    isOtherMonth={!areSameMonths(day, new Date(viewDate))}
                                                    selectedEvent={selectedEvent}
                                                    weekOfMonth={calendarWeekIndex}
                                                    dayOfWeek={weekDayIndex}
                                                    eventsByDate={eventsByDate}
                                                    itemsInCell={itemsInCell}
                                                    dragInfo={dragInfo}
                                                    dragStartDate={dragStartDate}
                                                    onDragStart={setDragStartDate}
                                                    onDropCellHover={setDragInfo}
                                                    onDrop={handleDropCellDrop}
                                                    onEventFormClose={resetEventForm}
                                                    selectionRange={selectionRange}
                                                    onCellMouseDown={handleCellMouseDown}
                                                    onCellMouseOver={handleCellMouseOver}
                                                    onCellKeyPress={handleCellKeyPressEvent}
                                                />
                                            </td>
                                        )
                                    })}
                                </tr>
                            ),
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    )
})
