import React, {useMemo} from 'react'
import type {Control} from 'react-hook-form'

import {PhoneTypes} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import type {CalendarEventFields, CalendarEventNewContact} from '@bpinc/cal-state-types'
import {SmForm} from '@bpinc/lib-external-ui-elements'
import {usePhoneValidationRules} from '@bpinc/lib-forms-base'
import {CheckboxField, HiddenField, SelectField, TextField} from '@bpinc/lib-forms-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'


type NewContactProps = {
    index: number
    control: Control<CalendarEventFields>
    contact: CalendarEventNewContact
}

export function NewContact({
    index,
    control,
    contact,
}: NewContactProps) {
    const {t: {CALENDAR_EVENT_FORM: message}} = useLocalizationState()
    const {formatPhoneType} = useFormatting()

    const optionsPhoneTypes = useMemo(() => {
        return Object.keys(PhoneTypes).map(type => ({
            value: type,
            text: formatPhoneType(type),
            'data-testid': 'option',
        }))
    }, [formatPhoneType])
    const phoneValidationRules = usePhoneValidationRules()

    return (
        <>
            <HiddenField
                control={control}
                name={`contacts.${index}`}
                defaultValue={contact}
            />
            <SmForm.Field className="mb-2">
                <TextField
                    control={control}
                    name={`contacts.${index}.name`}
                    defaultValue={contact.name}
                    placeholder={message.add_new_contact_name_placeholder()}
                    type="text"
                />
            </SmForm.Field>
            <SmForm.Group className="mb-2 items-center">
                <TextField
                    width={9}
                    control={control}
                    name={`contacts.${index}.phone.phone`}
                    defaultValue={contact.phone.phone}
                    placeholder={message.add_new_contact_phone_placeholder()}
                    type="tel"
                    rules={phoneValidationRules}
                />
                <SelectField
                    width={7}
                    control={control}
                    name={`contacts.${index}.phone.type`}
                    defaultValue={contact.phone.type}
                    options={optionsPhoneTypes}
                    selection={false}
                />
            </SmForm.Group>
            <SmForm.Field>
                <CheckboxField
                    control={control}
                    name={`contacts.${index}.create`}
                    defaultValue={contact.create}
                    label={message.add_new_contact_create_label()}
                />
            </SmForm.Field>
        </>
    )
}

