import {useEffect} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'


export function useBalancesLoader() {
    const {getTimeoffBalances} = useCalendarStateMethods()
    const calendarOwnerUserId = useCalendarState('calendarOwnerUserId')

    useEffect(() => {
        if (calendarOwnerUserId) {
            getTimeoffBalances(calendarOwnerUserId)
        }
    }, [calendarOwnerUserId, getTimeoffBalances])
}
