import React, {useCallback, useMemo} from 'react'

import {useDayOfWeek, useMonthName} from '@bpinc/cal-hooks'
import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import {type DayOfWeek, type DragInfo, type EventMapItem, EventType} from '@bpinc/cal-state-types'
import {stopPropagation} from '@bpinc/lib-helpers'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ViewMonthEvent} from '../ViewMonthEvent'


type ViewMonthMorePopupProps = {
    day: Date
    events: EventMapItem[]
    selectedEvent?: CalendarEventDetails
    dragInfo?: DragInfo
    dayOfWeek: DayOfWeek
    onDragStart: (date: Date) => void
    onEventFormClose: () => void
    onClose: () => void
}

export const ViewMonthMorePopup: React.FC<ViewMonthMorePopupProps> = props => {
    const {
        day,
        events,
        selectedEvent,
        dragInfo,
        dayOfWeek,
        onDragStart,
        onEventFormClose,
        onClose,
    } = props
    const {t} = useLocalizationState()
    const monthName = useMonthName(day)
    const dayName = useDayOfWeek(day)
    const title = useMemo(() =>
        buildTitle(day, monthName, dayName)
    , [day, monthName, dayName])

    const onMouseDown = useCallback((evt: React.MouseEvent) => {
        evt.stopPropagation()

        if (selectedEvent) {
            onEventFormClose()
        }
    }, [selectedEvent, onEventFormClose])

    const list = useMemo(() => (
        events.filter(({type}) => type !== EventType.EMPTY)
    ), [events])

    return (
        <div
            onMouseDown={onMouseDown}
            onMouseOver={stopPropagation}
            onMouseUp={stopPropagation}
        >
            <div className="flex flex-row justify-between">
                <div className="my-0 ml-0 mr-4 whitespace-nowrap text-lg/[23px]">
                    {title}
                </div>
                <SemanticButton
                    className="m-0 h-6 w-6 border-none p-0 shadow-none"
                    basic
                    icon="close"
                    tooltip={t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonClose()}
                    size="large"
                    onClick={onClose}
                />
            </div>
            <div className="mt-4">
                {list.map((item, i) => (
                    <ViewMonthEvent
                        key={i}
                        item={item}
                        selectedEvent={selectedEvent}
                        dayOfWeek={dayOfWeek}
                        dragInfo={dragInfo}
                        onDragStart={onDragStart}
                        showOccupied
                        showInModal
                    />
                ))}
            </div>
        </div>
    )
}

function buildTitle(day: Date, monthName: string, dayName: string): string {
    return `${dayName}, ${monthName} ${day.getDate()}`
}
