import React, {Fragment} from 'react'

import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import type {NotEmptyEventMapItem} from '@bpinc/cal-state-types'
import {MS_IN_DAY} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {DayEvent} from './DayEvent'


type DayEventsProps = {
    cellSize: [number, number]
    events: NotEmptyEventMapItem[][][][]
    selectedEvent?: CalendarEventDetails
    isSelectionInProgress: boolean
    start: number
    isCommunicator: boolean
}

export const DayEvents: React.FC<DayEventsProps> = ({
    start,
    events,
    isSelectionInProgress,
    selectedEvent,
    cellSize,
    isCommunicator,
}) => {
    return (
        <div
            id="day-events"
            className={tcn('absolute inset-x-0 top-0 flex h-0 w-auto select-none',
                'flex-row flex-nowrap pl-[53px]', {'pl-[58px]': isCommunicator})}
        >
            {events.map((dayBuckets, dayIndex) => {
                const dayStart = start + MS_IN_DAY * dayIndex
                const dayEnd = dayStart + MS_IN_DAY

                return (
                    <div key={dayIndex} className="relative flex-1">
                        {dayBuckets.map((bucket, bucketIndex) => (
                            <Fragment key={bucketIndex}>
                                {bucket.map((column, columnIndex) => (
                                    column.map(event => (
                                        <DayEvent
                                            key={event.data.id}
                                            item={event}
                                            selectedEvent={selectedEvent}
                                            isSelectionInProgress={isSelectionInProgress}
                                            viewStart={dayStart}
                                            viewEnd={dayEnd}
                                            cellSize={cellSize}
                                            line={columnIndex}
                                            lines={bucket.length}
                                        />
                                    ))
                                ))}
                            </Fragment>
                        ))}
                    </div>
                )
            })}
        </div>
    )
}
