import React from 'react'

import type {CalendarEventDetails} from '@bpinc/cal-request-urls'

import type {ScheduleTableRowEventData} from './ScheduleTableRowEvent'
import {ScheduleTableRowEvent} from './ScheduleTableRowEvent'
import type {ScheduleTableRowWeekTitleData} from './ScheduleTableRowWeekTitle'
import {ScheduleTableRowWeekTitle} from './ScheduleTableRowWeekTitle'


export enum ScheduleTableRowType {
    EVENT = 'EVENT',
    WEEK_TITLE = 'WEEK_TITLE',
}

export type ScheduleTableRowData = ScheduleTableRowEventData | ScheduleTableRowWeekTitleData

type ScheduleTableRowProps = {
    row: ScheduleTableRowData
    selectedEvent: CalendarEventDetails | undefined
    isCommunicator: boolean
}

export const ScheduleTableRow: React.FC<ScheduleTableRowProps> = ({
    row,
    selectedEvent,
    isCommunicator,
}) => {
    const cellStyles = 'relative h-[34px] p-0 no-underline first:w-[79px]'

    switch (row.type) {
        case ScheduleTableRowType.EVENT:
            return (
                <ScheduleTableRowEvent
                    row={row}
                    cellStyles={cellStyles}
                    selected={row.event.id === selectedEvent?.id}
                    isCommunicator={isCommunicator}
                />
            )
        case ScheduleTableRowType.WEEK_TITLE:
            return (
                <ScheduleTableRowWeekTitle
                    row={row}
                    cellStyles={cellStyles}
                    isCommunicator={isCommunicator}
                />
            )
        default:
            return null
    }
}
