import React from 'react'
import type {UseFormReturn} from 'react-hook-form'
import {DevTool} from '@hookform/devtools'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {
    EventEdit,
    EventErrors,
    EventPreview,
    useCalendarFormInit,
    useEventHandlers,
    useSubscribeToSelectedEvent,
} from '@bpinc/cal-event-ui'
import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import {useCalendarState} from '@bpinc/cal-state-context'
import type {CalendarEventFields} from '@bpinc/cal-state-types'
import {getDefaultValues, isNewEvent as isNewEventUtil} from '@bpinc/cal-utils'
import {SmForm} from '@bpinc/lib-external-ui-elements'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Loader} from '@bpinc/lib-ui-elements'

import {CalendarEventFormControls} from './CalendarEventFormControls'


export type CalendarEventFormProps = {
    form: UseFormReturn<CalendarEventFields>
    event: CalendarEventDetails
    onClose?: () => void
}

export const CalendarEventForm: React.FC<CalendarEventFormProps> = ({
    form,
    event,
    onClose,
}) => {
    const isCommunicator = useIsCommunicator()
    const {
        eventEditing,
        eventSaving,
        eventDeleting,
        calendarEventDetailsLoading,
    } = useCalendarState([
        'eventEditing',
        'eventSaving',
        'eventDeleting',
        'calendarEventDetailsLoading',
    ])

    const formIsSet = useCalendarFormInit(form, event)
    useSubscribeToSelectedEvent(form, event)

    const {
        clearDeleteResult,
        clearSaveResult,
        handleDelete,
        handleSave,
        saveRequestResult,
        deleteRequestResult,
    } = useEventHandlers(form, onClose)

    const defaultValues = getDefaultValues(event)
    const isNewEvent = isNewEventUtil(event)
    const eventId = isNewEvent ? event._temporaryId : event.id
    const isLoading = eventId && calendarEventDetailsLoading[eventId]

    return (
        <div
            className={tcn(
                'relative h-full w-[308px] overflow-x-hidden py-0 pl-3 pr-4',
                {'mt-5 w-full overflow-y-auto p-0 text-left': isCommunicator},
            )}
        >
            {isLoading && (
                <div className="absolute inset-0 bg-white opacity-50">
                    <Loader active size="medium" />
                </div>
            )}
            {!isLoading && (
                <>
                    <EventErrors
                        saveResult={saveRequestResult}
                        clearSaveResult={clearSaveResult}
                        deleteResult={deleteRequestResult}
                        clearDeleteResult={clearDeleteResult}
                    />
                    <SmForm
                        className="overflow-hidden"
                        onSubmit={form.handleSubmit(handleSave)}
                    >
                        <CalendarEventFormControls
                            defaultValues={defaultValues}
                            editing={eventEditing}
                            saving={eventSaving}
                            deleting={eventDeleting}
                            isNewEvent={isNewEvent}
                            onDelete={handleDelete}
                            isCommunicator={isCommunicator}
                        />

                        {!eventEditing && (
                            <EventPreview
                                form={form}
                                event={event}
                                showTitle={!isCommunicator}
                            />
                        )}
                        {eventEditing && formIsSet && (
                            <EventEdit
                                form={form}
                                event={event}
                            />
                        )}
                    </SmForm>
                    {process.env.NODE_ENV === 'development' && (
                        <DevTool control={form.control} />
                    )}
                </>
            )}
        </div>
    )
}
