import React, {useCallback, useEffect, useMemo, useRef} from 'react'
import {useDrag} from 'react-dnd'

import {useSelectEvent} from '@bpinc/cal-hooks'
import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import type {NotEmptyEventMapItem} from '@bpinc/cal-state-types'
import {DragItemTypes, EventCustomId, EventType} from '@bpinc/cal-state-types'
import {getEventStyles, getHeaderEventStyles} from '@bpinc/cal-utils'
import {stopPropagation, useOnKeyPressHandler, useValueUpdated} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import type {WeekDropResult} from '../ViewColumns'


export interface DayEventBlockProps extends Omit<React.HTMLAttributes<HTMLDivElement>, 'title'> {
    item: NotEmptyEventMapItem
    title: React.ReactNode | string
    className?: string
    viewStart: number
    viewEnd: number
    isSelectionInProgress: boolean
    cellSize: [number, number]
    selectedEvent?: CalendarEventDetails
    line?: number
    lines?: number
    header?: boolean
}

export const DayEventBlock: React.FC<DayEventBlockProps> = ({
    item,
    title,
    className,
    viewStart,
    viewEnd,
    isSelectionInProgress,
    selectedEvent,
    cellSize,
    line = 0,
    lines = 1,
    header,
}) => {
    // Ref to new event title to scroll to if it's not in view
    // Not for container because it can be too high, and we need to see title
    const titleRef = useRef<HTMLDivElement>(null)
    const selectEvent = useSelectEvent()

    const startTimeChanged = useValueUpdated(item.data.start)
    useEffect(() => {
        if (item.type === EventType.NEW_EVENT && startTimeChanged) {
            titleRef.current?.scrollIntoView({block: 'center', behavior: 'smooth'})
        }
    }, [item, startTimeChanged])

    const [{isDragging}, dragEventRef, previewDragRef] =
        useDrag<NotEmptyEventMapItem, WeekDropResult, { isDragging: boolean }>(() => ({
            type: header ? DragItemTypes.HEADER_EVENT : DragItemTypes.EVENT,
            item,
            collect: monitor => ({
                isDragging: monitor.isDragging(),
            }),
            end: (_, monitor) => {
                const dropResult = monitor.getDropResult()
                if (dropResult) {
                    dropResult.onEnd()
                }
            },
        }), [item, header])

    const [{isResizeDragging}, resizeEventRef, previewResizeRef] =
        useDrag<NotEmptyEventMapItem, WeekDropResult, { isResizeDragging: boolean }>(() => ({
            type: DragItemTypes.EVENT_RESIZE,
            item,
            collect: monitor => ({
                isResizeDragging: monitor.isDragging(),
            }),
            end: (_, monitor) => {
                const dropResult = monitor.getDropResult()
                if (dropResult) {
                    dropResult.onEnd()
                }
            },
        }), [item])

    const handleEventClick = useCallback(
        (e: React.MouseEvent) => {
            const id = e.currentTarget.getAttribute('data-event-id')
            id && selectEvent({id})
        },
        [selectEvent],
    )

    const handleEventPressKey = useCallback(
        (e: React.KeyboardEvent) => {
            if (!(e.target instanceof HTMLElement)) {
                return
            }

            const id = e.target.getAttribute('data-event-id')
            id && selectEvent({id})
        },
        [selectEvent],
    )

    const onKeyPressHandler = useOnKeyPressHandler(handleEventPressKey)

    const style = useMemo(() => {
        const properties = {
            viewStart,
            viewEnd,
            cellSize,
            start: item.data.start,
            end: item.data.end,
            line,
            lines,
        }
        return header ? getHeaderEventStyles(properties) : getEventStyles(properties)
    }, [item, cellSize, line, lines, header, viewEnd, viewStart])

    const noEnd = item.data.end > viewEnd
    const isSelected = item.data.id === selectedEvent?.id
    const isSelectionEvent = item.data.id === EventCustomId.SELECTION
    const isNewEvent = item.data.id === EventCustomId.NEW_EVENT

    return (
        <div
            tabIndex={0}
            data-event-id={item.data.id}
            className={tcn(
                className,
                'absolute z-0 box-border h-4.5 shrink-0 cursor-pointer overflow-hidden',
                'whitespace-nowrap border-l-4 border-solid text-cal-event-text',
                {
                    'pointer-events-none': isSelectionInProgress || isDragging || isResizeDragging || (selectedEvent && !isSelected && !isNewEvent),
                    'opacity-75': (isSelectionEvent && isSelectionInProgress) || isDragging || isResizeDragging,
                    'shadow': isSelected || isNewEvent,
                },
            )}
            style={style}
            onKeyDown={isNewEvent ? undefined : onKeyPressHandler}
            onClick={isNewEvent ? undefined : handleEventClick}
            // click within event item should not pop to the cell
            onMouseDown={stopPropagation}
        >
            <div
                className="relative z-[2] h-full w-full truncate py-0 pl-1 pr-0.5 text-sm/[1.7]"
                ref={dragEventRef}
            >
                <span ref={titleRef}>{title}</span>
            </div>
            {!header && (
                <>
                    <div
                        ref={resizeEventRef}
                        data-after="&nbsp;"
                        className={tcn('absolute inset-x-0 bottom-0 z-[2] cursor-s-resize text-center after:content-[attr(data-after)]', {
                            'hidden': noEnd,
                        })}
                    />
                    <div ref={previewResizeRef} className="invisible absolute p-2.5 opacity-0" />
                </>
            )}
            <div ref={previewDragRef} className="invisible absolute p-2.5 opacity-0" />
        </div>
    )
}
