import type {HTMLAttributes, RefObject} from 'react'
import React from 'react'
import {useDrop} from 'react-dnd'

import type {DragInfo, NotEmptyEventMapItem, ResizeInfo} from '@bpinc/cal-state-types'
import {DragItemTypes} from '@bpinc/cal-state-types'
import {CELLS_IN_DAY, getWeekCellIndices, MS_IN_CELL} from '@bpinc/cal-utils'
import {MS_IN_DAY} from '@bpinc/lib-helpers'

import type {WeekDropResult} from '../ViewColumns'


interface ColumnsDropContainerProps extends HTMLAttributes<HTMLDivElement> {
    start: number
    dragInfo?: DragInfo
    resizeInfo?: ResizeInfo
    onDrop: () => void
    onResize: () => void
    onHover: (dragInfo: DragInfo) => void
    onEventResizeMove: (resizeInfo: ResizeInfo) => void
    cellSize: [number, number]
}

export const ColumnsDropContainer = React.forwardRef<HTMLDivElement, ColumnsDropContainerProps>(({
    children,
    start,
    dragInfo,
    resizeInfo,
    onDrop,
    onResize,
    onHover,
    onEventResizeMove,
    cellSize,
    ...rest
}, ref) => {
    const [, drop] = useDrop<NotEmptyEventMapItem, WeekDropResult, null>(
        () => ({
            accept: [DragItemTypes.EVENT, DragItemTypes.EVENT_RESIZE],
            hover: (item, monitor) => {
                const dragType = monitor.getItemType()
                const diffCoords = monitor.getDifferenceFromInitialOffset()

                if (!diffCoords) {
                    return
                }

                const {x: diffX, y: diffY} = diffCoords
                const [cellWidth, cellHeight] = cellSize

                if (dragType === DragItemTypes.EVENT) {
                    const [dayIndex, cellIndex] = getWeekCellIndices(item.data.start, start)

                    if (dragInfo) {
                        let newDayIndex = dayIndex + Math.round(diffX / cellWidth)
                        let newCellIndex = cellIndex + Math.round(diffY / cellHeight)

                        if (newCellIndex < 0) {
                            newDayIndex -= Math.floor(-newCellIndex / CELLS_IN_DAY) + 1
                            newCellIndex += CELLS_IN_DAY
                        }

                        if (newDayIndex !== dragInfo.cell[0] || newCellIndex !== dragInfo.cell[1]) {
                            onHover({
                                item: dragInfo.item,
                                cell: [newDayIndex, newCellIndex],
                            })
                        }
                    } else {
                        onHover({
                            item,
                            cell: [dayIndex, cellIndex],
                        })
                    }
                }

                if (dragType === DragItemTypes.EVENT_RESIZE) {
                    const shiftX = Math.round(diffX / cellWidth) * MS_IN_DAY
                    const shiftY = Math.round(diffY / cellHeight) * MS_IN_CELL
                    const shift = shiftX + shiftY
                    if (!resizeInfo || resizeInfo.shift !== shift) {
                        onEventResizeMove({
                            item: resizeInfo?.item || item,
                            shift,
                        })
                    }
                }
            },
            drop: (_, monitor) => {
                const dragType = monitor.getItemType()
                return dragType === DragItemTypes.EVENT ? {onEnd: onDrop} : {onEnd: onResize}
            },
        }),
        [start, dragInfo, resizeInfo, cellSize],
    )

    drop(ref as RefObject<HTMLDivElement>)

    return (
        <div
            ref={ref}
            {...rest}
        >
            {children}
        </div>
    )
})
