
import type {GetDaysOfWeekOptions} from '@bpinc/cal-utils'

import {useDaysOfWeek} from './useDaysOfWeek'


type GetDayOfWeekOptions = Omit<GetDaysOfWeekOptions, 'startsFromMonday'>

export function useDayOfWeek(date: Date, options?: GetDayOfWeekOptions) {
    const daysOfWeek = useDaysOfWeek({...options, startsFromMonday: false})

    return daysOfWeek[date.getDay()]
}
