import React from 'react'

import type {DayEventProps} from './DayEvent'
import {DayEventBlock} from './DayEventBlock'


export const DayRequest: React.FC<DayEventProps> = props => {
    return (
        <DayEventBlock
            title={props.item.data.title}
            className="border-l-cal-request-border bg-cal-request-bg"
            {...props}
        />
    )
}
