import React, {useMemo} from 'react'

import {useMonthsList} from '@bpinc/cal-hooks'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import type {ScheduleTableRowType} from '.'


export interface ScheduleTableRowWeekTitleData {
    uuid: string
    type: ScheduleTableRowType.WEEK_TITLE
    date: number
}

type ScheduleTableRowWeekTitleProps = {
    row: ScheduleTableRowWeekTitleData
    cellStyles: string
    isCommunicator: boolean
}

export const ScheduleTableRowWeekTitle: React.FC<ScheduleTableRowWeekTitleProps> = ({
    row,
    cellStyles,
    isCommunicator,
}) => {
    const monthsList = useMonthsList({short: true})

    const weekTitle = useMemo(() => {
        const startDate = new Date(row.date)
        const startDay = startDate.getDate()
        const startMonthIndex = startDate.getMonth()
        const startMonthName = monthsList[startMonthIndex]

        const endDate = new Date(
            startDate.getFullYear(),
            startDate.getMonth(),
            startDay + 7,
        )
        const endDay = endDate.getDate()
        const endMonthIndex = endDate.getMonth()
        const endMonthName = monthsList[endMonthIndex]

        const shouldShowEndMonth = startMonthName !== endMonthName

        return `${startMonthName} ${startDay}-${shouldShowEndMonth ? `${endMonthName} ` : ''}${endDay}`
    }, [row.date, monthsList])

    return (
        <tr>
            <td className={cellStyles} />
            <td className={cellStyles}>
                <div
                    className={tcn(
                        'text-xs font-bold uppercase text-cal-header-text',
                        {'-translate-x-1': isCommunicator},
                    )}
                >
                    {weekTitle}
                </div>
            </td>
        </tr>
    )
}
