import {useMemo} from 'react'
import {flatten, inRange} from 'lodash-es'

import {
    type EventMap,
    type EventMapItem,
    type NewEvent,
    type NotEmptyEventMapItem,
    type RegularEvent,
    EventType,
} from '@bpinc/cal-state-types'
import {getEventMapKey, isWithinOneDay} from '@bpinc/cal-utils'
import {MS_IN_DAY} from '@bpinc/lib-helpers'


interface UseEventsListParams {
    daysAmount: number
    start: number
    events: EventMap
}

interface UseEventsListOptions {
    withinOneDay?: boolean
}

type EventsByLines = NotEmptyEventMapItem[][]

export function useAllDayEventsByLine({
    daysAmount,
    start: viewStart,
    events,
}: UseEventsListParams): [EventsByLines, number] {
    return useMemo(() => {
        const eventsByLines: EventsByLines = []
        const viewEnd = viewStart + daysAmount * MS_IN_DAY

        const eventsList = Object.values(events).flatMap(flatEvents =>
            flatEvents.filter((event: EventMapItem) =>
                isAllDayEvent(event) && isEventWithinView(event, viewStart, viewEnd),
            ),
        ) as NotEmptyEventMapItem[]

        const oldEvents = eventsList.filter(event => event.type !== EventType.NEW_EVENT)
        oldEvents.forEach(event => {
            let lineIndex = getLineIndex(eventsByLines, event)

            if (lineIndex === -1) {
            // create new line
                eventsByLines.push([])
                lineIndex = eventsByLines.length - 1
            }

            eventsByLines[lineIndex].push(event)
        })

        const newEvents = eventsList.filter(event => event.type === EventType.NEW_EVENT)
        if (newEvents.length > 0) {
            eventsByLines.unshift([])
            newEvents.forEach(event => eventsByLines[0].push(event))
        }

        return [eventsByLines, eventsByLines.length]
    }, [events, viewStart, daysAmount])
}

function isAllDayEvent(event: EventMapItem): event is NotEmptyEventMapItem {
    return (event.type === EventType.EVENT || event.type === EventType.NEW_EVENT)
        && (event.data.allDay || !isWithinOneDay(event.data.start, event.data.end))
}

function isEventWithinView(event: NotEmptyEventMapItem, viewStart: number, viewEnd: number) {
    return inRange(event.data.start, viewStart, viewEnd) ||
        inRange(event.data.end - 1, viewStart, viewEnd)
}

function getLineIndex(
    eventsByLines: EventsByLines,
    event: NotEmptyEventMapItem,
): number {
    if (!eventsByLines.length) {
        return -1
    }

    const {data: {start: eventStart, end: eventEnd}} = event

    let index = 0

    while (index < eventsByLines.length) {
        const lineHasEmptyRoom = eventsByLines[index].every(lineEvent => {
            const {
                data: {start: lineEventStart, end: lineEventEnd},
            } = lineEvent

            return (
                (lineEventStart < eventStart && lineEventEnd < eventStart) ||
                (lineEventStart > eventEnd && lineEventEnd > eventEnd)
            )
        })

        if (lineHasEmptyRoom) {
            return index
        }

        index++
    }

    return -1
}

export function useBodyEventsList(
    {daysAmount, start, events}: UseEventsListParams,
    {withinOneDay}: UseEventsListOptions = {},
) {
    return useMemo(() => {
        const eventsByDay = getEventsByDay({daysAmount, start, events})

        // First level: array of days [[<buckets for Monday>], [<buckets for Tuesday>], ...]
        // Seconds level: array of day events' buckets [[<bucket>], [<bucket>], ...]
        // One bucket is a bunch of intersecting events that are combined in columns
        // Column is an array of non-intersecting events inside the bucket
        // Example. We have only 3 intersecting events on Monday:
        // | -----  ----- |
        // | |   |  | 2 | |
        // | |   |  ----- |
        // | | 1 |        |
        // | |   |  ----- |
        // | |   |  | 3 | |
        // | -----  ----- |
        // Result: [
        //    [
        //       [
        //           [{id: 1}], // column 0
        //           [{id: 2}, {id: 3}], // column 1
        //       ], // bucket
        //    ], // monday
        //    [], // tuesday
        //    [], // wednesday
        //    [], // thursday
        //    [], // friday
        //    [], // saturday
        //    [], // sunday
        // ]
        return Array.from({length: daysAmount}).map((_, i) => {
            const buckets: NotEmptyEventMapItem[][][] = []

            eventsByDay[i].forEach(item => {
                const isWithinOneDayEvent = !item.data.allDay && isWithinOneDay(item.data.start, item.data.end)

                if ((!withinOneDay && isWithinOneDayEvent) || (withinOneDay && !isWithinOneDayEvent)) {
                    return
                }

                if (item.type === EventType.OCCUPIED) {
                    const lastBucket = buckets.length - 1
                    const lastColumn = buckets[lastBucket].length - 1
                    buckets[lastBucket][lastColumn].push(item)
                    return
                }

                let bucketIndex = buckets.findIndex(bucket =>
                    flatten(bucket).some(event => isEventInColumn(item, event)),
                )

                if (bucketIndex === -1) {
                    bucketIndex = buckets.length
                    buckets.push([])
                    buckets[bucketIndex].push([item])
                    return
                }

                let columnIndex = buckets[bucketIndex].findIndex(columns =>
                    columns.every(event => !isEventInColumn(item, event)),
                )
                if (columnIndex === -1) {
                    columnIndex = buckets[bucketIndex].length
                    buckets[bucketIndex].push([])
                }
                buckets[bucketIndex][columnIndex].push(item)
            })

            return buckets
        })
    }, [daysAmount, events, start, withinOneDay])
}

function getEventsByDay({daysAmount, start, events}: UseEventsListParams) {
    return Array.from({length: daysAmount}).map((_, i) => {
        const startOfday = start + MS_IN_DAY * i
        const key = getEventMapKey(new Date(startOfday))
        const list: NotEmptyEventMapItem[] = []

        if (key in events) {
            events[key].forEach(item => {
                if (item.type !== EventType.EMPTY) list.push(item)
            })
        }

        list.sort((a, b) => {
            if (a.type !== b.type) {
                const order: EventMapItem['type'][] = [
                    EventType.OCCUPIED,
                    EventType.EVENT,
                    EventType.NEW_EVENT,
                ]
                return order.indexOf(a.type) - order.indexOf(b.type)
            }
            return a.data.start - b.data.start
        })
        return list
    })
}

function isEventInColumn(
    event: RegularEvent | NewEvent,
    column: NotEmptyEventMapItem,
): boolean {
    const columnStart = new Date(column.data.start).setSeconds(0, 0)
    const columnEnd = new Date(column.data.end).setSeconds(0, 0)
    const eventStart = new Date(event.data.start).setSeconds(0, 0)
    const eventEnd = new Date(event.data.end).setSeconds(0, 0)

    // start or end of event is within column time range
    // (need to process edges of column to prevent inconsistent UI)
    return !(
        (eventStart < columnStart && eventEnd <= columnStart) ||
        (eventStart >= columnEnd && eventEnd > columnEnd)
    )
}
