import React from 'react'

import type {NotEmptyEventMapItem} from '@bpinc/cal-state-types'

import {useMonthEventTitle} from '../../../../../hooks/useMonthEventTitle'
import type {ViewMonthEventProps} from './ViewMonthEvent'
import {ViewMonthEventBlock} from './ViewMonthEventBlock'


type ViewMonthMeetingProps = Omit<ViewMonthEventProps, 'item'> & {
    item: NotEmptyEventMapItem
}

export const ViewMonthMeeting: React.FC<ViewMonthMeetingProps> = props => {
    const title = useMonthEventTitle(props.item)

    return (
        <ViewMonthEventBlock
            title={title}
            className="border-l-cal-event-border bg-cal-event-bg"
            classNameAllDay="!border-l-cal-day-event-border !bg-cal-day-event-bg"
            {...props}
        />
    )
}
