import {useCallback} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import {View} from '@bpinc/cal-state-types'
import {getRangeFromView} from '@bpinc/cal-utils'


export const useMoveDate = () => {
    const {viewDate, view} = useCalendarState(['viewDate', 'view'])
    const {changeViewDate} = useCalendarStateMethods()

    return useCallback((isMoveForward: boolean) => {
        const [start] = getRangeFromView(viewDate, view)
        const sampleDate = new Date(start)

        switch (view) {
            case View.MONTH: {
                const y = sampleDate.getFullYear()
                const m = sampleDate.getMonth()
                sampleDate.setFullYear(y, m + (isMoveForward ? 1 : -1), 1)
                changeViewDate(Number(sampleDate))
                break
            }
            case View.WEEK: {
                sampleDate.setHours(24 * 7 * (isMoveForward ? 1 : -1))
                changeViewDate(Number(sampleDate))
                break
            }
            case View.DAY: {
                sampleDate.setHours(24 * (isMoveForward ? 1 : -1))
                changeViewDate(Number(sampleDate))
                break
            }
            case View.SCHEDULE: {
                sampleDate.setHours(24 * (isMoveForward ? 1 : -1))
                changeViewDate(Number(sampleDate))
                break
            }
            default: {
                ((_x: never) => '')(view)
            }
        }
    }, [viewDate, view, changeViewDate])
}
