import type {
    calendar_Assignee,
    calendar_AssignEventRequest,
    calendar_ChangeCalendarColorRequest,
    calendar_ChangeCalendarPublishRequest,
    calendar_ContactItem,
    calendar_EventItemResponse,
    calendar_GetAvailabilitySettingsResponse,
    calendar_GetEventsByTimeFrameItemResponse,
    calendar_GetLocaleSettingsResponse,
    calendar_Response,
    calendar_SaveCalendarItemRequest,
    calendar_SaveCalendarItemResponse,
    calendar_UpsertAvailabilitySettingsRequest,
    calendar_UpsertCalendarRequest,
    calendar_UpsertLocaleSettingsRequest,
} from './api-service/'


export type GetCalendarEventsByTimeFrameOptions = {
    start: number
    end: number
    timezone: string
    user?: string
    calendarId?: string[]
    defaultCalendarEventsIncluded?: boolean
}

export enum EventType {
    EVENT = 'event',
    REQUEST = 'request',
    SHIFT = 'shift',
}

export type Calendar = calendar_Response

export type CalendarEvent = calendar_GetEventsByTimeFrameItemResponse

type CalendarEventCommon = Pick<CalendarEvent, 'id' | 'allDay' | 'start' | 'end' | 'title'>

type RegularCalendarItem = CalendarEventCommon & {
    type?: EventType.EVENT
}

type RequestCalendarItem = CalendarEventCommon & {
    type: EventType.REQUEST
}

type ShiftCalendarItem = CalendarEventCommon & {
    type: EventType.SHIFT
}

export type CalendarEventItem =
    | RegularCalendarItem
    | RequestCalendarItem
    | ShiftCalendarItem

export type CalendarEventDetails = calendar_EventItemResponse & {
    /** Temporary identifier of a newly created event */
    _temporaryId?: string
}

export type CalendarContact = calendar_ContactItem

export type CalendarAgendaRequest = {
    calendarId?: string[]
    defaultCalendarEventsIncluded?: boolean
}

export type UpsertCalendarRequest = calendar_UpsertCalendarRequest

export type SaveCalendarEventRequest = calendar_SaveCalendarItemRequest
export type SaveCalendarEventResponse = calendar_SaveCalendarItemResponse

export type CalendarAssignee = calendar_Assignee
export type AssignCalendarEventRequest = calendar_AssignEventRequest


export type CalendarLocaleSettings = calendar_GetLocaleSettingsResponse
export type UpsertLocaleSettingsRequest = calendar_UpsertLocaleSettingsRequest

export type CalendarAvailabilitySettings = calendar_GetAvailabilitySettingsResponse
export type UpsertAvailabilitySettingsRequest = calendar_UpsertAvailabilitySettingsRequest

export type ChangeCalendarColorRequest = calendar_ChangeCalendarColorRequest
export type ChangeCalendarPublishRequest = calendar_ChangeCalendarPublishRequest
