import React, {useMemo} from 'react'
import ReactDOM from 'react-dom'
import type {ControlPosition} from 'react-draggable'
import Draggable from 'react-draggable'

import {useBrowserTabMetaInfo} from '@bpinc/ad-current-tab-data-context'
import {useDraggableModal} from '@bpinc/lib-helpers-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Loader} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import headerIconSrc from '../../images/event-alert.svg'
import notificationBgSrc from '../../images/notification-bg.png'
import {Actions} from './Actions'
import {useDateFormatter} from './useDateFormatter'
import {useEventDetails} from './useEventDetails'
import {useTitle} from './useTitle'


export type CalendarEventNotificationModalProps = CommonModalProps & {
    eventId: string
    eventTime: number
    moreQueue?: number
    position?: ControlPosition
}

export const CalendarEventNotificationModal: React.FC<CalendarEventNotificationModalProps> = ({
    onClose,
    eventId,
    moreQueue,
    position,
    mountNode,
}) => {
    const {t} = useLocalizationState()
    const browserTabInfo = useBrowserTabMetaInfo()
    const appType = browserTabInfo?.app
    const formatDate = useDateFormatter()
    const {eventDetails, loading} = useEventDetails(eventId)
    const title = useTitle({eventDetails, moreQueue})

    const {draggableNodeRef, draggableProps} = useDraggableModal({
        modalType: 'calendar_event_notification',
        position,
    })

    const draggable = !!appType && ['ad', 'cal'].includes(appType)

    const notificationContent = useMemo(() => {
        if (!appType) {
            return null
        }

        return (
            <section
                role="alertdialog"
                aria-modal="true"
                style={draggable ? {backgroundImage: `url(${notificationBgSrc})`} : undefined}
                className={tcn(
                    'absolute inset-x-4 top-2 z-[100] rounded-md bg-cal-event-border p-4 text-main-text-inverted shadow',
                    {'fixed bottom-2 left-auto right-2 top-auto bg-repeat-y': draggable},
                )}
                aria-label={t.GLOBAL.heading_calendarAlert()}
                data-testid="calendar-event-alert"
                ref={draggable ? draggableNodeRef : undefined}
            >
                <header className="flex w-full items-center">
                    <img alt="calendar icon" src={headerIconSrc} />
                    <p className="my-0 ml-2 mr-0 h-[17px] text-base/[17px]">
                        {title}
                        {!draggable && eventDetails && ` ${formatDate(eventDetails.startTime)}`}
                    </p>
                </header>
                {draggable && (
                    <div>
                        <p className="mx-0 mb-0 mt-4 h-[17px] leading-[17px]">
                            {eventDetails && formatDate(eventDetails.startTime)}
                        </p>
                        <Loader active={loading} color="primary" size="medium" />
                    </div>
                )}
                <Actions eventId={eventId} onClose={onClose} type={appType} />
            </section>
        )
    }
    , [appType, draggable, draggableNodeRef, eventDetails, eventId, formatDate, loading, onClose, t.GLOBAL, title])

    if (!draggable) {
        return notificationContent
    }

    return (
        ReactDOM.createPortal(<Draggable {...draggableProps}>
            {notificationContent}
        </Draggable>, mountNode || document.getElementById('root') || window.document.body)
    )
}
