import {useEffect} from 'react'
import type {UseFormReturn} from 'react-hook-form'
import {isEqual} from 'lodash-es'

import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import type {CalendarEventFields} from '@bpinc/cal-state-types'
import {getDefaultValues, isFormDirty, isNewEvent, mergeEvents} from '@bpinc/cal-utils'

import {useEventDiscardModal} from './useEventDiscardModal'


export const useSubscribeToSelectedEvent = (
    form: UseFormReturn<CalendarEventFields>,
    selectedEvent: CalendarEventDetails,
) => {
    const {
        eventEditing: editing,
        nextSelectedEvent,
    } = useCalendarState([
        'eventEditing',
        'nextSelectedEvent',
    ])
    const {
        resetEventForm,
        discardEventForm,
    } = useCalendarStateMethods()

    const openDiscardModal = useEventDiscardModal()

    useEffect(() => {
        if (!editing) {
            if (!nextSelectedEvent) {
                discardEventForm()
                return
            }
        } else {
            const defaultValues = getDefaultValues(selectedEvent)
            const isNew = isNewEvent(selectedEvent)

            // close form with changes
            if (!nextSelectedEvent) {
                // check unsaved changes
                const hasChanges = isFormDirty(
                    isNew,
                    defaultValues,
                    form.getValues(),
                )

                if (!hasChanges) {
                    resetEventForm()
                    return
                }

                openDiscardModal()
                return
            }

            const eventsNotChanged = isEqual(selectedEvent, nextSelectedEvent)
            if (eventsNotChanged) {
                return
            }

            const newFormValues = mergeEvents(
                form.getValues(),
                defaultValues,
                nextSelectedEvent,
            )
            form.reset(newFormValues)
            discardEventForm()
        }
    }, [
        form,
        selectedEvent,
        nextSelectedEvent,
        editing,
        openDiscardModal,
        discardEventForm,
        resetEventForm,
    ])
}
