import {useMemo} from 'react'

import {useCalendarState} from '@bpinc/cal-state-context'
import {getRangeFromView} from '@bpinc/cal-utils'


export const useIsTodayFitsView = () => {
    const {viewDate, view} = useCalendarState(['viewDate', 'view'])

    return useMemo(() => {
        const now = Date.now()
        const [start, end] = getRangeFromView(viewDate, view)
        return now >= start && now <= end
    }, [viewDate, view])
}
