import {useCallback} from 'react'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactShortType, ContactType} from '@bpinc/ad-contacts-state-types'
import {useOpenSearchContactModal} from '@bpinc/lib-modals-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const useShowSearchContactModal = (onSelectContact: (contact: ContactType) => void) => {
    const openSearchContactModal = useOpenSearchContactModal()
    const {getContact} = useContactsStateMethods()
    const {t: {CALENDAR_EVENT_FORM: message}} = useLocalizationState()
    const handleSelectContact = useCallback(async ({id}: ContactShortType) => {
        const contact = await getContact({id, force: false})
        if (contact) {
            onSelectContact(contact)
        }
    }, [getContact, onSelectContact])

    return useCallback(() => {
        openSearchContactModal({
            dataKey: 'calendar-event-form-add-contact-modal',
            title: message.add_contact_dialog_title(),
            onSelect: handleSelectContact,
            selectButtonTitle: message.add_contact_dialog_select_button_text(),
        })
    }, [handleSelectContact, message, openSearchContactModal])
}
