import {useMemo} from 'react'

import {useTimezonesState} from '@bpinc/ad-timezones-state-context'
import {getUserTimezone} from '@bpinc/lib-helpers'


export function useLocalTimezone() {
    const {localTimezone} = useTimezonesState()

    return useMemo(() =>
        localTimezone.length ? localTimezone : getUserTimezone()
    , [localTimezone])
}
