import {useEffect, useMemo} from 'react'

import {useTimezonesState} from '@bpinc/ad-timezones-state-context'
import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import {View} from '@bpinc/cal-state-types'
import {getRangeFromView, getWeeksInMonth} from '@bpinc/cal-utils'
import {usePreviousValue, useValueUpdated} from '@bpinc/lib-helpers'


const VIEWS_ORDER: View[] = [View.MONTH, View.WEEK, View.DAY, View.SCHEDULE]

export function useEventsLoader() {
    const {getCalendarEvents} = useCalendarStateMethods()
    const {calendarOwnerUserId, viewDate, view} = useCalendarState(
        ['calendarOwnerUserId', 'viewDate', 'view'],
    )
    const {localTimezone} = useTimezonesState()
    const prevViewDate = usePreviousValue(viewDate)
    const prevView = usePreviousValue(view)
    const isViewChanged = useValueUpdated(view)

    const weeksInMonth = useMemo(() => getWeeksInMonth(viewDate), [viewDate])

    const shouldLoad = useMemo(() => {
        if (!prevViewDate) {
            return true
        }
        if (isViewChanged && prevView) {
            return VIEWS_ORDER.indexOf(prevView) > VIEWS_ORDER.indexOf(view)
        }
        if (prevViewDate === viewDate) {
            return false
        }
        const [startPrev] = getRangeFromView(prevViewDate, view)
        const [startNew] = getRangeFromView(viewDate, view)
        return startNew !== startPrev
    }, [prevViewDate, isViewChanged, prevView, viewDate, view])

    useEffect(() => {
        if (!shouldLoad) {
            return
        }

        const [start, end] = getRangeFromView(viewDate, view, {expand: weeksInMonth * 7})

        if (start && end && calendarOwnerUserId) {
            getCalendarEvents({
                user: calendarOwnerUserId,
                start,
                end,
                timezone: localTimezone,
                defaultCalendarEventsIncluded: true,
            })
        }
    }, [
        calendarOwnerUserId,
        localTimezone,
        viewDate,
        weeksInMonth,
        getCalendarEvents,
        shouldLoad,
        view,
    ])

}
