import React from 'react'

import {CalendarUserSelect} from './CalendarUserSelect'


type CalendarUserProps = {
    title: string
}

export const CalendarUser: React.FC<CalendarUserProps> = React.memo(({
    title,
}) => {
    return (
        <div className="self-center whitespace-nowrap">
            <span className="mr-1.5 text-xl text-cal-dark-text">
                {`${title}: `}
            </span>
            <CalendarUserSelect />
        </div>
    )
})
