import React, {useMemo} from 'react'

import {useCalendarState} from '@bpinc/cal-state-context'
import {eventsToEventMap} from '@bpinc/cal-utils'

import {useCalendarFilters} from '../hooks/useCalendarFilters'
import {useCalendarsLoader} from '../hooks/useCalendarsLoader'
import {CalendarBody} from './CalendarBody'
import {CalendarHeader} from './CalendarHeader'
import {CalendarSidebar} from './CalendarSidebar'


export const Calendar: React.FC = React.memo(() => {
    const {
        viewDate,
        calendarEvents,
        showCalendarSidebar,
    } = useCalendarState([
        'viewDate',
        'calendarEvents',
        'showCalendarSidebar',
    ])
    useCalendarsLoader()

    const eventsMap = useMemo(() => eventsToEventMap(calendarEvents), [calendarEvents])
    const filters = useCalendarFilters()

    return (
        <div className="z-0 flex flex-1 flex-col">
            <CalendarHeader />
            <div className="flex h-full flex-row items-stretch overflow-hidden">
                <CalendarBody />
                <CalendarSidebar
                    show={showCalendarSidebar}
                    viewDate={new Date(viewDate)}
                    eventsDatesMap={eventsMap}
                    filters={filters}
                />
            </div>
        </div>
    )
})
