
import {View} from '@bpinc/cal-state-types'
import type {WeekDay} from '@bpinc/lib-helpers'
import {
    areSameDays,
    DAYS_IN_WEEKEND,
    formatDate,
    getStartOfDay,
    isMidnight,
    MS_IN_DAY,
    MS_IN_HOUR,
    MS_IN_MINUTE,
    MS_IN_WEEK,
    WEEK_DAYS_INDICES,
    WEEK_DAYS_ORDER_MONDAY,
    WEEK_DAYS_ORDER_SUNDAY,
} from '@bpinc/lib-helpers'


export const LAST_DAY_OF_WEEK = 6
export const WEEKS_IN_MONTH = 5
export const MS_IN_ONE_MONTH = MS_IN_WEEK * WEEKS_IN_MONTH
export const MINUTES_IN_CELL = 30
export const MS_IN_CELL = MINUTES_IN_CELL * MS_IN_MINUTE


type Range = [number, number]

type Options = {
    // first/last date of month may be in a middle of the week
    // this controls whether to expand to the start/end of the week
    // number is used to fill extra week in the end of grid (e.g. Feb)
    expand?: boolean | number
    weekStartsFromMonday?: boolean
    withoutWeekend?: boolean
}

export const getDaysInMonth = (year: number, month: number): number => {
    // zero day of next month is the last day of current month
    return new Date(year, month + 1, 0).getDate()
}

/**
 * Returns number of weeks in month view
 * @param startOfMonth - start of month timestamp
 * @returns number of weeks in month view
 */
export function getWeeksInMonth(startOfMonth: number) {
    const [startOfMonthView] = getRangeFromView(startOfMonth, View.MONTH, {expand: true})

    const currentLastDay = getDateByMonthViewIndexes(
        new Date(startOfMonthView),
        WEEKS_IN_MONTH - 1,
        LAST_DAY_OF_WEEK,
    )

    const lastDayInMonth = getLastDayOfSameMonth(new Date(startOfMonth))

    if (getStartOfDay(currentLastDay) < getStartOfDay(lastDayInMonth)) {
        return WEEKS_IN_MONTH + 1
    }

    return WEEKS_IN_MONTH
}

export const getLastDayOfSameMonth = (date: Date): Date => {
    // Calculate the first day of the next month
    const firstDayNextMonth: Date = new Date(
        date.getFullYear(),
        date.getMonth() + 1,
        1,
    )
    // Subtract 1 day to get the last day of the original month
    return new Date(firstDayNextMonth.getTime() - 1)
}

export const getDayRange = (date: number, _options?: Options): Range => {
    const sampleDate = new Date(date)

    sampleDate.setHours(0, 0, 0, 0)
    const start = Number(sampleDate)

    sampleDate.setHours(24)
    const end = Number(sampleDate)

    return [start, end]
}

export const getWeekRange = (
    date: number,
    {
        weekStartsFromMonday,
        withoutWeekend,
    }: Options,
): Range => {
    const sampleDate = new Date(date)
    const weekStartShift = weekStartsFromMonday || withoutWeekend ? 0 : 1

    sampleDate.setHours(-24 * (sampleDate.getDay() - 1 + weekStartShift), 0, 0, 0)
    const start = Number(sampleDate)

    sampleDate.setHours(24 * (6 - (withoutWeekend ? DAYS_IN_WEEKEND : 0)))
    const end = Number(sampleDate)

    return [start, end]
}

export const getMonthRange = (date: number, options: Options): Range => {
    const sampleDate = new Date(date)

    sampleDate.setHours(-24 * (sampleDate.getDate() - 1), 0, 0, 0)
    const [start] = options.expand ? getWeekRange(Number(sampleDate), options) : [Number(sampleDate)]

    const daysInMonth = typeof options.expand === 'number'
        ? options.expand - 7
        : getDaysInMonth(sampleDate.getFullYear(), sampleDate.getMonth())

    sampleDate.setHours(24 * daysInMonth - 1)
    const [, end] = options.expand ? getWeekRange(Number(sampleDate), options) : [0, Number(sampleDate)]

    return [start, end]
}

const getScheduleRange = (timestamp: number): Range => {
    const startDate = new Date(timestamp)
    startDate.setHours(0, 0, 0, 0)
    const start = startDate.valueOf()

    const endDate = new Date(start)
    const HALF_OF_YEAR_IN_HOURS = 24 * 180
    endDate.setHours(HALF_OF_YEAR_IN_HOURS)
    const end = Number(endDate)

    return [start, end]
}

export const getRangeFromView = (
    date: number,
    view: 'day' | 'week' | 'month' | 'schedule',
    options: Options = {},
): Range => {
    const opts = {
        expand: false,
        weekStartsFromMonday: false,
        withoutWeekend: false,
        ...options,
    }
    switch (view) {
        case 'day':
            return getDayRange(date, opts)
        case 'week':
            return getWeekRange(date, opts)
        case 'month':
            return getMonthRange(date, opts)
        case 'schedule':
            return getScheduleRange(date)
        default:
            return [0, 0]
    }
}

export const getRangeInDays = (start: Date, end: Date) => {
    if (start > end) [start, end] = [end, start]

    let msDiff = Number(end) - Number(start)
    let daysDiff = Math.floor(msDiff / MS_IN_DAY)

    msDiff -= daysDiff * MS_IN_DAY

    const newEnd = new Date(Number(start) + msDiff)
    if (daysDiff !== 0 && start.getDate() !== newEnd.getDate()) {
        daysDiff++
    }
    return daysDiff + 1
}

export const getLasts = (lastsDays: number, dayOfWeek: number) => {
    return lastsDays + dayOfWeek <= 7
        ? // ends within current week
        lastsDays
        : // expands for the next week
        7 - dayOfWeek
}

export type GetDaysOfWeekOptions = {
    short?: boolean
    oneLetter?: boolean
    startsFromMonday?: boolean
    withoutWeekend?: boolean
}

export const getDaysOfWeek = (
    dictFull: Record<WeekDay, string>,
    dictShort: Record<WeekDay, string>,
    dictLetter: Record<WeekDay, string>,
    {
        short,
        oneLetter,
        startsFromMonday,
        withoutWeekend,
    }: GetDaysOfWeekOptions = {},
) => {
    const mondayOrder = withoutWeekend ? WEEK_DAYS_ORDER_MONDAY.slice(0, 5) : WEEK_DAYS_ORDER_MONDAY
    const sundayOrder = withoutWeekend ? WEEK_DAYS_ORDER_SUNDAY.slice(1, 6) : WEEK_DAYS_ORDER_SUNDAY
    const weekDaysOrder = startsFromMonday ? [...mondayOrder] : [...sundayOrder]

    if (short) {
        return weekDaysOrder.map(key => dictShort[key])
    }

    if (oneLetter) {
        return weekDaysOrder.map(key => dictLetter[key])
    }

    return weekDaysOrder.map(key => dictFull[key])
}

export type GetDaysOfWeekIndicesOptions = Omit<GetDaysOfWeekOptions, 'short' | 'oneLetter'>

export const getDaysOfWeekIndices = ({
    withoutWeekend,
    startsFromMonday,
}: GetDaysOfWeekIndicesOptions = {}) => {
    if (withoutWeekend) {
        return WEEK_DAYS_INDICES.slice(1, 6)
    }

    return startsFromMonday ?
        [...WEEK_DAYS_INDICES.slice(1), WEEK_DAYS_INDICES[0]] :
        [...WEEK_DAYS_INDICES]
}

export function isCurrentHalfHour(date: Date) {
    const now = new Date()
    const dateToCheck = new Date(date)

    const sameDay = now.toDateString() === dateToCheck.toDateString()
    const sameHour = now.getHours() === dateToCheck.getHours()
    const nowHalfHour = now.getMinutes() < 30 ? 0 : 1
    const dateToCheckHalfHour = dateToCheck.getMinutes() < 30 ? 0 : 1

    return sameDay && sameHour && nowHalfHour === dateToCheckHalfHour
}

export const getDateByMonthViewIndexes = (start: Date, calendarWeekIndex: number, weekDayIndex: number): Date => {
    const shift = calendarWeekIndex * 7 + weekDayIndex
    return new Date(
        start.getFullYear(),
        start.getMonth(),
        start.getDate() + shift,
    )
}

export const isTodayByDayIndex = (checkingStart: number, dayIndex: number) => {
    const startOfDay = checkingStart + MS_IN_DAY * dayIndex
    const now = Date.now()
    return startOfDay < now && now - startOfDay < MS_IN_DAY
}

export const getEventEndByStart = (startDate: Date): Date => {
    const endDate = new Date(Number(startDate))
    endDate.setMinutes(endDate.getMinutes() + MINUTES_IN_CELL)
    return endDate
}

export const getWeeksInRange = ([rangeStart, rangeEnd]: [Date, Date]): Date[] => {
    const weeksInRange = []
    const currentDate = new Date(rangeStart)

    function getWeekStart(curDate: Date) {
        const weekDayIndex = curDate.getDay()
        return new Date(new Date(curDate).setHours(-24 * weekDayIndex))
    }

    while (currentDate < rangeEnd) {
        weeksInRange.push(getWeekStart(currentDate))
        currentDate.setHours(7 * 24)
    }

    const weekStart = getWeekStart(currentDate)
    if (weekStart <= rangeEnd) {
        weeksInRange.push(weekStart)
    }

    return weeksInRange
}

export const getNextDayTime = (date: Date | number): number => {
    const time = typeof date === 'number' ? date : date.getTime()
    return time + MS_IN_DAY - 1
}

export const getDefaultTimeForHourEvent = (day: Date): {startTime: number; endTime: number} => {
    const startTime = day.setHours(8)
    const endTime = startTime + MS_IN_HOUR
    return {startTime: startTime, endTime: endTime}
}

export const isLastsAllDay = (start: number, end: number) => {
    const [startDate, endDate] = [new Date(start), new Date(end)]

    return areSameDays(startDate, endDate)
        && isMidnight(startDate)
        && endDate.getHours() === 23 && endDate.getMinutes() === 59
}

export const isWithinOneDay = (start: number, end: number) => {
    const daysRange = getRangeInDays(new Date(start), new Date(end))

    return daysRange === 1 && !isLastsAllDay(start, end)
}

export function getGMT(timezone: string | undefined) {
    const matches = timezone?.match(/GMT[-+]\d\d:\d\d/)

    return matches?.[0].replace(/:00$/, '')
}

export function formatRequestedTime(start: number, end: number): string {
    const daysRange = getRangeInDays(new Date(start), new Date(end))

    if (daysRange === 1) {
        return `${formatDate(start, 'LT')}  ${formatDate(end, 'LT')}, ${formatDate(start, 'L')}`
    }

    // @todo: 'days' localization
    return `${formatDate(start, 'L')}  ${formatDate(end, 'L')}, ${daysRange} days`
}

export function formatRequestDate(time: number): string {
    return `${formatDate(time, 'LT')}, ${formatDate(time, 'L')}`
}
