import React, {useMemo} from 'react'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import type {CalendarEvent} from '@bpinc/cal-request-urls'
import {useCalendarState} from '@bpinc/cal-state-context'
import {View} from '@bpinc/cal-state-types'
import {getRangeFromView} from '@bpinc/cal-utils'
import {DAYS_IN_WEEK, MS_IN_DAY} from '@bpinc/lib-helpers'
import {uuid} from '@bpinc/lib-uuid'

import type {ScheduleTableRowData} from './ScheduleTableRow'
import {ScheduleTableRow, ScheduleTableRowType} from './ScheduleTableRow'
import {ScheduleTimeline} from './ScheduleTimeline'


function checkIfRangesItersects(range1: [number, number], range2: [number, number]) {
    return (
        (
            range1[0] >= range2[0] &&
            range1[0] <= range2[1]
        ) ||
        (
            range1[1] >= range2[0] &&
            range1[1] <= range2[1]
        ) ||
        (
            range2[0] >= range1[0] &&
            range2[0] <= range1[1]
        ) ||
        (
            range2[1] >= range1[0] &&
            range2[1] <= range1[1]
        )
    )
}


export const ScheduleGrid: React.FC = React.memo(() => {
    const {
        viewDate,
        calendarEvents,
        selectedEvent,
    } = useCalendarState(['viewDate', 'calendarEvents', 'selectedEvent'])

    const isCommunicator = useIsCommunicator()

    const uniqueCalendarEvents = useMemo(() => {
        return calendarEvents.reduce((acc: CalendarEvent[], event: CalendarEvent) => {
            const hasEvent = acc.find(it => it.id === event.id)
            if (hasEvent) {
                return acc
            }

            return [
                ...acc,
                event,
            ]
        }, [])
    }, [calendarEvents])

    const [start, end] = getRangeFromView(viewDate, View.SCHEDULE)

    const tableRows = useMemo(() => {
        const items: ScheduleTableRowData[] = []

        let currentDateStart = start
        let daysCounter = 0
        while (currentDateStart < end) {
            const shouldShowWeekTitle = daysCounter % DAYS_IN_WEEK === 0

            if (shouldShowWeekTitle) {
                items.push({
                    type: ScheduleTableRowType.WEEK_TITLE,
                    uuid: uuid(),
                    date: currentDateStart,
                })
            }

            const currentDayEnd = currentDateStart + MS_IN_DAY - 1

            let isFirstEventInCurrentDay = true

            uniqueCalendarEvents.forEach(event => {
                const isEventIntersectsCurrentDay = checkIfRangesItersects(
                    [event.start, event.end],
                    [currentDateStart, currentDayEnd],
                )

                if (!isEventIntersectsCurrentDay) {
                    return
                }

                items.push({
                    type: ScheduleTableRowType.EVENT,
                    uuid: uuid(),
                    event,
                    isFirstEventInCurrentDay,
                    date: currentDateStart,
                })

                isFirstEventInCurrentDay = false
            })

            daysCounter++
            currentDateStart = currentDayEnd + 1
        }

        return items
    }, [uniqueCalendarEvents, start, end])

    return (
        <>
            <table
                className="-mt-px h-full w-full table-fixed border-collapse border-spacing-0"
            >
                <tbody className="overflow-auto">
                    {tableRows.map(row => (
                        <ScheduleTableRow
                            key={row.uuid}
                            row={row}
                            selectedEvent={selectedEvent}
                            isCommunicator={isCommunicator}
                        />
                    ))}
                </tbody>
            </table>

            <ScheduleTimeline tableRows={tableRows} />
        </>
    )
})
