import React, {useCallback, useEffect, useState} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {useCalendarOwner} from '@bpinc/cal-hooks'
import {useCalendarStateMethods} from '@bpinc/cal-state-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {SearchResultItem} from '@bpinc/lib-user-search-components'
import {MultipleUserSearch} from '@bpinc/lib-user-search-components'
import {useLocalizationState} from '@bpinc/state-localization-context'


export type CalendarUserSelectProps = {
    className?: string
}

const empty: SearchResultItem[] = []

export const CalendarUserSelect: React.FC<CalendarUserSelectProps> = ({
    className,
}) => {
    const {t} = useLocalizationState()
    const {setCalendarOwnerUserId} = useCalendarStateMethods()
    const {firstName, lastName} = useCalendarOwner()
    const {formatFullName} = useFormatting()

    const [isOpen, setIsOpen] = useState(false)
    const handleToggle = useCallback(() => setIsOpen(v => !v), [])
    const handleClose = useCallback(() => {
        setIsOpen(false)
        setSearchValue('')
        setSelectedItems([])
    }, [])

    const [searchValue, setSearchValue] = useState('')
    const [selectedItems, setSelectedItems] = useState<SearchResultItem[]>([])

    useEffect(() => {
        const selectedItem = selectedItems[0]

        if (selectedItem) {
            handleClose()
            setSelectedItems([])

            if (selectedItem.type === 'user') {
                setCalendarOwnerUserId(selectedItem.id)
            }
        }

    }, [selectedItems, handleClose, setCalendarOwnerUserId])

    return (
        <div className="relative inline-block">
            {isOpen ? (
                <MultipleUserSearch
                    searchForUsers
                    className={tcn('absolute left-2.5 top-[-25px] z-[3] w-[280px] rounded text-base shadow', className)}
                    searchForTeams
                    withSessionUser
                    autoFocus
                    uniqueSearchId="calendar-owner"
                    searchValue={searchValue}
                    onClickOutside={handleClose}
                    setSearchValue={setSearchValue}
                    selectedItems={empty}
                    setSelectedItems={setSelectedItems}
                    inputPlaceholder={t.CHAT_CENTER.chat_add_members_search_input_placeholder()}
                    loadingLabel={t.GLOBAL.loading()}
                />
            ) : (
                <button
                    className={tcn(
                        '-mx-1 my-0 border-none bg-none text-xl text-cal-dark-text',
                        'after:ml-[0.3em] after:inline-block after:h-0 after:w-0 after:border-solid after:content-["_"]',
                        'after:border-x-[0.3em] after:border-b-[0.1em] after:border-t-[0.3em] after:border-[#4d4d4d_transparent_transparent]',
                    )}
                    onClick={handleToggle}
                >
                    {formatFullName(firstName, lastName)}
                </button>
            )}
        </div>
    )
}
