import React, {useCallback, useLayoutEffect, useRef} from 'react'

import type {DragInfo, ResizeInfo, SelectionRange} from '@bpinc/cal-state-types'
import {
    CELLS_IN_DAY,
    getDateByCellIndex,
    getDateFromCell,
    isCurrentHalfHour,
    MINUTES_IN_CELL,
} from '@bpinc/cal-utils'
import {isHTMLDivElement} from '@bpinc/lib-html-utils'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {StateSetter} from '@bpinc/lib-types-helpers'

import {ColumnsDropContainer} from './ColumnsDropContainer'
import {DayTimeColumn} from './DayTimeColumn'
import {Timeline} from './Timeline'


type DaysColumnsProps = {
    start: number
    daysAmount: number
    headerLinesAmount: number
    cellSize: [number, number]
    dragInfo?: DragInfo
    resizeInfo?: ResizeInfo
    isCommunicator: boolean
    selectionRange: SelectionRange | undefined
    onEventDrop: () => void
    onEventResize: () => void
    onCellSizeChange: (size: [number, number]) => void
    onDropCellHover: StateSetter<DragInfo | undefined>
    onEventResizeMove: StateSetter<ResizeInfo | undefined>
    onCellMouseDown?: (e: React.MouseEvent, date: Date) => void
    onCellMouseOver?: (e: React.MouseEvent, date: Date) => void
}

export const DaysColumns: React.FC<DaysColumnsProps> = ({
    start,
    daysAmount,
    headerLinesAmount,
    cellSize,
    dragInfo,
    resizeInfo,
    isCommunicator,
    selectionRange,
    onEventDrop,
    onEventResize,
    onCellSizeChange,
    onCellMouseDown,
    onCellMouseOver,
    onDropCellHover,
    onEventResizeMove,
}) => {
    const containerRef = useRef<HTMLDivElement>(null)

    const handleMouseDown = useCallback(
        (e: React.MouseEvent) => {
            if (!onCellMouseDown || !isHTMLDivElement(e.target)) {
                return
            }

            const cell = e.target.closest('[data-cell]')
            if (!cell) {
                return
            }
            // find nearest time multiple of 30 minutes
            const date = getDateFromCell(cell, start)

            if (!date) {
                return
            }

            onCellMouseDown(e, date)
        },
        [onCellMouseDown, start],
    )
    const handleMouseOver = useCallback(
        (e: React.MouseEvent) => {
            if (!onCellMouseOver || !isHTMLDivElement(e.target)) {
                return
            }
            const cell = e.target.closest('[data-cell]')
            if (!cell) {
                return
            }
            const date = getDateFromCell(cell, start)
            if (!date) {
                return
            }

            let shift = MINUTES_IN_CELL
            if (selectionRange) {
                const [rangeStart] = selectionRange
                if (date < rangeStart) {
                    shift = 0
                }
            }
            date.setMinutes(date.getMinutes() + shift)
            onCellMouseOver(e, date)
        },
        [onCellMouseOver, start, selectionRange],
    )

    useLayoutEffect(() => {
        const container = containerRef.current
        const eventCell = container?.querySelector('[data-cell]:last-child')

        // change size of all cells depending on the size of very last cell
        let observer: ResizeObserver
        if (eventCell) {
            observer = new ResizeObserver(([resizeObserverEntry]) => {
                const {width, height} = resizeObserverEntry.target.getBoundingClientRect()
                onCellSizeChange([width, height])
            })
            observer.observe(eventCell)
        }

        return () => observer.disconnect()
    }, [onCellSizeChange])

    return (
        <ColumnsDropContainer
            ref={containerRef}
            className={tcn('m-0 h-full w-full', {
                '-mt-px': headerLinesAmount <= 1,
            })}
            start={start}
            cellSize={cellSize}
            dragInfo={dragInfo}
            resizeInfo={resizeInfo}
            onDrop={onEventDrop}
            onHover={onDropCellHover}
            onResize={onEventResize}
            onEventResizeMove={onEventResizeMove}
            onMouseDown={handleMouseDown}
            onMouseOver={onCellMouseOver && handleMouseOver}
        >
            <div id="day-columns" className="flex">
                <DayTimeColumn isCommunicator={isCommunicator} />
                {Array.from({length: daysAmount}).map((_, columnIndex) => {
                    return (
                        <div
                            data-element="day-column"
                            tabIndex={-1}
                            key={columnIndex}
                            className="grow"
                        >
                            {Array.from(
                                {length: CELLS_IN_DAY},
                                (__, lineIndex) => {
                                    const first = lineIndex === 0
                                    const className = tcn(
                                        'relative h-6 select-none',
                                        'border border-l-0 border-solid border-cal-border',
                                        {'border-b-0 border-t': first},
                                        {'odd:border-y-0 even:border-t-0': !first},
                                    )

                                    return (
                                        <div
                                            key={lineIndex}
                                            className={className}
                                            data-cell
                                            data-x={columnIndex}
                                            data-y={lineIndex}
                                        >
                                            {isCurrentHalfHour(
                                                getDateByCellIndex(
                                                    start,
                                                    columnIndex,
                                                    lineIndex,
                                                ),
                                            ) && <Timeline />}
                                        </div>
                                    )
                                },
                            )}
                        </div>
                    )
                })}
            </div>
        </ColumnsDropContainer>
    )
}
