import React from 'react'

import {useCalendarStateMethods} from '@bpinc/cal-state-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {CalendarSidebarButton} from './CalendarSidebar/CalendarSidebarButton'
import {BatchAssignEvents} from './BatchAssignEvents'
import {CalendarNavigation} from './CalendarNavigation'
import {CalendarUser} from './CalendarUser'
import {CalendarViewSelect} from './CalendarViewSelect'
import {CalendarViewTitle} from './CalendarViewTitle'


export const CalendarHeader: React.FC = () => {
    const {t} = useLocalizationState()
    const {toggleCalendarSidebar} = useCalendarStateMethods()

    return (
        <div>
            <div className="mb-1 flex flex-wrap items-start justify-end gap-x-4 gap-y-1">
                <div className="flex flex-1 justify-between gap-x-4">
                    <CalendarUser title={t.CALENDAR.calendar_heading()} />
                    <BatchAssignEvents />
                </div>
                <div className="flex gap-x-4">
                    <CalendarViewSelect />
                    <CalendarSidebarButton
                        onToggle={toggleCalendarSidebar}
                        tooltip={t.CALENDAR.month_nav_btn_tooltip()}
                    />
                </div>
            </div>
            <div className="mb-4 flex flex-row items-center justify-between after:flex-1">
                <CalendarNavigation />
                <CalendarViewTitle />
            </div>
        </div>
    )
}
