import dayjs from 'dayjs'

import {getRangeInDays} from '@bpinc/cal-utils'
import {areSameDays} from '@bpinc/lib-helpers'
import type {Language} from '@bpinc/state-localization'

import {createIntlFormatters} from './intlFormatters'


export const intlFormatters = createIntlFormatters()


type FormatEventTimesProps = {
    startTime: string | number
    endTime?: string | number
    allDay?: boolean
    allDayMessage?: string
    locale: Language
    timeZone?: string
}

type TimeDescriptor = {
    date: string
    time: string
}

type FormatEventTimesReturn = {
    from: TimeDescriptor
    to?: TimeDescriptor
}

/** Formats Event's from/to dates */
export const formatEventTimes = ({
    startTime,
    endTime,
    locale,
    timeZone,
    allDay = false,
    allDayMessage = '',
}: FormatEventTimesProps): FormatEventTimesReturn => {
    const {fullDate, h, hmm, hhmm, normalize} = intlFormatters({locale, timeZone})

    const startDate = typeof startTime === 'number'
        ? new Date(startTime)
        : dayjs(startTime).toDate()

    const from = {
        date: fullDate.format(startDate),
        time: '',
    }
    let to: TimeDescriptor | undefined

    if (!endTime) {
        from.time = normalize(hhmm.format(startDate))
        return {from, to}
    }

    const endDate = typeof endTime === 'number'
        ? new Date(endTime)
        : dayjs(endTime).toDate()

    if (allDay) {
        const daysRange = getRangeInDays(startDate, endDate)
        if (daysRange > 1) {
            to = {
                date: fullDate.format(endDate),
                time: allDayMessage,
            }
        } else {
            from.time = allDayMessage
        }
    } else if (areSameDays(startDate, endDate)) {
        from.time = normalize(
            hmm.formatRange(startDate, endDate),
        )
    } else {
        from.time = dayjs(startDate).minute() === 0
            ? h.format(startDate)
            : hmm.format(startDate)

        to = {
            date: fullDate.format(endDate),
            time: dayjs(endDate).minute() === 0
                ? h.format(endDate)
                : hmm.format(endDate),
        }
        from.time = normalize(from.time)
        to.time = normalize(to.time)
    }

    return {from, to}
}

/**
 * Merges date and time
 * @param dateString ISODate, from which the date is taken (the time is ignored)
 * @param timeString ISODate, from which the time is taken (the date is ignored)
 * @returns timestamp of merged date and time
 */
export const mergeDateTime = (dateString: string, timeString: string): number => {
    const date = new Date(dateString)
    const time = new Date(timeString)
    date.setHours(time.getHours())
    date.setMinutes(time.getMinutes())
    date.setSeconds(time.getSeconds())

    return Number(date)
}
