import React, {useCallback, useMemo} from 'react'

import type {ContactType} from '@bpinc/ad-contacts-state-types'
import type {CalendarEventContact} from '@bpinc/cal-state-types'
import {useBoolean, useOnKeyPressHandler} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {PopupButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useShowSearchContactModal} from '../../hooks/useShowSearchContactModal'
import {formatContactToContactsFields, getDefaultNewContact} from '../../utils'


type AddContactButtonProps = {
    onAdd: (contacts: CalendarEventContact) => void
}

export function AddContactButton({
    onAdd,
}: AddContactButtonProps) {
    const {value: show, toggle} = useBoolean()
    const {t: {CALENDAR_EVENT_FORM: message}} = useLocalizationState()

    const handleSelectedContact = useCallback((contact: ContactType) => {
        onAdd(formatContactToContactsFields(contact))
    }, [onAdd])

    const openSearchContactModal = useShowSearchContactModal(handleSelectedContact)

    const handleSearchContact = useCallback(() => {
        openSearchContactModal()
        toggle()
    }, [toggle, openSearchContactModal])

    const handleAddContactManually = useCallback(() => {
        onAdd(getDefaultNewContact())
        toggle()
    }, [toggle, onAdd])

    const onKeyPressHandler = useOnKeyPressHandler(toggle)

    const options = useMemo(() => {
        return [
            {
                children: message.field_contact_dropdown_search_label(),
                onClick: handleSearchContact,
                id: 'calendar-event-form-add-contact-popup',
            },
            {
                children: message.field_contact_dropdown_manually_label(),
                onClick: handleAddContactManually,
                id: 'calendar-event-form-add-contact-manually-button',
            },
        ]
    }, [message, handleAddContactManually, handleSearchContact])

    return (
        <PopupButton
            show={show}
            toggleShow={toggle}
            onKeyDown={onKeyPressHandler}
            options={options}
            size="small"
            optionClassName={tcn(
                'relative flex w-full cursor-pointer border-none bg-transparent px-3 py-1.5 text-sm/[16px] text-black',
                'focus-within:bg-menu-bg-hover hover:bg-menu-bg-hover focus:bg-menu-bg-hover',
            )}
            content={message.field_contact_label()}
        />
    )
}
