import {useEffect, useMemo} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'
import {useUserDetailsById, useUserDetailsOnceGetter} from '@bpinc/ad-user-details-state-context'
import {useCalendarState} from '@bpinc/cal-state-context'

//TODO: Move this logic to the (non-app-connector) calendar context once it created
export function useCalendarOwner() {
    const {userId: myUserId, firstName: myFirstName, lastName: myLastName} = useSessionState()
    const calendarOwnerUserId = useCalendarState('calendarOwnerUserId')
    const getUserDetailsOnce = useUserDetailsOnceGetter()
    const userDetails = useUserDetailsById(calendarOwnerUserId)

    const user = useMemo(() => {
        if (calendarOwnerUserId === myUserId) {
            return {
                userId: calendarOwnerUserId,
                firstName: myFirstName || '',
                lastName: myLastName || '',
            }
        }

        const details = userDetails?.data

        return {
            userId: calendarOwnerUserId,
            firstName: details?.firstName || '',
            lastName: details?.lastName || '',
        }
    }, [calendarOwnerUserId, myUserId, userDetails, myFirstName, myLastName])

    useEffect(() => {
        let timeout: number | undefined

        if (!user) {
            timeout = setTimeout(() => {
                getUserDetailsOnce({userIds: [calendarOwnerUserId]})
            }, 500)
        }

        return () => clearTimeout(timeout)
        // do not track methods
        // eslint-disable-next-line
    }, [calendarOwnerUserId])

    return user
}
