import React from 'react'

import {EventType as EventBlockType} from '@bpinc/cal-request-urls'
import type {EventMapItem} from '@bpinc/cal-state-types'
import {EventType} from '@bpinc/cal-state-types'

import {ViewMonthEmptyBlock} from './ViewMonthEmptyBlock'
import {type ViewMonthEventBlockProps} from './ViewMonthEventBlock'
import {ViewMonthMeeting} from './ViewMonthMeeting'
import {ViewMonthRequest} from './ViewMonthRequest'
import {ViewMonthShift} from './ViewMonthShift'


export type ViewMonthEventProps =
    Omit<ViewMonthEventBlockProps, 'item' | 'title' | 'className' | 'classNameAllDay'>
    & {
        item: EventMapItem
    }

export const ViewMonthEvent: React.FC<ViewMonthEventProps> = ({
    item,
    ...rest
}) => {
    if (item.type === EventType.EVENT || item.type === EventType.NEW_EVENT ||
        (item.type === EventType.OCCUPIED && (rest.dayOfWeek === 0 || rest.showOccupied))
    ) {
        switch (item.data.type) {
            case EventBlockType.REQUEST:
                return <ViewMonthRequest item={item} {...rest} />
            case EventBlockType.SHIFT:
                return <ViewMonthShift item={item} {...rest} />
            default:
                return <ViewMonthMeeting item={item} {...rest} />
        }
    }

    return <ViewMonthEmptyBlock />
}
