import React from 'react'

import {useDaysOfWeek} from '@bpinc/cal-hooks'
import {tcn} from '@bpinc/lib-tailwind-classnames'


type ViewMonthHeaderProps = {
    isCommunicator: boolean
    isStartFromMonday: boolean
    showWeekends: boolean
}

export const ViewMonthHeader: React.FC<ViewMonthHeaderProps> = React.memo(({
    isCommunicator,
    isStartFromMonday,
    showWeekends,
}) => {
    const daysOfWeek = useDaysOfWeek(isCommunicator ?
        {oneLetter: true, startsFromMonday: isStartFromMonday, withoutWeekend: !showWeekends} :
        {short: true, startsFromMonday: isStartFromMonday, withoutWeekend: !showWeekends},
    )

    return (
        <div className="relative flex-none">
            <table className="-mt-px h-full w-full table-fixed border-collapse border-spacing-0">
                <thead className="border-b border-solid border-b-cal-border">
                    <tr>
                        {daysOfWeek.map((name, index) => (
                            <th
                                key={`${name}_${index}`}
                                className={tcn(
                                    'h-11 text-center text-sm font-bold uppercase text-cal-header-text',
                                    {'h-5 border-none': isCommunicator},
                                )}
                            >
                                {name}
                            </th>
                        ))}
                    </tr>
                </thead>
            </table>
        </div>
    )
})
