import React from 'react'

import {Svg} from '@bpinc/lib-ui-elements'

import {acceptedIcon, deniedIcon, pendingIcon, waitlistedIcon} from '../../../icons'
import {RequestStatus} from './types'


type Props = {
    status: RequestStatus
}

export const StatusIcon: React.FC<Props> = ({status}) => {
    switch (status) {
        case RequestStatus.APPROVED:
            return <Svg src={acceptedIcon} />
        case RequestStatus.DENIED:
            return <Svg src={deniedIcon} />
        case RequestStatus.WAITLISTED:
            return <Svg src={waitlistedIcon} />
        case RequestStatus.PENDING:
            return <Svg src={pendingIcon} />
        default:
            return null
    }
}
