import React from 'react'

import {useCalendarState} from '@bpinc/cal-state-context'

import {Balance} from './Balance'


export const Balances: React.FC = React.memo(() => {
    const {timeoffBalances, timeoffTypes} = useCalendarState([
        'timeoffBalances',
        'timeoffTypes',
    ])

    if (!timeoffBalances.length) {
        return null
    }

    return (
        <div className="mb-4 flex px-3 pt-4">
            {timeoffBalances.map(balance => {
                const timeoffType = timeoffTypes.find(
                    type => type.id === balance.time_off_type_id,
                )

                if (!timeoffType) return null

                return (
                    <Balance
                        key={balance.time_off_type_id}
                        timeoffType={timeoffType}
                        balance={balance}
                    />
                )
            })}
        </div>
    )
})
