import {useCallback} from 'react'
import queueMicrotask from 'queue-microtask'

import {useSelectEvent} from '@bpinc/cal-hooks'
import {useCalendarStateMethods} from '@bpinc/cal-state-context'
import type {VoidFunction} from '@bpinc/lib-types-helpers'


interface Params {
    onClose: VoidFunction
    eventId: string | undefined
}

export function useHandlers({onClose, eventId}: Params) {
    const {completeCalendarEvent, snoozeCalendarEvent} = useCalendarStateMethods()
    const selectEvent = useSelectEvent()
    const handleOpen = useCallback(() => {
        if (eventId) {
            completeCalendarEvent(eventId)
            queueMicrotask(() => selectEvent({id: eventId}))
        }

        onClose()
    }, [completeCalendarEvent, eventId, onClose, selectEvent])

    const handleSnooze = useCallback(() => {
        if (eventId) {
            snoozeCalendarEvent(eventId)
        }

        onClose()
    }, [eventId, onClose, snoozeCalendarEvent])

    const handleDismiss = useCallback(() => {
        if (eventId) {
            completeCalendarEvent(eventId)
        }

        onClose()
    }, [completeCalendarEvent, eventId, onClose])

    return {handleDismiss, handleSnooze, handleOpen}
}

