import React from 'react'

import type {PhoneTypes} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'


type ContactPhoneLabelProps = {
    type: PhoneTypes
    phone: string
}

export function ContactPhoneLabel({phone, type}: ContactPhoneLabelProps) {
    const {formatPhoneType} = useFormatting()

    return (
        <div>
            <span className="text-primary-legacy">{formatPhone(phone)}</span>
            {' '}
            <span>
                {`(${formatPhoneType(type)})`}
            </span>
        </div>
    )
}
