import React, {useCallback, useMemo} from 'react'
import {useDrop} from 'react-dnd'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import type {
    DayOfWeek,
    DragInfo,
    EventMap,
    EventMapItem,
    NotEmptyEventMapItem,
    SelectionRange,
    WeekOfMonth,
} from '@bpinc/cal-state-types'
import {DragItemTypes, EventType} from '@bpinc/cal-state-types'
import {
    checkIfCellIsDropTarget,
    checkIfCellIsSelected,
    getEventMapKey,
    getInfoForMore,
    getSelectionRange,
} from '@bpinc/cal-utils'
import {isToday, stopPropagation, useOnKeyPressHandler} from '@bpinc/lib-helpers'
import {isHTMLDivElement} from '@bpinc/lib-html-utils'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {StateSetter} from '@bpinc/lib-types-helpers'

import {useOpenDayView} from '../../../../hooks/useOpenDayView'
import {MoreEventsPopupOpener, MoreEventsPopupToggle} from './MoreEventsPopupOpener'
import {ViewMonthEvent} from './ViewMonthEvent'


export interface ViewMonthEventsProps {
    day: Date
    dayOfWeek: DayOfWeek
    dragInfo?: DragInfo
    dragStartDate?: Date
    onDragStart: StateSetter<Date | undefined>
    eventsByDate: EventMap
    itemsInCell: number
    onCellMouseDown: (e: React.MouseEvent, day: Date) => void
    onCellMouseOver: (e: React.MouseEvent, day: Date) => void
    onCellKeyPress: (day: Date) => void
    onDrop: () => void
    onDropCellHover: StateSetter<DragInfo | undefined>
    onEventFormClose: () => void
    weekOfMonth: WeekOfMonth
    selectedEvent?: CalendarEventDetails
    selectionRange?: SelectionRange
    isOtherMonth: boolean
}

const noEvents: EventMapItem[] = []

export const ViewMonthEvents: React.FC<ViewMonthEventsProps> = ({
    day,
    dayOfWeek,
    dragInfo,
    dragStartDate,
    onDragStart,
    eventsByDate,
    itemsInCell,
    onCellMouseDown,
    onCellMouseOver,
    onCellKeyPress,
    onDrop,
    onDropCellHover,
    onEventFormClose,
    weekOfMonth,
    selectedEvent,
    selectionRange,
    isOtherMonth,
}) => {
    const events = eventsByDate[getEventMapKey(day)] || noEvents

    const isCommunicator = useIsCommunicator()

    const openDayView = useOpenDayView()

    const [{isDragging}, drop] = useDrop(
        () => ({
            accept: DragItemTypes.EVENT,
            hover: (item: NotEmptyEventMapItem) => {
                const cell: [number, number] = [weekOfMonth, dayOfWeek]

                if (
                    !dragInfo ||
                    dragInfo.item.data.id !== item.data.id ||
                    dragInfo.cell[0] !== cell[0] ||
                    dragInfo.cell[1] !== cell[1]
                ) {
                    onDropCellHover({
                        cell,
                        item,
                    })
                }
            },
            collect: monitor => ({
                isDragging: !!monitor.getItem(),
            }),
            drop: onDrop,
        }),
        [dragInfo],
    )

    const [hasMore, moreAmount] = getInfoForMore(events, itemsInCell, eventsByDate)

    const cellEvents = useMemo(() => {
        return [...events]
            // show new event on the top
            .sort((a, b) => {
                const isANewEvent = a.type === EventType.NEW_EVENT
                const isBNewEvent = b.type === EventType.NEW_EVENT
                return Number(isBNewEvent) - Number(isANewEvent)
            })
            // add space for has more link
            .slice(0, hasMore ? itemsInCell - 1 : itemsInCell)
    }, [events, hasMore, itemsInCell])

    const handleCellMouseDown = useCallback(
        (e: React.MouseEvent) => {
            onCellMouseDown(e, day)
        },
        [day, onCellMouseDown],
    )
    const handleCellMouseOver = useCallback(
        (e: React.MouseEvent) => {
            onCellMouseOver(e, day)
        },
        [day, onCellMouseOver],
    )
    const handleDayClick = useCallback(() => {
        openDayView(day)
    }, [day, openDayView])

    const handleCellKeyPressHandler = useCallback((e: React.KeyboardEvent) => {
        if (!isHTMLDivElement(e.target) || e.target.dataset.element !== 'month-cell') {
            return
        }
        onCellKeyPress(day)
    }, [onCellKeyPress, day])

    const onKeyPressCellHandler = useOnKeyPressHandler(handleCellKeyPressHandler)
    const onKeyPressDayHandler = useOnKeyPressHandler(handleDayClick)

    const isSelectionInProgress = !!selectionRange
    const range = selectionRange || getSelectionRange(selectedEvent)
    const isSelected = range && checkIfCellIsSelected(day, range)
    const isDropTarget = dragInfo && checkIfCellIsDropTarget(weekOfMonth, dayOfWeek, dragInfo, dragStartDate)

    return (
        <div
            tabIndex={0}
            data-element="month-cell"
            data-day={Number(day)}
            className={tcn('h-full', {
                'bg-[#bce8f1]':
                    (!isDragging && !dragInfo && isSelected) || isDropTarget,
            })}
            ref={drop}
            onMouseDown={handleCellMouseDown}
            onMouseOver={isSelectionInProgress ? handleCellMouseOver : undefined}
            onKeyDown={onKeyPressCellHandler}
        >
            <div className="absolute left-0 top-0 flex h-full w-full flex-col items-stretch">
                <div
                    className={tcn('flex justify-start px-4 py-1', {
                        'justify-end p-0.5': isCommunicator,
                    })}
                >
                    <button
                        onClick={handleDayClick}
                        onMouseDown={stopPropagation}
                        onMouseOver={stopPropagation}
                        onMouseUp={stopPropagation}
                        onKeyDown={onKeyPressDayHandler}
                        className={tcn(
                            'flex h-6 w-6 select-none items-center justify-center text-base text-primary-legacy',
                            {
                                'rounded-full bg-primary-legacy text-primary-contrast': isToday(day),
                                'h-5 w-5 text-sm underline': isCommunicator,
                                'pointer-events-none': isSelectionInProgress || selectedEvent,
                                'opacity-50': isOtherMonth,
                            },
                        )}
                    >
                        {day.getDate()}
                    </button>
                </div>
                <div
                    id="month-events-container"
                    className="flex shrink grow basis-full flex-col items-center justify-start"
                >
                    {cellEvents.map((item, i) => (
                        <ViewMonthEvent
                            key={i}
                            item={item}
                            selectedEvent={selectedEvent}
                            dayOfWeek={dayOfWeek}
                            dragInfo={dragInfo}
                            onDragStart={onDragStart}
                            isSelectionInProgress={isSelectionInProgress}
                        />
                    ))}
                    {hasMore && (
                        <MoreEventsPopupOpener
                            events={events}
                            selectedEvent={selectedEvent}
                            onEventFormClose={onEventFormClose}
                            day={day}
                            dayOfWeek={dayOfWeek}
                            onDragStart={onDragStart}
                        >
                            {({toggle}) => (
                                <MoreEventsPopupToggle
                                    eventsCount={moreAmount}
                                    onClick={toggle}
                                />
                            )}
                        </MoreEventsPopupOpener>
                    )}
                </div>
            </div>
        </div>
    )
}
