import {useCallback, useRef} from 'react'

import {useCalendarStateMethods} from '@bpinc/cal-state-context'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'


/**
 * Opens the Discard event dialog, restores selectedEvent
 */
export const useEventDiscardModal = () => {
    const {
        resetEventForm,
        continueEventForm,
    } = useCalendarStateMethods()

    const isDialogOpenRef = useRef(false)

    const handleDiscard = useCallback(() => {
        isDialogOpenRef.current = false
        resetEventForm()
    }, [resetEventForm])

    const handleCancel = useCallback(() => {
        isDialogOpenRef.current = false
        continueEventForm()
    }, [continueEventForm])

    const {openModal} = useModalsStateMethods()

    const {t: {CALENDAR_EVENT_FORM: message}} = useLocalizationState()

    return useCallback(() => {
        if (!isDialogOpenRef.current) {
            isDialogOpenRef.current = true

            openModal({
                actions: {
                    ok: {
                        action: handleDiscard,
                        title: message.discard_modal_discard(),
                    },
                    cancel: {
                        action: handleCancel,
                        title: message.discard_modal_cancel(),
                    },
                },
                afterClose() {
                    if (isDialogOpenRef.current) {
                        isDialogOpenRef.current = false
                        handleCancel()
                    }
                },
                body: message.discard_modal_text(),
            })
        }
    }, [
        message,
        openModal,
        handleDiscard,
        handleCancel,
    ])
}
