import React from 'react'

import {SemanticButton} from '@bpinc/lib-ui-elements'


export type CalendarSidebarPaginationProps = {
    goNextPage: () => void
    goPreviousPage: () => void
    nextTooltip?: string
    previousTooltip?: string
}

export const CalendarSidebarPagination: React.FC<CalendarSidebarPaginationProps> = ({
    goNextPage,
    goPreviousPage,
    nextTooltip,
    previousTooltip,
}) => {
    return (
        <div className="absolute right-3 top-3 flex">
            <SemanticButton
                transparent
                className="m-0 h-6 w-6"
                onClick={goPreviousPage}
                tooltip={previousTooltip}
                icon="chevron left"
                enabledInOffline
            />
            <SemanticButton
                transparent
                className="m-0 h-6 w-6"
                onClick={goNextPage}
                tooltip={nextTooltip}
                icon="chevron right"
                enabledInOffline
            />
        </div>
    )
}
