import React, {useCallback} from 'react'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {useSelectEvent} from '@bpinc/cal-hooks'
import {useCalendarStateMethods} from '@bpinc/cal-state-context'
import {SmIcon, SmMenu} from '@bpinc/lib-external-ui-elements'
import {getStartOfDay, MS_IN_HOUR} from '@bpinc/lib-helpers'
import {IconCalendarToday, IconChevronLeft, IconChevronRight} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useIsTodayFitsView} from '../hooks/useIsTodayFitsView'
import {useMoveDate} from '../hooks/useMoveDate'


export const CalendarNavigation: React.FC = () => {
    const isCommunicator = useIsCommunicator()
    const {changeViewDate} = useCalendarStateMethods()

    const {t} = useLocalizationState()
    const selectEvent = useSelectEvent()
    const moveDate = useMoveDate()

    const handlePrevClick = useCallback(() => {
        moveDate(false)
    }, [moveDate])

    const handleNextClick = useCallback(() => {
        moveDate(true)
    }, [moveDate])

    const handleTodayClick = useCallback(() => {
        changeViewDate(Date.now())
    }, [changeViewDate])

    const handleAddEventClick = useCallback(() => {
        selectEvent({
            startTime: getStartOfDay(new Date().valueOf()) + (8 * MS_IN_HOUR),
            endTime: getStartOfDay(new Date().valueOf()) + (9 * MS_IN_HOUR),
        })
    }, [selectEvent])

    const todayFitsView = useIsTodayFitsView()

    return (
        <div className="flex flex-1">
            <SemanticButton
                size="small"
                onClick={handleAddEventClick}
                className={tcn({'mr-2': !isCommunicator})}
                icon="plus"
                color={!isCommunicator ? 'blue' : undefined}
                tooltip={t.CALENDAR.add_event_btn_tooltip()}
            />
            <SemanticButton
                basic
                size="small"
                color={todayFitsView ? 'grey' : 'blue'}
                disabled={todayFitsView}
                onClick={handleTodayClick}
                className={tcn('mr-2', {
                    'px-2 py-1.5': isCommunicator,
                })}
                tooltip={t.CALENDAR.today()}
            >
                {isCommunicator ? <IconCalendarToday /> : t.CALENDAR.today()}
            </SemanticButton>
            <SmMenu compact className="m-0 min-h-0 shadow-none last:m-0">
                <SmMenu.Item
                    as="button"
                    className={tcn('p-[9px]', {
                        'h-8 w-8 px-[11px] py-[7px]': isCommunicator,
                    })}
                    onClick={handlePrevClick}
                    title={t.GLOBAL.back()}
                >
                    {isCommunicator ? (
                        <IconChevronLeft className="h-4.5 w-2.5" />
                    ) : <SmIcon className="m-0" name="angle left" />}
                </SmMenu.Item>
                <SmMenu.Item
                    as="button"
                    className={tcn('p-[9px]', {
                        'h-8 w-8 px-[11px] py-[7px]': isCommunicator,
                    })}
                    onClick={handleNextClick}
                    title={t.GLOBAL.next()}
                >
                    {isCommunicator ? (
                        <IconChevronRight className="h-4.5 w-2.5" />
                    ) : <SmIcon className="m-0" name="angle right" />}
                </SmMenu.Item>
            </SmMenu>
        </div>
    )
}
