import {useCallback} from 'react'

import {isHTMLElement} from '@bpinc/lib-html-utils'


const MOVE_KEYS = ['ArrowDown', 'ArrowUp', 'ArrowLeft', 'ArrowRight']
const VIEW_WEEK_ELEMENTS = ['day-header-cell', 'all-day-cell', 'day-column']


function checkDayViewElement(element: HTMLElement) {
    if (element.dataset.element && VIEW_WEEK_ELEMENTS.includes(element.dataset.element)) {
        return element
    }
}

export const useHandlerMoveKeysCalendar = () => {
    return useCallback((e: KeyboardEvent) => {
        if (MOVE_KEYS.includes(e.key) && e.target && isHTMLElement(e.target)) {
            if (e.target.dataset.element === 'month-cell') {
                handlerMoveKeysMonthView(e.key, e.target)
                return
            }
            if (checkDayViewElement(e.target)) {
                handlerMoveKeysDayView(e.key, e.target)
                return
            }
            if (e.target.dataset.element === 'timezone-item') {
                handlerVerticalNavigationElements(e.key, e.target)
                return
            }
        }
    }, [])
}

function handlerMoveKeysMonthView(key: string, currentElement: HTMLElement) {
    const daysOfMonthView = getNavigationByKeysElements(currentElement)
    if (!daysOfMonthView?.length) {
        return
    }
    let currentFocusIndex = daysOfMonthView.indexOf(currentElement)
    switch (key) {
        case 'ArrowDown':
            if (currentFocusIndex + 7 < daysOfMonthView.length) {
                currentFocusIndex = currentFocusIndex + 7
            } else {
                currentFocusIndex = (currentFocusIndex + 1) % 7
            }
            return daysOfMonthView[currentFocusIndex].focus()
        case 'ArrowUp':
            if (currentFocusIndex >= 7) {
                currentFocusIndex = currentFocusIndex - 7
            } else {
                currentFocusIndex = currentFocusIndex === 0 ? daysOfMonthView.length - 1 : currentFocusIndex + 27
            }
            return daysOfMonthView[currentFocusIndex].focus()
        case 'ArrowLeft':
            if (currentFocusIndex === 0) {
                currentFocusIndex = daysOfMonthView.length
            }
            if (currentFocusIndex > 0) {
                currentFocusIndex--
            }
            return daysOfMonthView[currentFocusIndex].focus()
        case 'ArrowRight':
            if (currentFocusIndex === daysOfMonthView.length - 1) {
                currentFocusIndex = 0
            } else if (currentFocusIndex < daysOfMonthView.length - 1) {
                currentFocusIndex++
            }
            return daysOfMonthView[currentFocusIndex].focus()
        default:
            return
    }
}
function handlerMoveKeysDayView(key: string, currentElement: HTMLElement) {
    const headerScaleDateRow = document.getElementById('row-header-days')
    const allDayRow = document.getElementById('row-all-day-cells')
    const bodyTableOfDayColumns = document.getElementById('day-columns')
    if (!allDayRow || !headerScaleDateRow || !bodyTableOfDayColumns) {
        return
    }
    const headerScaleDateElements = Array.from(headerScaleDateRow.children) as HTMLElement[]
    const allDayElements = Array.from(allDayRow.children) as HTMLElement[]
    const dayColumns = Array.from(bodyTableOfDayColumns.children) as HTMLElement[]
    const availableElementsOnView = [...headerScaleDateElements, ...allDayElements, ...dayColumns].filter(item => {
        return checkDayViewElement(item)
    })
    let currentFocusIndex = availableElementsOnView.indexOf(currentElement)
    switch (key) {
        case 'ArrowLeft':
            if (currentFocusIndex > 0) {
                currentFocusIndex--
            }
            return availableElementsOnView[currentFocusIndex].focus()
        case 'ArrowRight':
            if (currentFocusIndex < availableElementsOnView.length - 1) {
                currentFocusIndex++
            }
            return availableElementsOnView[currentFocusIndex].focus()
        case 'ArrowDown':
            if (availableElementsOnView.length === 3 && currentFocusIndex < availableElementsOnView.length - 1) {
                currentFocusIndex++
            } else if (currentFocusIndex + 7 < availableElementsOnView.length) {
                currentFocusIndex = currentFocusIndex + 7
            } else if (currentFocusIndex < availableElementsOnView.length && currentFocusIndex) {
                currentFocusIndex = currentFocusIndex % 7
            }
            return availableElementsOnView[currentFocusIndex].focus()
        case 'ArrowUp':
            if (availableElementsOnView.length === 3 && currentFocusIndex > 0) {
                currentFocusIndex--
            } else if (availableElementsOnView.length > 2 && currentFocusIndex >= 7) {
                currentFocusIndex = currentFocusIndex - 7
            }
            return availableElementsOnView[currentFocusIndex].focus()
        default:
            return
    }
}

function handlerVerticalNavigationElements(key: string, currentElement: HTMLElement) {
    const navigationByKeysElements = getNavigationByKeysElements(currentElement)
    if (!navigationByKeysElements?.length) {
        return
    }
    let currentFocusIndex = navigationByKeysElements.indexOf(currentElement)

    if (key === 'ArrowDown') {
        currentFocusIndex = currentFocusIndex < navigationByKeysElements.length - 1 ? currentFocusIndex + 1 : 0
    }
    if (key === 'ArrowUp') {
        currentFocusIndex = currentFocusIndex > 0 ? currentFocusIndex - 1 : navigationByKeysElements.length - 1
    }
    return navigationByKeysElements[currentFocusIndex].focus()
}

function getNavigationByKeysElements(currentElement: HTMLElement) {
    const root = document.querySelector('[data-root-navigation-by-keys]')
    if (!root) {
        return
    }
    return Array.from(root.querySelectorAll<HTMLElement>(`[data-element=${currentElement.dataset.element}]`))
}
