import React, {useMemo} from 'react'

import {formatEventTimes} from '@bpinc/cal-event-ui'
import {type NotEmptyEventMapItem, EventCustomId, EventType} from '@bpinc/cal-state-types'
import {removeSpaces} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const useMonthEventTitle = (item: NotEmptyEventMapItem) => {
    const language = useLocalizationState('language')

    return useMemo(() => {
        const isAllDay = item.data.allDay || item.lastsDays > 1
        const showEventTime = isShowEventTime(item, isAllDay)
        const eventTime = formatEventTimes({
            startTime: item.data.start,
            locale: language,
        }).from.time.replace(/\s/g, '')

        return (
            <>
                {showEventTime && (
                    <span>{removeSpaces(eventTime) + ' '}</span>
                )}
                <span>{item.data.title}</span>
            </>
        )
    }, [item, language])
}

function isShowEventTime(item: NotEmptyEventMapItem, isAllDay: boolean): boolean {
    return (item.type === EventType.EVENT || item.type === EventType.NEW_EVENT) &&
        item.data.id !== EventCustomId.SELECTION &&
        !isAllDay
}
