import {useCallback} from 'react'

import {useDaysOfWeek, useMonthsList} from '@bpinc/cal-hooks'
import {addLeadingZero} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


//TODO: Get actual formatting logic from the designer, implement it localized
export function useDateFormatter() {
    const {t} = useLocalizationState()
    const monthsShort = useMonthsList({short: true})
    const daysOfWeek = useDaysOfWeek({startsFromMonday: false, short: true})
    return useCallback(
        (timestamp: number) => {
            const date = new Date(timestamp)
            const month = monthsShort[date.getMonth()]
            const day = date.getDate()
            const dow = daysOfWeek[day]
            let h = date.getHours()
            const m = date.getMinutes()
            const ampm = h < 12 ? 'AM' : 'PM'
            h = h % 12 || 12
            const formatted = `${month} ${day}, ${dow}, ${addLeadingZero(h)}:${addLeadingZero(m)}${ampm}`
            return t.INCOMING_CALL_DIALOG_BOX.calendarAlert_dateString(formatted)
        },
        [t.INCOMING_CALL_DIALOG_BOX, monthsShort, daysOfWeek],
    )
}
