import React, {useMemo} from 'react'

import {useMonthsList} from '@bpinc/cal-hooks'
import type {CalendarEvent} from '@bpinc/cal-request-urls'
import {isToday as checkIsToday} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import type {ScheduleTableRowType} from '..'
import {ScheduleEvent} from './ScheduleEvent'


export interface ScheduleTableRowEventData {
    uuid: string
    type: ScheduleTableRowType.EVENT
    date: number
    event: CalendarEvent
    isFirstEventInCurrentDay: boolean
}

type ScheduleTableRowEventProps = {
    row: ScheduleTableRowEventData
    cellStyles: string
    selected: boolean
    isCommunicator: boolean
}

export const ScheduleTableRowEvent: React.FC<ScheduleTableRowEventProps> = ({
    row,
    cellStyles,
    selected,
    isCommunicator,
}) => {
    const monthsList = useMonthsList({short: true})

    const dayTitle = useMemo(() => {
        const date = new Date(row.date)
        const monthIndex = date.getMonth()
        const monthName = monthsList[monthIndex]
        const weekDayName = date.toLocaleDateString('en', {weekday: 'short'})

        return `${monthName}, ${weekDayName}`
    }, [row.date, monthsList])

    const dayNumber = new Date(row.date).getDate()

    const isToday = checkIsToday(new Date(row.date))

    return (
        <tr>
            <td className={cellStyles}>
                {row.isFirstEventInCurrentDay && (
                    <>
                        <div
                            className={tcn(
                                'text-center text-xs font-bold uppercase text-cal-header-text',
                                {'-translate-x-1': isCommunicator},
                            )}
                        >
                            {dayTitle}
                        </div>

                        <div className="flex justify-center">
                            <div
                                className={tcn('text-sm no-underline', {
                                    'text-black': !isToday,
                                })}
                            >
                                <div
                                    className={tcn(
                                        'flex h-5 w-5 select-none items-center justify-center text-sm text-primary-legacy',
                                        {
                                            'rounded-full bg-primary-legacy text-primary-contrast no-underline': isToday,
                                            '-translate-x-1': isCommunicator,
                                        },
                                    )}
                                >
                                    {dayNumber}
                                </div>
                            </div>
                        </div>
                    </>
                )}
            </td>

            <td className={cellStyles}>
                <ScheduleEvent
                    event={row.event}
                    selected={selected}
                />
            </td>
        </tr>
    )
}
