import type {FC} from 'react'
import {useEffect} from 'react'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {useLocalSettings} from '@bpinc/ad-local-settings-context'
import {useCalendarAlertState} from '@bpinc/cal-alert-state-context'
import {useCalendarState} from '@bpinc/cal-state-context'
import {useOpenGlobalSlidingModal} from '@bpinc/lib-sliding-modal-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {CalendarEventNotificationModal} from '../CalendarEventNotificationModal'


export const CalendarModalsAutoOpener: FC = () => {
    const openModal = useModalComponentOpener(CalendarEventNotificationModal)
    const openGlobalSlidingModal = useOpenGlobalSlidingModal()
    const calendarEventAlerts = useCalendarAlertState('calendarEventAlerts')
    const localSettings = useLocalSettings()
    const isCommunicator = useIsCommunicator()
    const hasSelectedEvent = useCalendarState(s => !!s.selectedEvent)

    useEffect(
        function openCalendarEventModal() {
            openModalWithConfig()

            async function openModalWithConfig() {
                if (calendarEventAlerts.length) {
                    const {id: eventId, time: eventTime} = calendarEventAlerts[0]
                    const moreQueue = calendarEventAlerts.length > 1 ? calendarEventAlerts.length - 1 : undefined

                    const modalVisualParams = await localSettings.get('modals')
                    const config = modalVisualParams?.calendar_event_notification ?? {}

                    openModal({
                        eventId,
                        eventTime,
                        moreQueue,
                        ...config,
                    }, {
                        id: 'calendar_event_notification',
                        notification: true,
                    })
                }
            }
        },
        [calendarEventAlerts, calendarEventAlerts.length, localSettings, openModal],
    )

    useEffect(() => {
        if (!isCommunicator) {
            return
        }
        if (hasSelectedEvent) {
            openGlobalSlidingModal('calendarEvent')
        }

    }, [hasSelectedEvent, isCommunicator, openGlobalSlidingModal])

    return null
}
