import {useMemo} from 'react'

import {useTimezonesState} from '@bpinc/ad-timezones-state-context'
import {MS_IN_MINUTE} from '@bpinc/lib-helpers'


export const useTimezoneDifference = () => {
    const {secondaryTimezone, localTimezone, allTimezones} = useTimezonesState()

    return useMemo(() => {
        const getOffset = (timeZoneId: string) => allTimezones.find(tz => tz.id === timeZoneId)?.offset
        if (!secondaryTimezone) {
            return 0
        }

        const secondaryOffset = getOffset(secondaryTimezone) ?? 0
        const localOffset = getOffset(localTimezone) ?? 0

        return (localOffset - secondaryOffset) * MS_IN_MINUTE
    }, [allTimezones, localTimezone, secondaryTimezone])
}
