import React, {useMemo} from 'react'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {useMonthsList} from '@bpinc/cal-hooks'
import {useCalendarState} from '@bpinc/cal-state-context'
import {getViewTitle} from '@bpinc/cal-utils'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useIsStartFromMonday} from '../hooks/useIsStartFromMonday'
import {useShowWeekends} from '../hooks/useShowWeekends'


export const CalendarViewTitle: React.FC = React.memo(() => {
    const {t} = useLocalizationState()
    const {view, viewDate} = useCalendarState(['view', 'viewDate'])
    const monthsList = useMonthsList()
    const monthsListShort = useMonthsList({short: true})
    const isCommunicator = useIsCommunicator()
    const isStartFromMonday = useIsStartFromMonday()
    const showWeekends = useShowWeekends()
    const title = useMemo(() =>
        getViewTitle(view, viewDate, monthsList, monthsListShort, {
            weekStartsFromMonday: isStartFromMonday,
            withoutWeekend: !showWeekends,
        })
    , [
        view,
        viewDate,
        monthsList,
        monthsListShort,
        isStartFromMonday,
        showWeekends,
    ])

    return (
        <div
            className={tcn(
                'ml-2.5 flex flex-1 items-center justify-center text-center text-xl text-cal-dark-text',
                {'text-base': isCommunicator},
            )}
            title={t.CALENDAR.date_title_tooltip()}
        >
            {title}
        </div>
    )
})
