/* eslint-disable react-perf/jsx-no-new-function-as-prop */
import type {FC} from 'react'
import React from 'react'
import type {UseFormReturn} from 'react-hook-form'

import type {CalendarEventFields} from '@bpinc/cal-state-types'
import {SmIcon} from '@bpinc/lib-external-ui-elements'
import {TextField} from '@bpinc/lib-forms-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface Props {
    form: UseFormReturn<CalendarEventFields>
    staticValue?: number
}

/**
 * Displays screenpopSize property of an Event
 * see info about screenpopSize https://help.brightpattern.com/5.8:Scenario-builder-reference-guide/ScenarioBlocks/WebScreenPop
 */
export const EventDetails: FC<Props> = ({form, staticValue}) => {
    const {control, getValues, setValue} = form

    const isEditableFormField = staticValue === undefined
    const screenpopSize = isEditableFormField
        ? getValues('screenpopSize') || 0
        : staticValue

    /** */
    const handleDetailsRemove = () => {
        setValue('screenpopSize', 0, {shouldDirty: true})
    }

    const {t: {CALENDAR_EVENT_FORM: message}} = useLocalizationState()

    return (
        <>
            <label>{message.field_details_label()}</label>
            <p>
                {screenpopSize > 0
                    ? (
                        <>
                            <span>
                                {message.field_details_text(screenpopSize) }
                            </span>
                            {isEditableFormField && (
                                <SmIcon
                                    className="inline-block pl-2 align-text-top"
                                    color="grey"
                                    fitted
                                    link
                                    name="times"
                                    onClick={handleDetailsRemove}
                                />
                            )}
                        </>
                    )
                    : message.field_details_none()}
            </p>
            {isEditableFormField && (
                <TextField
                    className="hidden"
                    control={control}
                    name="screenpopSize"
                    defaultValue={screenpopSize}
                />
            )}
        </>
    )
}
