import React, {useCallback} from 'react'

import {formatEventTimes} from '@bpinc/cal-event-ui/src/utils'
import {useSelectEvent} from '@bpinc/cal-hooks'
import type {CalendarEvent} from '@bpinc/cal-request-urls'
import {useOnKeyPressHandler} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


type ScheduleEventProps = {
    event: CalendarEvent
    selected: boolean
}

export const ScheduleEvent: React.FC<ScheduleEventProps> = ({
    event,
    selected,
}) => {
    const {t, language} = useLocalizationState()
    const selectEvent = useSelectEvent()

    const handleClick = useCallback(() => {
        selectEvent({id: event.id})
    }, [event.id, selectEvent])

    const onKeyPressHandler = useOnKeyPressHandler(handleClick)

    const {from} = formatEventTimes({
        allDay: event.allDay,
        allDayMessage: t.CALENDAR.all_day(),
        startTime: event.start,
        endTime: event.end,
        locale: language,
    })

    return (
        <div
            tabIndex={0}
            data-event-id={event.id}
            onKeyDown={onKeyPressHandler}
            className={tcn(
                'mb-1 cursor-pointer px-1 py-0.5',
                'text-sm/[16px] text-cal-event-text',
                'border-l-4 border-solid border-l-cal-event-border bg-cal-event-bg',
                {
                    'border-l-cal-day-event-border bg-cal-day-event-bg': event.allDay,
                    'shadow': selected,
                },
            )}
            onClick={handleClick}
        >
            <div>{event.title}</div>
            <div>{from.time}</div>
        </div>
    )
}
