
import type {Dispatch, SetStateAction} from 'react'
import {useEffect, useState} from 'react'

import {useCalendarState} from '@bpinc/cal-state-context'
import type {SelectionRange} from '@bpinc/cal-state-types'
import {usePrevious} from '@bpinc/lib-helpers'


type ReturnValue = [
    SelectionRange | undefined,
    Dispatch<SetStateAction<SelectionRange | undefined>>
]

export const useSelectionRange = (): ReturnValue => {
    const selectedEvent = useCalendarState('selectedEvent')
    const [selectionRange, setSelectionRange] = useState<SelectionRange | undefined>()

    const prevSelectedEvent = usePrevious(selectedEvent)

    useEffect(() => {
        if (selectedEvent && !prevSelectedEvent) {
            setSelectionRange(undefined)
        }
    }, [selectedEvent, prevSelectedEvent])

    return [selectionRange, setSelectionRange]
}
