import {createSelector} from 'reselect'

import type {Language} from '@bpinc/state-localization'


type SelectorParams = {locale: Language; timeZone?: string}

type Formatters = {
    fullDate: Intl.DateTimeFormat
    h: Intl.DateTimeFormat
    hmm: Intl.DateTimeFormat
    hhmm: Intl.DateTimeFormat
    normalize(date: string): string
}

/** */
export const createIntlFormatters = () =>
    createSelector(
        ({locale}: SelectorParams) => locale,
        ({timeZone}: SelectorParams) => timeZone,
        (locale, timeZone): Formatters => ({
            fullDate: new Intl.DateTimeFormat(
                locale,
                {
                    day: 'numeric',
                    month: 'long',
                    timeZone,
                    weekday: 'long',
                },
            ),
            h: new Intl.DateTimeFormat(
                locale,
                {
                    hour: 'numeric',
                    timeZone,
                },
            ),
            hmm: new Intl.DateTimeFormat(
                locale,
                {
                    hour: 'numeric',
                    minute: '2-digit',
                    timeZone,
                },
            ),
            hhmm: new Intl.DateTimeFormat(
                locale,
                {
                    hour: '2-digit',
                    minute: '2-digit',
                    timeZone,
                },
            ),
            normalize: date => new RegExp('AM|PM', 'g').test(date) ?
                date
                    .replace(/^0/, '')
                    .replace(/:00/g, '')
                    .replace(/AM|PM/g, a => a.toLowerCase()) :
                date,
        }),
    )

