import React from 'react'

import {useWeekEventTitle} from '../../../../hooks/useWeekEventTitle'
import type {DayEventProps} from './DayEvent'
import {DayEventBlock} from './DayEventBlock'


export const DayMeeting: React.FC<DayEventProps> = props => {
    const title = useWeekEventTitle(props.item, props.header)

    return (
        <DayEventBlock
            title={title}
            className="border-cal-event-border bg-cal-event-bg"
            {...props}
        />
    )
}
