import {useEffect, useRef} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'


export function useShiftsLoader() {
    const {getShifts} = useCalendarStateMethods()
    const calendarOwnerUserId = useCalendarState('calendarOwnerUserId')
    const loaded = useRef(false)

    useEffect(() => {
        if (loaded.current) {
            return
        }

        if (calendarOwnerUserId) {
            loaded.current = true
            getShifts(calendarOwnerUserId)
        }
    }, [
        loaded,
        calendarOwnerUserId,
        getShifts,
    ])
}
