import React, {useCallback, useMemo, useState} from 'react'
import {useForm} from 'react-hook-form'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import type {TimeoffRequestFields} from '@bpinc/cal-state-types'
import {SmForm} from '@bpinc/lib-external-ui-elements'
import {CheckboxField, DatePickerField, SelectField, TextAreaField} from '@bpinc/lib-forms-ui'
import {MS_IN_DAY} from '@bpinc/lib-helpers'
import {Modal, SemanticButton, Tabs} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'


enum RequestType {
    ABSENCE = 'absence',
    SCHEDULE_CHANGE = 'schedule_change',
    OVERTIME = 'overtime',
}

enum AbsenceType {
    ABSENCE_SICK = 'absence_sick',
}

type CreateRequestModalProps = CommonModalProps

export const CreateRequestModal: React.FC<CreateRequestModalProps> = ({
    onClose,
    ...restProps
}) => {
    const {t} = useLocalizationState()
    const {timeoffTypes, timeoffRequestSaving} = useCalendarState([
        'timeoffTypes',
        'timeoffRequestSaving',
    ])
    const {saveTimeoffRequest} = useCalendarStateMethods()
    const [activeRequestTab, setActiveRequestTab] = useState<RequestType>(RequestType.ABSENCE)

    const form = useForm<TimeoffRequestFields>({
        defaultValues: getDefaultValues(),
    })
    const {control} = form

    const absenceTypeOptions = useMemo(() => {
        return [AbsenceType.ABSENCE_SICK].map(type => {
            let text = ''
            if (type === AbsenceType.ABSENCE_SICK) {
                text = t.CALENDAR_REQUESTS.absence_sick()
            }

            const typeId = timeoffTypes.find(tType => tType.name === type)?.id

            return {
                key: type,
                text,
                value: typeId,
            }
        })
    }, [t, timeoffTypes])

    const requestTabs = useMemo(() => [
        {id: RequestType.ABSENCE, title: t.CALENDAR_REQUESTS.absence_request()},
        {id: RequestType.SCHEDULE_CHANGE, title: t.CALENDAR_REQUESTS.schedule_change_request()},
        {id: RequestType.OVERTIME, title: t.CALENDAR_REQUESTS.overtime_request()},
    ], [t])

    const onRequestTabChange = useCallback((type: RequestType) => {
        setActiveRequestTab(type)
    }, [])

    const handleCreate = useCallback(() => {
        saveTimeoffRequest(form.getValues())
            .then(() => onClose())
            .catch(() => onClose())
    }, [form, saveTimeoffRequest, onClose])

    return (
        <Modal
            onClose={onClose}
            aria-labelledby="activeDialogHeader"
            className="!max-w-[600px]"
            {...restProps}
        >
            <Modal.Header
                id="activeDialogHeader"
                role="heading"
                className="border-b-0 p-0"
            >
                <div className="px-6 pt-6 text-lg">
                    {t.CALENDAR_REQUESTS.schedule_request()}
                </div>
                <Tabs
                    tabs={requestTabs}
                    activeTabId={activeRequestTab}
                    headerClassName="bg-transparent"
                    tabClassName="after:bg-black pt-2"
                    activeTabClassName="font-bold"
                    onChange={onRequestTabChange}
                />
            </Modal.Header>
            <Modal.Content>
                <SmForm onSubmit={form.handleSubmit(handleCreate)}>
                    <SelectField
                        control={control}
                        label={t.CALENDAR_REQUESTS.absence_type()}
                        name="timeOffTypeId"
                        options={absenceTypeOptions}
                    />
                    <SmForm.Group
                        className="border-b border-b-cal-border pb-6"
                    >
                        <DatePickerField
                            readOnly
                            control={control}
                            clearable={false}
                            width="4"
                            icon="caret down"
                            name="startDate"
                            label={t.CALENDAR_REQUESTS.date()}
                        />
                        <DatePickerField
                            readOnly
                            clearable={false}
                            width="4"
                            control={control}
                            icon="caret down"
                            name="endDate"
                            label="&nbsp;"
                        />
                    </SmForm.Group>
                    <TextAreaField
                        control={control}
                        label={t.CALENDAR_REQUESTS.comments()}
                        maxRows={5}
                        minRows={5}
                        name="comment"
                    />
                    <div className="mb-6">
                        <CheckboxField
                            className="mb-1"
                            control={control}
                            name="allOrNothing"
                            label={t.CALENDAR_REQUESTS.all_or_nothing()}
                        />
                        <div className="text-sm text-cal-dark-text">
                            {t.CALENDAR_REQUESTS.if_unavailable_deny()}
                        </div>
                    </div>
                    <div className="text-right">
                        <SemanticButton className="mr-2.5" onClick={() => onClose()}>
                            {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                        </SemanticButton>
                        <SemanticButton
                            type="submit"
                            primary
                            disabled={timeoffRequestSaving}
                            loading={timeoffRequestSaving}
                        >
                            {t.CALENDAR_REQUESTS.create_request()}
                        </SemanticButton>
                    </div>
                </SmForm>
            </Modal.Content>
        </Modal>
    )
}

function getDefaultValues(): TimeoffRequestFields {
    return {
        timeOffTypeId: '',
        startDate: new Date().toISOString(),
        endDate: new Date(Date.now() + MS_IN_DAY).toISOString(),
        comment: '',
        allOrNothing: false,
    }
}
