import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import {
    type DragInfo,
    type EventMap,
    type EventMapItem,
    type SelectionRange,
    EventType,
} from '@bpinc/cal-state-types'
import {
    areSameDays,
    DAYS_IN_WEEK,
    getStartOfDay,
    isMidnight,
    MS_IN_HOUR,
} from '@bpinc/lib-helpers'

import {getNextDayTime, getRangeInDays, isWithinOneDay} from './dates'
import {getEventMapKey} from './event-map'
import {getSortedSelectionRange, isRegularEvent} from './events-common'


export const getSelectionRange = (selectedItem?: CalendarEventDetails): SelectionRange | undefined => {
    if (selectedItem && !selectedItem.id) {
        const {startTime, endTime} = selectedItem
        if (startTime && endTime) {
            return [new Date(startTime), new Date(endTime)]
        }
    }
}

export const getDragStartDate = (x: number, y: number): Date | null => {
    const monthContainer = document.getElementById('month-content')
    if (!monthContainer) {
        return null
    }
    const monthCells = Array.from(monthContainer.querySelectorAll<HTMLDivElement>('[data-element="month-cell"]'))

    const resultCell = monthCells.find(cell => {
        const rect = cell.getBoundingClientRect()
        // Check if the coordinates are within the cell's boundaries
        return x >= rect.left && x <= rect.right && y >= rect.top && y <= rect.bottom
    })

    if (!resultCell) {
        return null
    }

    const dateStr = resultCell.dataset.day
    if (!dateStr) {
        return null
    }

    return new Date(Number(dateStr))
}

export const checkIfCellIsSelected = (
    day: Date,
    selection: SelectionRange,
) => {
    const [rangeStart, rangeEnd] = getSortedSelectionRange(selection)
    const allDay = !isWithinOneDay(Number(rangeStart), Number(rangeEnd))

    // All day selection
    if (allDay) {
        const [start, end] = [rangeStart, rangeEnd]
            .map(d => new Date(getStartOfDay(d)))
        return start <= day && day <= end
    }

    // If not all day event for example from 10pm to 12am, then no select next day
    if (isMidnight(rangeEnd)) {
        return areSameDays(day, rangeStart)
    }

    return areSameDays(day, rangeStart) || areSameDays(day, rangeEnd)
}

export const checkIfCellIsDropTarget = (
    weekOfMonth: number,
    dayOfWeek: number,
    dragInfo?: DragInfo,
    dragStartDate?: Date,
) => {
    if (!dragInfo || !dragStartDate) return false

    const {item, cell} = dragInfo
    const [start, end] = [item.data.start, item.data.end].map(v => new Date(v))
    const lastsDays = getRangeInDays(start, end)
    const dragDiffDays = getRangeInDays(start, dragStartDate) - 1
    const [w, d] = cell
    const cellOrdinal = weekOfMonth * DAYS_IN_WEEK + dayOfWeek
    const startCell = w * DAYS_IN_WEEK + d - dragDiffDays

    return startCell <= cellOrdinal && cellOrdinal < startCell + lastsDays
}

export const getMonthEventSelectionRange = (
    selection: SelectionRange,
    eventsByDate?: EventMap,
): SelectionRange => {
    const [selectionStart, selectionEnd] = getSortedSelectionRange(selection)
    const withinOneDay = areSameDays(selectionStart, selectionEnd)

    if (!withinOneDay) {
        return [
            selectionStart,
            new Date(getNextDayTime(Number(selectionEnd))),
        ]
    }

    if (!eventsByDate) {
        return [selectionStart, selectionEnd]
    }

    // on month view we set up time for one-hour event
    // if there are events in the day, we need to set it up after all events until 00.00
    // if day is empty or there isn't free slot for one-hour event after all events, we need to set it up to work day start
    const clickCellDate = new Date(selection[0])
    const eventsOfDay =
        eventsByDate[getEventMapKey(clickCellDate)].filter(isRegularEvent)
    const lastEvent = eventsOfDay.length > 0 ? eventsOfDay[eventsOfDay.length - 1] : undefined

    const [start, end] = [selectionStart, selectionEnd].map((dateItem, i) => {
        const sample = new Date(dateItem)
        if (lastEvent) {
            // check 1 hour slot after the last event
            const hasSlot = areSameDays(clickCellDate, new Date(lastEvent.data.end + MS_IN_HOUR - 1))
            if (hasSlot) {
                const lastEventEndDate = new Date(lastEvent.data.end)
                sample.setHours(lastEventEndDate.getHours() + i)
                sample.setMinutes(lastEventEndDate.getMinutes())
                return sample
            }
        }
        sample.setHours(8 + i)
        return sample
    })

    return [start, end]
}

export const getInfoForMore = (
    list: EventMapItem[],
    limit: number,
    eventsByDate: EventMap,
): [boolean, number] => {
    let hasMore = false
    let moreAmount = 0

    if (limit <= 0) {
        moreAmount = list.length
        hasMore = !!moreAmount
        return [hasMore, moreAmount]
    }

    if (list.length > limit) {
        // one spot will be occupied by more link
        limit--

        moreAmount = list.filter((_, i) => i >= limit).length

        if (moreAmount) {
            hasMore = true
            return [hasMore, moreAmount]
        }
    } else if (list.length === limit) {
        const lastItem = list[list.length - 1]

        // if last item lasts for few days,
        // check if it should be collapsed
        // in order to give space for "more" link
        // to some next day
        if (lastItem.type === EventType.EVENT && lastItem.lastsDays > 1) {
            const nextSection = new Date(lastItem.data.start)

            for (let i = 1; i < lastItem.lastsDays; i++) {
                nextSection.setHours(24) // increment day

                // break if new week (Sunday)
                if (nextSection.getDay() === 0) {
                    break
                }

                const key = getEventMapKey(nextSection)
                const nextList = eventsByDate[key]
                const [nextDayHasMore] = getInfoForMore(nextList, limit, eventsByDate)

                if (nextDayHasMore) {
                    hasMore = true
                    moreAmount = 1
                    return [hasMore, moreAmount]
                }
            }
        }
    }

    return [hasMore, moreAmount]
}
