import {useCallback} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import {View} from '@bpinc/cal-state-types'
import {getRangeFromView} from '@bpinc/cal-utils'
import {MS_IN_DAY} from '@bpinc/lib-helpers'


export const useOpenDayView = () => {
    const {viewDate} = useCalendarState(['viewDate'])
    const {changeViewDate, changeView} = useCalendarStateMethods()
    const [start] = getRangeFromView(viewDate, View.WEEK)

    return useCallback((day: number | Date) => {
        const dateTime = typeof day === 'object' ? Number(day) : start + MS_IN_DAY * day
        changeViewDate(dateTime)
        changeView(View.DAY)
    }, [start, changeViewDate, changeView])
}
