import type {FC} from 'react'
import React, {useEffect, useState} from 'react'

import {MINUTES_IN_CELL} from '@bpinc/cal-utils'
import {tcn} from '@bpinc/lib-tailwind-classnames'


function timeToCssPosition(date: Date) {
    const minutes = date.getMinutes() % MINUTES_IN_CELL

    const percentsFromTop = (minutes / MINUTES_IN_CELL) * 100

    return {top: `${percentsFromTop}%`}
}

export const Timeline: FC = React.memo(() => {
    const [position, setPosition] = useState(timeToCssPosition(new Date()))

    useEffect(() => {
        const interval = setInterval(() => {
            setPosition(timeToCssPosition(new Date()))
        }, 30_000)

        return () => {
            clearInterval(interval)
        }
    }, [])

    return (
        <div className="relative h-[calc(100%_+_2px)] w-[calc(100%_+_4px)]">
            <div
                style={position}
                className={tcn(
                    'absolute top-0 z-[1] h-px w-full bg-cal-timeline',
                    'before:absolute before:block before:h-2 before:w-2',
                    'before:-translate-x-2 before:-translate-y-1/2 before:rounded-full before:bg-cal-timeline',
                )}
            />
        </div>
    )
})
