import React, {useCallback, useMemo, useState} from 'react'
import type {ColumnDef, ColumnFiltersState} from '@tanstack/react-table'
import {flexRender, getCoreRowModel, useReactTable} from '@tanstack/react-table'

import {formatRequestDate, formatRequestedTime} from '@bpinc/cal-utils'
import {isHTMLElement} from '@bpinc/lib-html-utils'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {Checkbox, Svg} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {commentIcon} from '../../../icons'
import {Cell} from './Cell'
import {Filters} from './Filters'
import {HeaderCell} from './HeaderCell'
import {mockData} from './mock-data'
import {RequestSidebar} from './RequestSidebar'
import {StatusIcon} from './StatusIcon'
import type {ApprovedRequest, DeniedRequest, Request} from './types'
import {ApprovalType, RequestStatus} from './types'


export const Table: React.FC = () => {
    const {t: {CALENDAR_REQUESTS: message}} = useLocalizationState()
    const [requests] = useState(() => [...mockData])
    const [selectedRequest, setSelectedRequest] = useState<Request>()
    const [columnFilters, setColumnFilters] = useState<ColumnFiltersState>([])

    const selectRequestByRow = useCallback((row: HTMLTableRowElement) => {
        const reqId = row.dataset.id
        assertDefined(reqId)
        const request = requests.find(r => r.id === reqId)
        setSelectedRequest(request)
    }, [requests])

    const onRequestClick = useCallback((e: React.MouseEvent<HTMLTableSectionElement>) => {
        if (!isHTMLElement(e.target)) {
            return
        }
        const row = e.target.closest('tr')
        if (row) {
            selectRequestByRow(row)
        }
    }, [selectRequestByRow])

    const onRowKeyDown = useCallback((e: React.KeyboardEvent<HTMLTableRowElement>) => {
        if (e.key === 'Enter') {
            const row = e.target
            if (row instanceof HTMLTableRowElement) {
                selectRequestByRow(row)
            }
        }
    }, [selectRequestByRow])

    const onRequestSidebarClose = useCallback(() => {
        setSelectedRequest(undefined)
    }, [])

    const columns = useMemo<ColumnDef<Request, string>[]>(() => [
        {
            id: 'select',
            header: ({table}) => (
                <div className="w-9.5 px-2.5 pt-1">
                    <Checkbox
                        {...{
                            checked: table.getIsAllRowsSelected(),
                            indeterminate: table.getIsSomeRowsSelected(),
                            onChange: checked => table.toggleAllPageRowsSelected(checked),
                        }}
                    />
                </div>
            ),
            cell: ({row}) => (
                <div className="w-9.5 px-2.5 pt-1">
                    <Checkbox
                        {...{
                            checked: row.getIsSelected(),
                            disabled: !row.getCanSelect(),
                            indeterminate: row.getIsSomeSelected(),
                            onChange: checked => row.toggleSelected(checked),
                        }}
                    />
                </div>
            ),
            size: 38,
        },
        {
            id: 'status',
            accessorKey: 'status',
            header: () => null,
            cell: info => (
                <Cell>
                    <StatusIcon status={info.getValue() as RequestStatus} />
                </Cell>
            ),
            size: 30,
        },
        {
            id: 'type',
            accessorKey: 'type',
            header: () => <HeaderCell>{message.type()}</HeaderCell>,
            cell: info => <Cell>{info.getValue()}</Cell>,
            size: 150,
        },
        {
            id: 'requested-time',
            accessorFn: row => formatRequestedTime(row.reqTimeStart, row.reqTimeEnd),
            header: () => <HeaderCell>{message.requested_time()}</HeaderCell>,
            cell: info => <Cell>{info.getValue()}</Cell>,
            size: 250,
        },
        {
            id: 'submission-date',
            accessorFn: row => formatRequestDate(row.submissionDate),
            header: () => <HeaderCell>{message.submission_date()}</HeaderCell>,
            cell: info => <Cell>{info.getValue()}</Cell>,
            size: 160,
        },
        {
            id: 'approval-date',
            accessorFn: row => row.status !== RequestStatus.PENDING && formatRequestDate(row.approvalDate),
            header: () => <HeaderCell>{message.approval_date()}</HeaderCell>,
            cell: info => <Cell>{info.getValue()}</Cell>,
            size: 160,
        },
        {
            id: 'approval',
            accessorFn: row => formatApproval(row, message.system_approval()),
            header: () => null,
            cell: info => (
                <Cell className="text-cal-header-text">
                    {info.getValue()}
                </Cell>
            ),
            size: 120,
        },
        {
            id: 'reason',
            accessorFn: row => row.status === RequestStatus.DENIED,
            header: () => null,
            cell: info => (
                <Cell>
                    {info.getValue()
                        ? <Svg src={commentIcon} />
                        : null}
                </Cell>
            ),
            size: 40,
        },
        {
            id: 'comment',
            accessorKey: 'comment',
            header: () => <HeaderCell>{message.comments()}</HeaderCell>,
            cell: info => (
                <Cell className="max-w-[150px] truncate">
                    {info.getValue()}
                </Cell>
            ),
            enableResizing: true,
        },
    ], [message])

    const table = useReactTable({
        data: requests,
        columns,
        state: {columnFilters},
        onColumnFiltersChange: setColumnFilters,
        getCoreRowModel: getCoreRowModel(),
    })

    return (
        <div className="flex flex-1 flex-col gap-3">
            <Filters />
            <div className="relative h-full overflow-hidden">
                <table className="flex flex-1 flex-col">
                    <thead className="box-border h-[34px] border-y border-cal-border bg-cal-light-bg">
                        {table.getHeaderGroups().map(headerGroup => (
                            <tr key={headerGroup.id}>
                                {headerGroup.headers.map(header => (
                                    <th
                                        key={header.id}
                                        style={{
                                            minWidth: header.getSize(),
                                        }}
                                    >
                                        {header.isPlaceholder
                                            ? null
                                            : flexRender(
                                                header.column.columnDef.header,
                                                header.getContext(),
                                            )}
                                    </th>
                                ))}
                            </tr>
                        ))}
                    </thead>
                    <tbody onClick={onRequestClick}>
                        {table.getRowModel().rows.map(row => (
                            <tr
                                key={row.id}
                                className="h-9.5 cursor-pointer border-b border-cal-border"
                                data-id={row.original.id}
                                tabIndex={0}
                                onKeyDown={onRowKeyDown}
                            >
                                {row.getVisibleCells().map(cell => (
                                    <td
                                        key={cell.id}
                                        style={{
                                            minWidth: cell.column.getSize(),
                                        }}
                                    >
                                        {flexRender(
                                            cell.column.columnDef.cell,
                                            cell.getContext(),
                                        )}
                                    </td>
                                ))}
                            </tr>
                        ))}
                    </tbody>
                </table>
                <RequestSidebar
                    request={selectedRequest}
                    onClose={onRequestSidebarClose}
                />
            </div>
        </div>
    )
}

export function isApprovedRequest(req: Request): req is ApprovedRequest {
    return req.status === RequestStatus.APPROVED
}

export function isDeniedRequest(req: Request): req is DeniedRequest {
    return req.status === RequestStatus.DENIED
}

export function isRequestProcessed(req: Request): req is ApprovedRequest | DeniedRequest {
    return isApprovedRequest(req) || isDeniedRequest(req)
}

export function formatApproval(req: Request, sysApprovalMsg: string) {
    if (isRequestProcessed(req)) {
        if (req.approvalType === ApprovalType.SYSTEM) {
            return sysApprovalMsg
        }

        return `by ${req.approvedBy}`
    }

    return ''
}
