import React, {useCallback, useState} from 'react'
import type {BpPeriodSelectProps} from '@servicepattern/jskit'
import {BpPeriodSelect} from '@servicepattern/jskit'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import {getLongDateFormat, languageToLocaleMap} from '@bpinc/lib-helpers'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import type {SearchResultItem} from '@bpinc/lib-user-search-components'
import {MultipleUserSearch} from '@bpinc/lib-user-search-components'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'


type ReassignCalendarEventModalProps = CommonModalProps & {
    eventId?: string
}

export const ReassignCalendarEventModal: React.FC<ReassignCalendarEventModalProps> = ({
    eventId,
    onClose,
    ...restProps
}) => {
    const {t, language} = useLocalizationState()
    const {assignCalendarEvent} = useCalendarStateMethods()
    const calendarOwnerUserId = useCalendarState('calendarOwnerUserId')

    const [searchValue, setSearchValue] = useState('')

    const [dateRange, setDateRange] = useState<BpPeriodSelectProps['value']>({
        after: null,
        before: null,
    })
    const [users, setUsers] = useState<SearchResultItem[]>([])

    const handleConfirm = useCallback(() => {
        assignCalendarEvent({
            fromUser: calendarOwnerUserId,
            toUser: users.map(user => user.id),
            start: dateRange?.before || null,
            end: dateRange?.after || null,
        })

        onClose()
    }, [onClose, dateRange, calendarOwnerUserId, users, assignCalendarEvent])

    return (
        <Modal
            onClose={onClose}
            closeIcon
            aria-labelledby="activeDialogHeader"
            className="!max-w-[540px]"
            {...restProps}
        >
            <Modal.Header role="heading" id="activeDialogHeader">
                {t.CALENDAR_EVENT_FORM.batchAssignEvents()}
            </Modal.Header>
            <Modal.Content>
                <div>
                    <div className="-mt-2 mb-4">
                        <MultipleUserSearch
                            searchForUsers
                            searchForTeams
                            withSessionUser
                            autoFocus
                            uniqueSearchId="calendar-owner"
                            searchValue={searchValue}
                            setSearchValue={setSearchValue}
                            selectedItems={users}
                            setSelectedItems={setUsers}
                            inputPlaceholder={t.CALENDAR_EVENT_FORM.batch_assign_event_users_placeholder()}
                            loadingLabel={t.GLOBAL.loading()}
                        />
                    </div>

                    {!eventId && (
                        <div className="mb-4">
                            <div className="mx-0 mb-2 mt-0 items-center">
                                <div className="max-w-[330px]">
                                    <BpPeriodSelect
                                        config={{
                                            input: {
                                                format: `${getLongDateFormat('L')} ${getLongDateFormat('LT')}`,
                                                label: t.CALENDAR.events_period(),
                                                placeholder: '',
                                            },
                                            form: {
                                                dateFormat: languageToLocaleMap[language],
                                                timeFormat: languageToLocaleMap[language],
                                            },
                                        }}
                                        hideOnScrollPopup={false}
                                        onChange={setDateRange}
                                        value={dateRange}
                                    />
                                </div>
                            </div>
                        </div>
                    )}

                    <div className="text-right">
                        <SemanticButton
                            className="mr-2.5"
                            onClick={() => onClose()}
                        >
                            {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                        </SemanticButton>

                        <SemanticButton
                            primary
                            disabled={users.length === 0}
                            onClick={handleConfirm}
                        >
                            {t.CALENDAR.assign_button()}
                        </SemanticButton>
                    </div>
                </div>
            </Modal.Content>
        </Modal>
    )
}
