import {useMemo} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import {DefaultCalendars} from '@bpinc/cal-state-types'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {
    CalendarSidebarFilter,
    CalendarSidebarFiltersProps,
} from '../components/CalendarSidebar/CalendarSidebarFilters'


export function useCalendarFilters(): CalendarSidebarFiltersProps {
    const {t} = useLocalizationState()
    const {
        calendars,
        calendarFilters,
    } = useCalendarState(['calendars', 'calendarFilters'])
    const {changeCalendarFilter} = useCalendarStateMethods()

    return useMemo<CalendarSidebarFiltersProps>(() => {
        const options: CalendarSidebarFilter[] = [
            {name: DefaultCalendars.HOLIDAYS, label: t.CALENDAR.holidays(), disabled: true, checked: calendarFilters[DefaultCalendars.HOLIDAYS]},
            {name: DefaultCalendars.EVENTS, label: t.CALENDAR.events_and_meetings(), disabled: true, checked: calendarFilters[DefaultCalendars.EVENTS]},
            {name: DefaultCalendars.REQUESTS, label: t.CALENDAR.requests(), disabled: false, checked: calendarFilters[DefaultCalendars.REQUESTS]},
            {name: DefaultCalendars.SHIFTS, label: t.CALENDAR.shifts(), disabled: false, checked: calendarFilters[DefaultCalendars.SHIFTS]},
        ]
        calendars.forEach(calendar => {
            options.push({
                name: calendar.id,
                label: calendar.title,
            })
        })

        return {
            title: t.CALENDAR.calendars_heading(),
            filters: options,
            onFilterChange: changeCalendarFilter,
        }
    }, [t, calendars, calendarFilters, changeCalendarFilter])
}
