import type {Dispatch, SetStateAction} from 'react'
import React, {useCallback, useMemo} from 'react'

import {Page} from '@bpinc/cal-state-types'
import {Tabs} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = {
    activePage: Page
    onPageChange: Dispatch<SetStateAction<Page | null>>
}

export const CalendarPageHeader: React.FC<Props> = React.memo(({
    activePage,
    onPageChange,
}) => {
    const {t} = useLocalizationState()
    const tabs = useMemo(() => [{id: 'calendar', title: t.CALENDAR.calendar_heading()}], [t])
    const pagesTabs = useMemo(() => [
        {id: Page.MY_CALENDAR, title: t.CALENDAR.my_calendar()},
        {id: Page.MY_REQUESTS, title: t.CALENDAR.my_requests()},
    ], [t])

    const onTabChange = useCallback((id: Page) => {
        onPageChange(id)
    }, [onPageChange])

    return (
        <header className="flex h-[50px] justify-between bg-tabs-header-bg pr-44">
            <Tabs
                tabs={tabs}
                activeTabId="calendar"
                headerClassName="bg-transparent border-b-transparent"
            />
            <Tabs
                tabs={pagesTabs}
                activeTabId={activePage}
                tabClassName="after:bg-black pt-3"
                headerClassName="bg-transparent border-b-transparent"
                activeTabClassName="font-bold"
                onChange={onTabChange}
            />
        </header>
    )
})
