import type {SyntheticEvent} from 'react'
import {useCallback} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import {View} from '@bpinc/cal-state-types'
import {getRangeFromView, isNewEvent} from '@bpinc/cal-utils'
import type {SmMenuItemProps} from '@bpinc/lib-external-ui-elements'

import {useIsStartFromMonday} from './useIsStartFromMonday'
import {useShowWeekends} from './useShowWeekends'


export const useHandleViewChange = () => {
    const {changeViewDate, changeView, closeEventForm} = useCalendarStateMethods()
    const {
        viewDate,
        view,
        selectedEvent,
    } = useCalendarState(['viewDate', 'view', 'selectedEvent'])
    const weekStartsFromMonday = useIsStartFromMonday()
    const showWeekends = useShowWeekends()

    return useCallback((_evt: SyntheticEvent, data: SmMenuItemProps) => {
        const newView = data.view

        // We can't create events on schedule view
        if (newView === View.SCHEDULE && selectedEvent && isNewEvent(selectedEvent)) {
            closeEventForm()
            return
        }

        changeView(newView)

        const order = [View.DAY, View.WEEK, View.MONTH]
        const isIncreasingView = order.indexOf(newView) >= order.indexOf(view)

        if (isIncreasingView) {
            return
        }

        if (selectedEvent && !selectedEvent.id) {
            if (viewDate !== selectedEvent.startTime) {
                changeViewDate(selectedEvent.startTime)
                return
            }
        }

        const [newStart, newEnd] = getRangeFromView(
            viewDate,
            newView,
            {weekStartsFromMonday, withoutWeekend: !showWeekends},
        )
        const isStillWithinRange = newStart <= viewDate && viewDate <= newEnd

        if (isStillWithinRange) {
            return
        }

        changeViewDate(newStart)
    }, [
        viewDate,
        selectedEvent,
        view,
        changeViewDate,
        changeView,
        closeEventForm,
        weekStartsFromMonday,
        showWeekends,
    ])
}
