import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Loader} from '@bpinc/lib-ui-elements'


export const CalendarBodyLoader = React.memo(() => {
    return (
        <div
            className={tcn(
                'flex items-center justify-center before:absolute before:inset-0',
                'before:z-[5] before:bg-primary-contrast before:opacity-70',
            )}
        >
            <Loader active size="big" color="primary" />
        </div>
    )
})
