import React from 'react'

import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import type {NotEmptyEventMapItem} from '@bpinc/cal-state-types'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {DayEvent} from '../DayEvent'


type HeaderEventsProps = {
    cellSize: [number, number]
    eventsByLine: NotEmptyEventMapItem[][]
    isSelectionInProgress: boolean
    selectedEvent?: CalendarEventDetails
    viewStart: number
    viewEnd: number
    isCommunicator?: boolean
}

export const HeaderEvents: React.FC<HeaderEventsProps> = props => {
    const {
        cellSize,
        eventsByLine,
        isSelectionInProgress,
        selectedEvent,
        viewStart,
        viewEnd,
        isCommunicator,
    } = props

    return (
        <div
            className={tcn(
                'absolute inset-x-0 bottom-0 top-auto flex h-0 w-auto select-none flex-row flex-nowrap pl-[53px]',
                {'pl-[58px]': isCommunicator},
            )}
        >
            {eventsByLine.map((line, lineIndex) => (
                <div key={lineIndex} className="absolute">
                    {line.map(item => (
                        <DayEvent
                            key={item.data.id}
                            header
                            item={item}
                            viewStart={viewStart}
                            viewEnd={viewEnd}
                            line={lineIndex}
                            lines={eventsByLine.length}
                            isSelectionInProgress={isSelectionInProgress}
                            selectedEvent={selectedEvent}
                            cellSize={cellSize}
                        />
                    ))}
                </div>
            ))}
        </div>
    )
}
