import React from 'react'

import type {TimeoffBalance, TimeoffType} from '@bpinc/cal-wfm-request-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = {
    timeoffType: TimeoffType
    balance: TimeoffBalance
}

export const Balance: React.FC<Props> = React.memo(({timeoffType, balance}) => {
    const {t} = useLocalizationState()

    return (
        <div className="mr-2 flex min-w-[200px] flex-col rounded border border-cal-border px-4 py-2.5 last:mr-0">
            <div className="mb-1.5 text-sm/[16px] text-cal-dark-text">
                {timeoffType.name}
            </div>
            <div>
                <span className="mr-2 text-2xl/[28px] font-bold text-cal-dark-text">
                    {balance.unused}
                </span>
                <span className="text-sm">{t.CALENDAR_REQUESTS.hours_available()}</span>
            </div>
        </div>
    )
})
