import type {FC} from 'react'
import React from 'react'

import {stopPropagation} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


type MoreEventsPopupToggleProps = {
    eventsCount: number
    onClick: () => void
}

export const MoreEventsPopupToggle: FC<MoreEventsPopupToggleProps> = ({
    eventsCount,
    onClick,
}) => {
    const {t} = useLocalizationState()

    return (
        <button
            className={tcn(
                'relative z-[2] self-start overflow-hidden whitespace-nowrap text-sm/[14px]',
                'ease mt-0.5 box-border px-2 py-0.5 no-underline transition-all duration-300',
                'h-4.5 w-[calc(100%-16px)] bg-cal-light-bg',
                'cursor-pointer select-none text-cal-dark-text hover:underline',
            )}
            onClick={onClick}
            onMouseDown={stopPropagation}
        >
            {t.CALENDAR.more(eventsCount.toString())}
        </button>
    )
}
