import React, {useCallback} from 'react'

import {useOpenReassignCalendarEventModal} from '@bpinc/cal-modals-ui'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const BatchAssignEvents: React.FC = React.memo(() => {
    const {t} = useLocalizationState()
    const openReassignCalendarEventModal = useOpenReassignCalendarEventModal()

    const handleUserReassignClick = useCallback(() => {
        openReassignCalendarEventModal({})
    }, [openReassignCalendarEventModal])

    return (
        <SemanticButton
            basic
            size="small"
            className="self-start whitespace-nowrap"
            onClick={handleUserReassignClick}
            tooltip={t.CALENDAR_EVENT_FORM.batchAssignEvents()}
        >
            {t.CALENDAR_EVENT_FORM.batchAssignEvents()}
        </SemanticButton>
    )
})
