import React, {useCallback} from 'react'

import {capitalize} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {RequestStatus} from '../types'


type Props = {
    value?: RequestStatus
    onChange: (status?: RequestStatus) => void
}

export const StatusFilter: React.FC<Props> = ({
    value,
    onChange,
}) => {
    const {t: {CALENDAR_REQUESTS: message}} = useLocalizationState()

    const onStatusChange = useCallback((status?: RequestStatus) => {
        onChange(status)
    }, [onChange])

    return (
        <div className="flex gap-2">
            <Option
                color="#DEDEDF"
                active={value === undefined}
                label={message.all()}
                onChoose={onStatusChange}
            />
            <Option
                color="#FFC107"
                active={value === RequestStatus.PENDING}
                value={RequestStatus.PENDING}
                onChoose={onStatusChange}
            />
            <Option
                color="#FFC107"
                active={value === RequestStatus.WAITLISTED}
                value={RequestStatus.WAITLISTED}
                onChoose={onStatusChange}
            />
            <Option
                color="#4CAF50"
                active={value === RequestStatus.APPROVED}
                value={RequestStatus.APPROVED}
                onChoose={onStatusChange}
            />
            <Option
                color="#EA4335"
                active={value === RequestStatus.DENIED}
                value={RequestStatus.DENIED}
                onChoose={onStatusChange}
            />
        </div>
    )
}


type OptionProps = {
    color: string
    active: boolean
    value?: RequestStatus
    label?: string
    onChoose: (status?: RequestStatus) => void
}

const Option: React.FC<OptionProps> = ({
    color,
    active,
    value,
    label,
    onChoose,
}) => {
    const onClick = useCallback(() => {
        onChoose(value)
    }, [value, onChoose])

    return (
        <button
            className={tcn(
                'box-border flex h-7 select-none items-center text-sm text-cal-header-text',
                'cursor-pointer rounded-[8px] border px-2',
                {'text-cal-dark-text': active},
            )}
            style={{
                borderColor: color,
                backgroundColor: active ? color : 'transparent',
            }}
            onClick={onClick}
        >
            {value && capitalize(value) || label}
        </button>
    )
}
