import React, {useCallback, useMemo, useState} from 'react'
import type {FieldValues, UseControllerProps} from 'react-hook-form'
import {useController} from 'react-hook-form'

import type {SearchResultItem} from '@bpinc/lib-user-search-components'
import {MultipleUserSearch} from '@bpinc/lib-user-search-components'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface AssigneesComboBoxProps<FormValues extends FieldValues> extends UseControllerProps<FormValues> {
    label?: string
}

const COMBO_BOX_ID = 'calendar-event-asignee'

export function AssigneesComboBox<FormValues extends FieldValues>({
    name,
    control,
    label,
    rules,
    defaultValue,
    disabled,
}: AssigneesComboBoxProps<FormValues>) {
    const {t} = useLocalizationState()
    const [searchValue, setSearchValue] = useState('')

    const {
        field: {onBlur, onChange, value},
    } = useController({
        name,
        control,
        rules,
        defaultValue,
        disabled,
    })

    const handleSelected = useCallback((
        selected: SearchResultItem[] | ((prevSelected: SearchResultItem[]) => SearchResultItem[]),
    ) => {
        if (typeof selected === 'function') {
            const newValue = selected(value)
            onChange(newValue)
            return newValue
        }

        onChange(selected)
        return selected
    }, [onChange, value])

    const selectedItems = useMemo(() => value || [], [value])

    return (
        <>
            {label && <label htmlFor={COMBO_BOX_ID}>{label}</label>}
            <MultipleUserSearch
                searchForUsers
                withSessionUser
                searchForTeams
                uniqueSearchId={COMBO_BOX_ID}
                searchValue={searchValue}
                setSearchValue={setSearchValue}
                selectedItems={selectedItems}
                setSelectedItems={handleSelected}
                noSearchValueLabel={t.CALENDAR.assignees_search_tooltip()}
                inputPlaceholder={t.CHAT_CENTER.chat_add_members_search_input_placeholder()} //TODO: Waiting for proper placeholder from the designer
                loadingLabel={t.GLOBAL.loading()}
                onInputBlur={onBlur}
                disabled={disabled}
            />
        </>
    )
}
