import {useCallback, useState} from 'react'
import type {UseFormReturn} from 'react-hook-form'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import type {CalendarEventFields, RequestResult} from '@bpinc/cal-state-types'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'

import {getEventDetailsFromFields} from '../utils'


type EventHandlersReturn = {
    saveRequestResult: RequestResult | undefined
    deleteRequestResult: RequestResult | undefined
    clearSaveResult: () => void
    clearDeleteResult: () => void
    handleSave: () => void
    handleDelete: () => void
}

export const useEventHandlers = (
    form: UseFormReturn<CalendarEventFields>,
    onComplete?: () => void,
): EventHandlersReturn => {
    const [saveRequestResult, setSaveRequestResult] = useState<RequestResult>()
    const [deleteRequestResult, setDeleteRequestResult] = useState<RequestResult>()

    const {
        selectedEvent,
        eventSaving,
        eventDeleting,
    } = useCalendarState([
        'selectedEvent',
        'eventSaving',
        'eventDeleting',
    ])

    assertDefined(selectedEvent)

    const {
        resetEventForm,
        saveCalendarEventDetails,
        deleteCalendarEvent,
    } = useCalendarStateMethods()
    const {openModal} = useModalsStateMethods()
    const {t: {CALENDAR_EVENT_FORM: message}} = useLocalizationState()

    const clearSaveResult = useCallback(() => {
        setSaveRequestResult(undefined)
    }, [])

    const handleSave = useCallback(() => {
        if (eventSaving) {
            return
        }

        setDeleteRequestResult(undefined)
        setSaveRequestResult(undefined)

        const event = getEventDetailsFromFields(
            selectedEvent,
            form.getValues(),
        )

        saveCalendarEventDetails(event)
            .then(
                () => {
                    resetEventForm()
                    onComplete?.()
                },
                () => {
                    setSaveRequestResult('error')
                },
            )
    }, [
        selectedEvent,
        form,
        eventSaving,
        onComplete,
        saveCalendarEventDetails,
        resetEventForm,
    ])

    /** Deletes the event */
    const handleDelete = useCallback(() => {
        if (eventDeleting) {
            return
        }

        assertDefined(selectedEvent)

        setDeleteRequestResult(undefined)
        setSaveRequestResult(undefined)

        openModal({
            actions: {
                ok: {
                    action() {
                        assertDefined(selectedEvent.id)

                        deleteCalendarEvent(selectedEvent.id)
                            .then(
                                () => {
                                    resetEventForm()
                                    onComplete?.()
                                },
                                () => {
                                    setDeleteRequestResult('error')
                                },
                            )
                    },
                    title: message.delete_modal_ok(),
                },
                cancel: {
                    title: message.delete_modal_cancel(),
                },
            },
            body: message.delete_modal_text(
                selectedEvent.title,
            ),
            title: message.delete_modal_title(),
        })
    }, [
        selectedEvent,
        deleteCalendarEvent,
        eventDeleting,
        message,
        onComplete,
        openModal,
        resetEventForm,
    ])

    const clearDeleteResult = useCallback(() => {
        setDeleteRequestResult(undefined)
    }, [])


    return {
        saveRequestResult,
        deleteRequestResult,
        clearDeleteResult,
        clearSaveResult,
        handleDelete,
        handleSave,
    }
}
