import type {FC} from 'react'
import React, {useEffect, useRef} from 'react'
import type {UseFormReturn} from 'react-hook-form'

import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import {useCalendarStateMethods} from '@bpinc/cal-state-context'
import {type CalendarEventFields} from '@bpinc/cal-state-types'
import {getDefaultValues} from '@bpinc/cal-utils'
import {SmForm} from '@bpinc/lib-external-ui-elements'
import {useOnlyDigitsValidationRule} from '@bpinc/lib-forms-base'
import {
    CheckboxField,
    DatePickerField,
    SelectField,
    TextAreaField,
    TextField,
} from '@bpinc/lib-forms-ui'
import {MS_IN_HOUR, useAsRef} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useScrollCalendarToDate} from '../hooks'
import {mergeDateTime} from '../utils'
import {AssigneesComboBox} from './AssigneesComboBox'
import {EventContacts} from './EventContacts'
import {EventDetails} from './EventDetails'
import {TimezoneSelect} from './TimezoneSelect'


const units: CalendarEventFields['remindInUnit'][] = ['MINUTES', 'HOURS', 'DAYS']

const remindUnitsOptions = units.map(unit => ({
    key: unit,
    text: unit,
    value: unit,
}))

interface EventEditProps {
    form: UseFormReturn<CalendarEventFields>
    event: CalendarEventDetails
}

export const EventEdit: FC<EventEditProps> = ({
    form,
    event,
}) => {
    const {t: {CALENDAR_EVENT_FORM: message}} = useLocalizationState()
    const scrollCalendarToDate = useScrollCalendarToDate()

    const {updateSelectedEvent} = useCalendarStateMethods()

    const {control, watch, setValue} = form
    const [
        allDay,
        startDate,
        startTime,
        endDate,
        endTime,
    ] = watch([
        'allDay',
        'startDate',
        'startTime',
        'endDate',
        'endTime',
    ])

    // Remember start and end time to compare them with changed ones.
    // This is needed to handle situation when user selects a start time
    // after end time, and vise versa
    // const momentRef = usePrevious(favoritesQueue)
    const momentRef = useRef({endMoment: 0, startMoment: 0})

    const defaultValues = getDefaultValues(event)
    const calendarEventRef = useAsRef(event)

    const onlyDigitsValidationRule = useOnlyDigitsValidationRule()

    /**
     * Updates selectedEvent after a user changed date or time.
     * Also corrects mistakes when start moment is after end moment
    */
    useEffect(() => {
        if (!startDate || !endDate || !startTime || !endTime || !calendarEventRef.current) {
            return
        }

        let nextStartMoment = mergeDateTime(startDate, startTime)
        let nextEndMoment = mergeDateTime(endDate, endTime)

        if (nextEndMoment <= nextStartMoment) {
            // Detect which moment was changed by user
            if (nextEndMoment !== momentRef.current.endMoment) {
                nextStartMoment = Math.floor(nextEndMoment / MS_IN_HOUR) * MS_IN_HOUR
                if (nextEndMoment === nextStartMoment) {
                    nextStartMoment -= MS_IN_HOUR
                }
                const isoString = new Date(nextStartMoment).toISOString()
                setValue('startTime', isoString)
                setValue('startDate', isoString)
            } else {
                nextEndMoment = Math.ceil(nextStartMoment / MS_IN_HOUR) * MS_IN_HOUR
                if (nextEndMoment === nextStartMoment) {
                    nextEndMoment += MS_IN_HOUR
                }
                const isoString = new Date(nextEndMoment).toISOString()
                setValue('endTime', isoString)
                setValue('endDate', isoString)
            }
        }

        if (
            momentRef.current.endMoment !== nextEndMoment ||
                momentRef.current.startMoment !== nextStartMoment
        ) {
            updateSelectedEvent({startTime: nextStartMoment, endTime: nextEndMoment})

            momentRef.current.endMoment = nextEndMoment
            momentRef.current.startMoment = nextStartMoment

            /**  If new event start time is not in current view, scroll calendar to it */
            scrollCalendarToDate(nextStartMoment)
        }
    }, [
        setValue,
        updateSelectedEvent,
        startDate,
        startTime,
        endDate,
        endTime,
        calendarEventRef,
        scrollCalendarToDate,
    ])

    /**
     * Updates selectedEvent after a user changed "all day" checkbox
     */
    useEffect(() => {
        if (!calendarEventRef.current) {
            return
        }
        if (allDay !== calendarEventRef.current.allDay) {
            updateSelectedEvent({allDay})
        }
    }, [allDay, calendarEventRef, updateSelectedEvent])

    return (
        <div className="overflow-y-auto overflow-x-hidden">
            <TextField
                autoFocus
                required
                tabIndex={0}
                name="title"
                control={control}
                placeholder={message.field_title_placeholder()}
            />
            <CheckboxField
                control={control}
                name="completed"
                label={message.field_completed_label()}
            />
            <SmForm.Group widths="equal">
                <SmForm.Field width="8">
                    <DatePickerField
                        readOnly
                        control={control}
                        clearable={false}
                        icon="caret down"
                        name="startDate"
                        label={message.field_date_label()}
                    />
                    <div className="mt-2">
                        <CheckboxField
                            control={control}
                            name="allDay"
                            label={message.field_allday_label()}
                        />
                    </div>
                </SmForm.Field>
                <DatePickerField
                    readOnly
                    clearable={false}
                    width="8"
                    control={control}
                    icon="caret down"
                    name="endDate"
                    label="&nbsp;"
                />
            </SmForm.Group>
            <SmForm.Group widths="equal">
                <DatePickerField
                    readOnly
                    clearable={false}
                    disabled={allDay}
                    width="8"
                    control={control}
                    displayFormat="HH:mm"
                    icon="caret down"
                    name="startTime"
                    label={message.field_time_label()}
                />
                <DatePickerField
                    readOnly
                    clearable={false}
                    disabled={allDay}
                    width="8"
                    control={control}
                    displayFormat="HH:mm"
                    icon="caret down"
                    name="endTime"
                    label="&nbsp;"
                />
            </SmForm.Group>
            {!allDay && <TimezoneSelect control={control} />}
            <SmForm.Group>
                <TextField
                    width="4"
                    tabIndex={0}
                    control={control}
                    defaultValue={0}
                    label={message.field_reminder_label()}
                    rules={onlyDigitsValidationRule}
                    name="remindIn"
                    placeholder="0"
                />
                <SelectField
                    width="12"
                    tabIndex={0}
                    control={control}
                    defaultValue="MINUTES"
                    label="&nbsp;"
                    name="remindInUnit"
                    options={remindUnitsOptions}
                />
            </SmForm.Group>
            <SmForm.Field>
                <AssigneesComboBox
                    control={control}
                    defaultValue={defaultValues.assignees}
                    label={message.field_assignee_label()}
                    name="assignees"
                />
            </SmForm.Field>
            <TextAreaField
                tabIndex={0}
                control={control}
                label={message.field_summary_label()}
                maxRows={3}
                minRows={3}
                name="notes"
            />
            <SmForm.Field>
                <EventDetails form={form} />
            </SmForm.Field>
            <SmForm.Field>
                <EventContacts control={control} />
            </SmForm.Field>
        </div>
    )
}
