import React, {useMemo} from 'react'

import {useGetContactPictureUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import type {CalendarEventContact} from '@bpinc/cal-state-types'
import type {PropsAvatar} from '@bpinc/lib-contact-list-item-ui'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {Panel} from '@bpinc/lib-ui-elements'

import {ContactPhoneLabel} from '../EventContacts/ContactPhoneLabel'


type EventContactsDetailsProps = {
    contact: CalendarEventContact
}

export function EventContactDetails({
    contact,
}: EventContactsDetailsProps) {
    const getContactPictureUrl = useGetContactPictureUrl()
    const {formatFullName} = useFormatting()

    const {id, firstName, lastName, phoneType, phone} = useMemo(() => {
        if (contact.type == 'existing') {
            return ({
                id: contact.id,
                firstName: contact.firstName,
                lastName: contact.lastName,
                phone: contact.primaryPhone?.phone || '',
                phoneType: contact.primaryPhone?.type,
            })
        }
        return {
            id: undefined,
            firstName: contact.name,
            lastName: '',
            phone: contact.phone.phone,
            phoneType: contact.phone.type,
        }
    }, [contact])


    const avatar = useMemo<PropsAvatar>(() => (
        {firstName, lastName, url: getContactPictureUrl(id), type: 'contact'}
    ), [firstName, id, lastName, getContactPictureUrl])

    const body = useMemo(() => phoneType ? <ContactPhoneLabel phone={phone} type={phoneType} /> : null, [phone, phoneType])

    return (
        <Panel className="mb-2 last:mb-0">
            <ContactListItem
                className="[&>*]:min-h-[auto]"
                contentClassName="p-0"
                contactId={id}
                title={formatFullName(firstName, lastName)}
                body={body}
                avatarProps={avatar}
            />
        </Panel>
    )
}

