import {isEqual} from 'lodash-es'

import type {PhoneTypes} from '@bpinc/ad-contacts-state-types'
import {formatFullName} from '@bpinc/ad-formatting-helpers'
import type {SessionData} from '@bpinc/ad-session-types'
import type {CalendarContact, CalendarEventDetails, SaveCalendarEventRequest} from '@bpinc/cal-request-urls'
import type {CalendarEventExistingContact, SelectionRange} from '@bpinc/cal-state-types'
import {
    type CalendarEventContact,
    type CalendarEventFields,
    type EventMapItem,
    type RegularEvent,
    EventType,
} from '@bpinc/cal-state-types'
import {assertDefined, typedKeys} from '@bpinc/lib-types-helpers'


/**
 * This fields
 */
export const TIME_FIELDS: Array<keyof CalendarEventFields> =
    ['endDate', 'endTime', 'startDate', 'startTime']


/**
 * Converts event details to form default values.
 * These default values are used as base values for comparision
 * with current form values and detecting dirty fields
 */
export const getDefaultValues = (
    selectedEvent: CalendarEventDetails,
): CalendarEventFields => {
    const startMoment = new Date(selectedEvent.startTime).toISOString()
    const endMoment = new Date(selectedEvent.endTime).toISOString()

    return {
        allDay: selectedEvent.allDay || false,
        assignees: selectedEvent.assignees || [],
        completed: selectedEvent.completed,
        contacts: selectedEvent.contacts?.map(c => formatCalendarContactToContactFields(c)) || [],
        email: selectedEvent.email || undefined,
        endDate: endMoment,
        endTime: endMoment,
        notes: selectedEvent.notes || '',
        remindIn: selectedEvent.remindIn ? Number(selectedEvent.remindIn) : 0,
        remindInUnit: selectedEvent.remindInUnit ?? 'MINUTES',
        screenpopSize: selectedEvent.screenpopSize || 0,
        startDate: startMoment,
        startTime: startMoment,
        timezone: selectedEvent.timezone || '',
        title: selectedEvent.title || '',
    }
}

/**
 * Converts event contact to contact form fields
 */
export const formatCalendarContactToContactFields = (
    contact: CalendarContact,
): CalendarEventContact => {
    const firstName = contact.first_name || 'Anonymous'
    const lastName = contact.last_name || ''

    if (contact.id) {
        return {
            type: 'existing',
            id: contact.id,
            firstName,
            lastName,
            primaryPhone: {
                phone: contact.calendar_phone?.phone || '',
                type: contact.calendar_phone?.type as PhoneTypes,
            },
        }
    }
    return {
        type: 'new',
        name: firstName + (lastName && ' ' + lastName),
        phone: {
            phone: contact.calendar_phone?.phone || '',
            type: contact.calendar_phone?.type as PhoneTypes,
        },
        create: false,
    }
}

/**
 * Detects that two events are equal.
 * Equility means that both events have identical id, or both are new events
 */
export const isEqualCalendarEvents = (
    event1: CalendarEventDetails | undefined,
    event2: CalendarEventDetails | undefined,
): boolean => {
    return event1 && event2
        ? (isNewEvent(event1) && isNewEvent(event2)) || event1.id === event2.id
        : false
}

/**
 * Checks that any of form values is dirty with respect to default values
 */
export const isFormDirty = (
    isNewEvent: boolean | undefined,
    defaultValues: CalendarEventFields,
    values: CalendarEventFields,
): boolean => {
    const dirtyFields = typedKeys(values)
        .reduce<Record<string, boolean>>((acc, key) => {
            if (!isEqual(values[key], defaultValues[key])) {
                acc[key] = true
            }
            return acc
        }, {})

    // For a new event exclude time fields from dirty fields
    if (isNewEvent) {
        TIME_FIELDS.forEach(field => { delete dirtyFields[field] })
    }
    return typedKeys(dirtyFields).length > 0
}

/**
* Detects that event has not been saved
*/
export const isNewEvent = (event: CalendarEventDetails): boolean =>
    Boolean(!event.id && event._temporaryId)


export function isRegularEvent(event: EventMapItem): event is RegularEvent {
    return event.type === EventType.EVENT
}

/**
 * Makes event details suitable to save on the backend
 * @param event Event details
 * @param sessionData Session data
 * @returns Event details to save
 */
export const normalizeEventDetailsToSave = (
    event: CalendarEventDetails,
    sessionData: SessionData,
): SaveCalendarEventRequest => {
    const {userId: myUserId, firstName, lastName, displayNameFormat} = sessionData

    const myFullName = formatFullName(displayNameFormat, firstName, lastName)
    const userName = event.userName || myFullName
    const userId = event.userId || myUserId

    assertDefined(userId)

    return {
        ...event,
        completed: !!event.completed,
        startTime: event.startTime,
        endTime: event.endTime,
        remindIn: event.remindIn ? Number(event.remindIn) : undefined,
        remindInUnit: event.remindInUnit ?? 'MINUTES',
        screenpopSize: event.screenpopSize || -1,
        userId,
        userName,
    }
}

/**
 * Prevents selection range from component state to be changed in place
 */
export const getSortedSelectionRange = (range: SelectionRange): SelectionRange => {
    return range[0] > range[1] ? [range[1], range[0]] : [...range]
}

export const mergeEvents = (
    formValues: CalendarEventFields,
    defaultValues: CalendarEventFields,
    newEvent: CalendarEventDetails,
) => {
    const nextDefaultValues = getDefaultValues(newEvent)

    const keys = typedKeys(nextDefaultValues).filter(
        key => key in formValues,
    )

    const newFormValues = keys.reduce<CalendarEventFields>(
        (acc, key) => {
            const value = acc[key]
            const nextDefaultValue = nextDefaultValues[key]

            if (value === nextDefaultValue) {
                return acc
            }

            // We prevent dirty fields (fields that user changed),
            // but always reset time fields
            const isDirtyField = value !== defaultValues[key]
            const needForceUpdate = TIME_FIELDS.includes(key)
            if (isDirtyField && !needForceUpdate) {
                return acc
            }

            return {
                ...acc,
                [key]: nextDefaultValue,
            }
        },
        {...formValues},
    )

    return newFormValues
}

export function isExistingContact(contact: CalendarEventContact): contact is CalendarEventExistingContact {
    return contact.type === 'existing'
}
