
import {useMemo} from 'react'

import type {GetDaysOfWeekOptions} from '@bpinc/cal-utils'
import {getDaysOfWeek} from '@bpinc/cal-utils'
import type {WeekDay} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


export function useDaysOfWeek(options: GetDaysOfWeekOptions) {
    const {t: {DAYS: messages}} = useLocalizationState()

    const dictFull: Record<WeekDay, string> = useMemo(() => ({
        'sunday': messages.sunday(),
        'monday': messages.monday(),
        'tuesday': messages.tuesday(),
        'wednesday': messages.wednesday(),
        'thursday': messages.thursday(),
        'friday': messages.friday(),
        'saturday': messages.saturday(),
    }), [messages])

    const dictShort: Record<WeekDay, string> = useMemo(() => ({
        'sunday': messages.sundayShort(),
        'monday': messages.mondayShort(),
        'tuesday': messages.tuesdayShort(),
        'wednesday': messages.wednesdayShort(),
        'thursday': messages.thursdayShort(),
        'friday': messages.fridayShort(),
        'saturday': messages.saturdayShort(),
    }), [messages])

    const dictLetter: Record<WeekDay, string> = useMemo(() => ({
        'sunday': messages.sundayLetter(),
        'monday': messages.mondayLetter(),
        'tuesday': messages.tuesdayLetter(),
        'wednesday': messages.wednesdayLetter(),
        'thursday': messages.thursdayLetter(),
        'friday': messages.fridayLetter(),
        'saturday': messages.saturdayLetter(),
    }), [messages])

    return useMemo(() =>
        getDaysOfWeek(dictFull, dictShort, dictLetter, options)
    , [dictFull, dictShort, dictLetter, options])
}
