import React from 'react'

import type {NotEmptyEventMapItem} from '@bpinc/cal-state-types'

import {useShiftTitle} from '../../../../../hooks/useShiftTitle'
import type {ViewMonthEventProps} from './ViewMonthEvent'
import {ViewMonthEventBlock} from './ViewMonthEventBlock'


type ViewMonthShiftProps = Omit<ViewMonthEventProps, 'item'> & {
    item: NotEmptyEventMapItem
}

export const ViewMonthShift: React.FC<ViewMonthShiftProps> = props => {
    const title = useShiftTitle(props.item)

    return (
        <ViewMonthEventBlock
            title={title}
            className="border-l-cal-shift-border bg-cal-shift-bg"
            {...props}
        />
    )
}
