import React, {useCallback, useMemo, useState} from 'react'
import type {BpPeriodSelectProps} from '@servicepattern/jskit'
import {BpPeriodSelect} from '@servicepattern/jskit'

import {useOpenCreateRequestModal} from '@bpinc/cal-modals-ui'
import {SmSelect} from '@bpinc/lib-external-ui-elements'
import {getLongDateFormat, languageToLocaleMap} from '@bpinc/lib-helpers'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {RequestStatus} from '../types'
import {RequestType} from '../types'
import {StatusFilter} from './StatusFilter'


export const Filters: React.FC = () => {
    const {t: {CALENDAR_REQUESTS: message}, language} = useLocalizationState()
    const openCreateRequestModal = useOpenCreateRequestModal()

    const [dateRange, setDateRange] = useState<BpPeriodSelectProps['value']>({
        after: null,
        before: null,
    })
    const [statusFilter, setStatusFilter] = useState<RequestStatus>()

    const onNewRequest = useCallback(() => {
        openCreateRequestModal()
    }, [openCreateRequestModal])

    const onStatusChange = useCallback((status?: RequestStatus) => {
        setStatusFilter(status)
    }, [])

    const typeOptions = useMemo(() => ([
        {key: 'all', value: '', text: message.any_type()},
        {key: RequestType.PTO, value: RequestType.PTO, text: RequestType.PTO},
        {key: RequestType.VACATION, value: RequestType.VACATION, text: RequestType.VACATION},
    ]), [message])

    return (
        <div className="flex justify-between">
            <div>
                <SemanticButton
                    size="small"
                    onClick={onNewRequest}
                    className="mr-4"
                    icon="plus"
                    color="blue"
                    tooltip={message.create_request()}
                />
                <span className="text-sm text-cal-dark-text">
                    {message.new_request()}
                </span>
            </div>
            <div className="flex items-center">
                <BpPeriodSelect
                    popupPosition="bottom center"
                    config={{
                        input: {
                            className: 'cursor-pointer min-w-[300px] mr-2',
                            format: getLongDateFormat('L'),
                            placeholder: message.choose_requests_period(),
                        },
                        form: {
                            showTime: false,
                            showSearchCheckbox: false,
                            dateFormat: languageToLocaleMap[language],
                            timeFormat: languageToLocaleMap[language],
                        },
                    }}
                    hideOnScrollPopup={false}
                    onChange={setDateRange}
                    value={dateRange}
                    clearable
                />
                <SmSelect
                    className="mr-2"
                    placeholder={message.any_type()}
                    options={typeOptions}
                />
                <StatusFilter
                    value={statusFilter}
                    onChange={onStatusChange}
                />
            </div>
        </div>
    )
}
