import React from 'react'

import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {AppType} from '@bpinc/state-lookup'

import {OpenEventButton} from './OpenEventButton/OpenEventButton'
import type {CalendarEventNotificationModalProps} from './CalendarEventNotificationModal'
import {useHandlers} from './useHandlers'


type ActionsProps = Pick<CalendarEventNotificationModalProps, 'eventId' | 'onClose'> & {
    type: AppType
}

export const Actions: React.FC<ActionsProps> = ({eventId, onClose, type}) => {
    const {t} = useLocalizationState()
    const {handleDismiss, handleSnooze, handleOpen} = useHandlers({eventId, onClose})

    return (
        <div className="-ml-2 mt-4 flex justify-end">
            <OpenEventButton
                type={type}
                eventId={eventId}
                onClick={handleOpen}
            />
            <SemanticButton
                className="mx-2 flex h-7 items-center justify-center text-base font-normal"
                inverted
                onClick={handleSnooze}
            >
                {t.INCOMING_CALL_DIALOG_BOX.calendarAlert_snooze()}
            </SemanticButton>
            <SemanticButton
                onClick={handleDismiss}
                inverted
                className="ml-6 flex h-7 items-center justify-center text-base font-normal"
            >
                {t.INCOMING_CALL_DIALOG_BOX.calendarAlert_dismiss()}
            </SemanticButton>
        </div>
    )
}
