import type {FC} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


type CommonOpenEventButtonProps = {
    onClick?: () => void
    disabled?: boolean
}

export const CommonOpenEventButton: FC<CommonOpenEventButtonProps> = ({disabled, onClick}) => {
    const {t} = useLocalizationState()

    return (
        <SemanticButton
            disabled={disabled}
            onClick={onClick}
            inverted
            className={tcn(
                'mx-2 flex h-7 items-center justify-center text-base font-normal',
                'bg-primary-legacy hover:bg-primary-contrast focus:bg-primary-contrast',
            )}
        >
            {t.INCOMING_CALL_DIALOG_BOX.incomingInteraction_open()}
        </SemanticButton>
    )
}
