import {useEffect} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import {View} from '@bpinc/cal-state-types'
import {getRangeFromView} from '@bpinc/cal-utils'
import {usePreviousValue} from '@bpinc/lib-helpers'


export function useRequestsLoader() {
    const {getTimeoffRequests} = useCalendarStateMethods()
    const {calendarOwnerUserId, viewDate} = useCalendarState(
        ['calendarOwnerUserId', 'viewDate'],
    )
    const prevViewDate = usePreviousValue(viewDate)

    useEffect(() => {
        if (prevViewDate) {
            return
        }

        const [start, end] = getRangeFromView(viewDate, View.MONTH)
        if (calendarOwnerUserId) {
            getTimeoffRequests({
                userId: calendarOwnerUserId,
                dateFrom: new Date(start).toISOString(),
                dateTo: new Date(end).toISOString(),
            })
        }
    }, [
        calendarOwnerUserId,
        viewDate,
        prevViewDate,
        getTimeoffRequests,
    ])
}
