import React, {useCallback} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import {formatRequestDate, formatRequestedTime} from '@bpinc/cal-utils'
import {SmTransition} from '@bpinc/lib-external-ui-elements'
import {capitalize, useEscapeManager} from '@bpinc/lib-helpers'
import {IconClose, IconTrash} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {StatusIcon} from './StatusIcon'
import {formatApproval, isRequestProcessed} from './Table'
import type {Request} from './types'


type Props = {
    request?: Request
    onClose: () => void
}

export const RequestSidebar: React.FC<Props> = React.memo(({
    request,
    onClose,
}) => {
    const {t: {CALENDAR_REQUESTS: message}} = useLocalizationState()
    const isDeleting = useCalendarState('timeoffRequestDeleting')
    const {deleteTimeoffRequest} = useCalendarStateMethods()
    const show = Boolean(request)

    const onDelete = useCallback(() => {
        if (request) {
            deleteTimeoffRequest(request.id)
                .then(() => onClose())
                .catch(() => onClose())
        }
    }, [request, deleteTimeoffRequest, onClose])

    useEscapeManager(onClose, {enabled: show})

    return (
        <div
            className={tcn(
                'absolute right-0 top-0 h-full w-0 bg-white shadow',
                'ease transition-[width] duration-300',
                {'w-90': show},
            )}
        >
            <SmTransition
                visible={show}
                duration="300"
                animation="fade right"
                unmountOnHide
            >
                <div
                    className={tcn(
                        'relative box-border flex h-full w-90 flex-col',
                        'overflow-y-auto overflow-x-hidden',
                    )}
                >
                    <div className="box-border flex h-[34px] items-center justify-end border-y border-cal-border bg-cal-light-bg px-2">
                        <button
                            className={tcn(
                                'flex h-8 w-8 cursor-pointer items-center justify-center',
                                {'cursor-not-allowed': isDeleting},
                            )}
                            onClick={isDeleting ? undefined : onDelete}
                        >
                            <IconTrash className="h-4.5 w-3.5" />
                        </button>
                        <button
                            className="flex h-8 w-8 cursor-pointer items-center justify-center"
                            onClick={onClose}
                        >
                            <IconClose className="w-5" />
                        </button>
                    </div>
                    {request && (
                        <div className="flex flex-col px-6 py-5">
                            <div className="mb-4 border-b border-cal-border">
                                <span className="mb-4 block text-lg font-bold text-cal-dark-text">
                                    {message.absence_request()}
                                </span>
                                <div className="mb-4 flex flex-col gap-1">
                                    <InfoItem
                                        label={message.requested_time()}
                                        value={formatRequestedTime(request.reqTimeStart, request.reqTimeEnd)}
                                    />
                                    <InfoItem
                                        label={message.type()}
                                        value={request.type}
                                    />
                                    <InfoItem
                                        label={message.created_at()}
                                        value={formatRequestDate(request.submissionDate)}
                                    />
                                </div>
                            </div>
                            {request.comment && (
                                <span className="mb-6">
                                    {request.comment}
                                </span>
                            )}
                            <div className="flex flex-col gap-1">
                                <div className="flex">
                                    <StatusIcon status={request.status} />
                                    <span className="ml-2 font-bold">{capitalize(request.status)}</span>
                                </div>
                                {isRequestProcessed(request) && (
                                    <>
                                        <div className="mb-1 text-sm text-cal-header-text">
                                            <span>{capitalize(request.status) + ' at '}</span>
                                            <span>{formatRequestDate(request.approvalDate) + ', '}</span>
                                            <span>{formatApproval(request, message.by_system())}</span>
                                        </div>
                                        <span>{request.decisionDetails}</span>
                                    </>
                                )}
                            </div>
                        </div>
                    )}
                </div>
            </SmTransition>
        </div>
    )
})

type InfoItemProps = {
    label: string
    value?: string
}

const InfoItem: React.FC<InfoItemProps> = ({label, value}) => {
    return (
        <div className="flex">
            <div className="mr-2 w-32 shrink-0 truncate text-cal-header-text">
                {label}
            </div>
            <div className="truncate text-cal-dark-text">
                {value}
            </div>
        </div>
    )
}
