import type {FC} from 'react'
import React, {memo, useCallback, useMemo} from 'react'

import {useCalendarStateMethods} from '@bpinc/cal-state-context'
import type {CalendarEventFields} from '@bpinc/cal-state-types'
import {SmHeader} from '@bpinc/lib-external-ui-elements'
import {IconTrash} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = {
    defaultValues: CalendarEventFields
    isNewEvent: boolean
    editing: boolean
    deleting: boolean
    saving: boolean
    onDelete(): void
    isCommunicator: boolean
}

export const CalendarEventFormControls: FC<Props> = memo(({
    defaultValues,
    isNewEvent,
    editing,
    deleting,
    saving,
    onDelete,
    isCommunicator,
}) => {
    const {t: {CALENDAR_EVENT_FORM: message}} = useLocalizationState()

    const {
        closeEventForm,
        editSelectedEvent,
    } = useCalendarStateMethods()

    const handleCancel = useCallback(() => closeEventForm(), [closeEventForm])
    const handleEdit = useCallback(() => editSelectedEvent(), [editSelectedEvent])

    const saveButton = useMemo(() => (
        <SemanticButton
            type="submit"
            color="blue"
            aria-label={message.aria_event_save_label()}
            content={message.button_save_text()}
            loading={saving}
            disabled={deleting}
        />
    ), [message, saving, deleting])

    const editButton = useMemo(() => (
        <SemanticButton
            type="button"
            basic
            aria-label={message.aria_event_edit_label()}
            content={message.button_edit_text()}
            enabledInOffline
            disabled={deleting}
            onClick={handleEdit}
        />
    ), [message, deleting, handleEdit])

    const cancelButton = useMemo(() => (
        <SemanticButton
            type="button"
            onClick={handleCancel}
            aria-label={message.aria_event_cancel_label()}
            {...(isCommunicator ? {
                basic: true,
                content: message.button_cancel_text(),
                disabled: saving || deleting,
            } : {
                className: 'flex h-auto items-center bg-transparent p-1',
                size: 'large',
                icon: 'times',
            })}
        />
    ), [message, saving, deleting, handleCancel, isCommunicator])

    const deleteButton = useMemo(() => (
        <SemanticButton
            type="button"
            basic
            className={tcn({'px-[11px] py-[7px]': !isCommunicator})}
            aria-label={message.aria_event_delete_label()}
            onClick={onDelete}
            loading={deleting}
            disabled={saving}
        >
            {isCommunicator ? message.button_delete_text() : (
                <IconTrash
                    className={tcn('h-4.5 w-3.5', {
                        'opacity-0': deleting,
                    })}
                />
            )}
        </SemanticButton>
    ), [message, saving, deleting, onDelete, isCommunicator])

    if (isCommunicator) {
        return (
            <div className="flex items-center justify-end pb-4">
                {!editing && (
                    <SmHeader as="h3" className="m-0">
                        {defaultValues.title}
                    </SmHeader>
                )}
                {editing && !isNewEvent && (
                    <>
                        {cancelButton}
                        {saveButton}
                    </>
                )}
                <div className="flex grow-[2] justify-end last:!mr-0">
                    {isNewEvent && cancelButton}
                    {isNewEvent && saveButton}
                    {!editing && editButton}
                    {!isNewEvent && deleteButton}
                </div>
            </div>
        )
    }

    return (
        <div className="flex items-center justify-end pb-4">
            {cancelButton}
            <div className="flex grow-[2] justify-end">
                {editing && saveButton}
                {!editing && editButton}
                {!isNewEvent && deleteButton}
            </div>
        </div>
    )
})
