import type {FC} from 'react'
import React, {useEffect, useMemo, useState} from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'

import type {ScheduleTableRowData} from './ScheduleTableRow'
import {ScheduleTableRowType} from './ScheduleTableRow'


type ScheduleTimelineProps = {
    tableRows: ScheduleTableRowData[]
}

export const ScheduleTimeline: FC<ScheduleTimelineProps> = ({tableRows}) => {
    const [top, setTop] = useState(0)
    const style = useMemo(() => ({top}), [top])

    useEffect(() => {
        function timeToTop() {
            const now = new Date()

            const firstTableRowMoreThanNowIndex = tableRows.findIndex(row => row.date >= now.valueOf())
            const previousRows = tableRows.slice(0, firstTableRowMoreThanNowIndex)

            const previousRowsHeightSum = previousRows.reduce((sum, row) => {
                const heightsByRowType = {
                    [ScheduleTableRowType.EVENT]: 44,
                    [ScheduleTableRowType.WEEK_TITLE]: 34,
                } as const

                const rowHeight = heightsByRowType[row.type]

                return sum + rowHeight
            }, 0)

            return previousRowsHeightSum
        }

        const recalculateTop = () => setTop(timeToTop())
        const interval = setInterval(recalculateTop, 30_000)
        recalculateTop()

        return () => clearInterval(interval)
    }, [tableRows])

    return (
        <div
            className={tcn(
                'absolute top-0 z-[1] h-px w-full bg-cal-timeline',
                'before:absolute before:block before:h-2 before:w-2',
                'before:-translate-x-2 before:-translate-y-1/2 before:rounded-full before:bg-cal-timeline',
            )}
            style={style}
        />
    )
}
