import {omit} from 'lodash-es'

import type {ContactType} from '@bpinc/ad-contacts-state-types'
import {PhoneTypes} from '@bpinc/ad-contacts-state-types'
import type {CalendarContact, CalendarEventDetails} from '@bpinc/cal-request-urls'
import type {CalendarEventContact, CalendarEventFields, CalendarEventNewContact} from '@bpinc/cal-state-types'
import {getUserTimezone} from '@bpinc/lib-helpers'

import {mergeDateTime} from './time'

/**
 * Converts contact type to contact form fields
 */
export const formatContactToContactsFields = (contact: ContactType): CalendarEventContact => {
    return {
        type: 'existing',
        id: contact.Id.oid,
        lastName: contact.lastName,
        firstName: contact.firstName,
        primaryPhone: contact.primaryPhone,
    }
}

/**
 * Default values for created manually calendar contact
 */
export const getDefaultNewContact = (): CalendarEventNewContact => {
    return {
        type: 'new',
        name: '',
        phone: {
            type: PhoneTypes.BUSINESS,
            phone: '',
        },
        create: false,
    }
}

/**
 * Makes event details to save them
 */
export const getEventDetailsFromFields = (
    selectedEvent: CalendarEventDetails,
    fields: Partial<CalendarEventFields>,
): CalendarEventDetails => {
    const startTime = fields.startDate && fields.startTime
        ? mergeDateTime(fields.startDate, fields.startTime)
        : selectedEvent.startTime
    const endTime = fields.endDate && fields.endTime
        ? mergeDateTime(fields.endDate, fields.endTime)
        : selectedEvent.endTime

    return {
        ...selectedEvent,
        ...omit(fields, ['timezone', 'startTime', 'startDate', 'endTime', 'endDate', 'contacts']),
        contacts: fields.contacts?.map(contact => getContactDetailsFromFields(contact)) || [],
        timezone: fields.allDay ? getUserTimezone() : fields.timezone,
        startTime,
        endTime,
    }
}

/**
 * Get first and last name from contact name
 */
export const parseContactName = (name: string): [string, string] => {
    if (name.length === 0) {
        return ['Anonymous', '']
    }
    const parts = name.replace(/\s+/g, ' ').trim().split(' ')
    return [parts[0], parts.slice(1).join(' ')]
}

/**
 * Prepare contact data to save
 */
const getContactDetailsFromFields = (contact: CalendarEventContact): CalendarContact => {
    if (contact.type === 'new') {
        const [firstName, lastName] = parseContactName(contact.name)
        return {
            create: contact.create,
            first_name: firstName,
            last_name: lastName,
            calendar_phone: {
                type: contact.phone.type,
                phone: contact.phone.phone,
            },
        }
    }

    return {id: contact.id}
}
