import React, {useCallback} from 'react'

import {Checkbox} from '@bpinc/lib-ui-elements'


export type CalendarSidebarFilter = {
    name: string
    label: string
    disabled?: boolean
    checked?: boolean
}

export type CalendarSidebarFiltersProps = {
    title: string
    filters: CalendarSidebarFilter[]
    onFilterChange: (calendar: string, value: boolean) => void
}

export const CalendarSidebarFilters: React.FC<CalendarSidebarFiltersProps> = React.memo(({
    title,
    filters,
    onFilterChange,
}) => {
    const onChange = useCallback((calendar: string) => (value: boolean) => {
        onFilterChange(calendar, value)
    }, [onFilterChange])

    return (
        <div className="pl-2">
            <div className="mb-4">{title}</div>
            {filters.map(({name, label, disabled, checked}) => (
                <Checkbox
                    key={name}
                    className="mb-2"
                    name={name}
                    label={label}
                    onChange={onChange(name)}
                    disabled={disabled}
                    checked={checked}
                />
            ))}
        </div>
    )
})
