import {useCallback, useMemo} from 'react'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import {getRangeFromView} from '@bpinc/cal-utils'
import {getStartOfDay} from '@bpinc/lib-helpers'


export const useScrollCalendarToDate = () => {
    const {view, viewDate} = useCalendarState(['view', 'viewDate'])
    const {changeViewDate} = useCalendarStateMethods()

    const [viewStart, viewEnd] = useMemo(() =>
        getRangeFromView(viewDate, view, {expand: true})
    , [view, viewDate])

    return useCallback((date: number) => {
        if (viewStart > date || viewEnd < date) {
            changeViewDate(getStartOfDay(date))
        }
    }, [viewStart, viewEnd, changeViewDate])
}
