import React, {useCallback} from 'react'
import type {Control} from 'react-hook-form'
import {useFieldArray} from 'react-hook-form'

import type {CalendarEventContact, CalendarEventFields} from '@bpinc/cal-state-types'
import {isExistingContact} from '@bpinc/cal-utils'
import {Button, Panel} from '@bpinc/lib-ui-elements'

import {AddContactButton} from './AddContactButton'
import {ExistingContact} from './ExistingContact'
import {NewContact} from './NewContact'


type EventContactsProps = {
    control: Control<CalendarEventFields>
}

export function EventContacts({
    control,
}: EventContactsProps) {
    const {fields: contacts, remove, append} = useFieldArray({
        control,
        name: 'contacts',
        keyName: 'dataId',
    })
    const onContactAdd = useCallback((contact: CalendarEventContact) => {
        const addedContacts = contacts.map(c => isExistingContact(c) ? c : null).filter(Boolean)
        if (isExistingContact(contact) && addedContacts.some(c => contact.id === c?.id)) {
            return
        }
        append(contact)
    }, [append, contacts])

    return (
        <>
            {!!contacts.length && (
                <div className="my-4">
                    {contacts.map((contact, index) => (
                        <Contact key={contact.dataId} index={index} contact={contact} remove={remove} control={control} />
                    ))}
                </div>
            )}
            <AddContactButton onAdd={onContactAdd} />
        </>
    )
}

type SavedContactProps = {
    contact: CalendarEventContact
    remove: (index: number) => void
    index: number
    control: Control<CalendarEventFields>
}

function Contact({
    contact,
    remove,
    index,
    control,
}: SavedContactProps) {
    const handleRemove = useCallback(() => remove(index), [index, remove])

    return (
        <Panel
            className="mb-2 last:mb-0"
            actions={
                <Button
                    type="button"
                    className="text-main opacity-50 hover:opacity-100 focus:opacity-100 active:opacity-100"
                    transparent
                    semanticIcon="delete"
                    onClick={handleRemove}
                />}
        >
            {contact.type == 'existing'
                ? <ExistingContact index={index} contact={contact} control={control} />
                : <NewContact index={index} contact={contact} control={control} />
            }
        </Panel>
    )
}
