import React from 'react'

import {useShiftTitle} from '../../../../hooks/useShiftTitle'
import type {DayEventProps} from './DayEvent'
import {DayEventBlock} from './DayEventBlock'


export const DayShift: React.FC<DayEventProps> = props => {
    const title = useShiftTitle(props.item)

    return (
        <DayEventBlock
            title={title}
            className="border-l-cal-shift-border bg-cal-shift-bg"
            {...props}
        />
    )
}
