import React, {useMemo} from 'react'

import {useMonthName} from '@bpinc/cal-hooks'
import {getDateByMonthViewIndexes, getEventMapKey, getRangeFromView, getWeeksInMonth} from '@bpinc/cal-utils'
import {isToday} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'


type CalendarSidebarMonthProps = {
    monthDate: Date
    viewStart: Date
    viewEnd: Date
    eventsMap: Record<string, unknown>
    daysOfWeek: string[]
    hidePreviousMonth?: boolean
    hideNextMonth?: boolean
}

export const CalendarSidebarMonth: React.FC<CalendarSidebarMonthProps> = React.memo(({
    monthDate,
    viewStart,
    viewEnd,
    eventsMap,
    daysOfWeek,
    hidePreviousMonth,
    hideNextMonth,
}) => {
    const monthName = useMonthName(monthDate)
    const [start] = getRangeFromView(monthDate.getTime(), 'month', {expand: true}).map(d => new Date(d))

    const monthTitle = useMemo(() => {
        const year = monthDate.getFullYear()
        return `${monthName} ${year}`
    }, [monthDate, monthName])

    const weeksInMonth = useMemo(() =>
        getWeeksInMonth(monthDate.getTime())
    , [monthDate])

    return (
        <div className="mb-9 w-full">
            <div className="mb-1 pl-2 text-cal-dark-text">
                {monthTitle}
            </div>
            <table>
                <thead>
                    <tr>
                        {daysOfWeek.map((name, i) => (
                            <th
                                key={name + i}
                                className="h-8 text-sm font-normal text-cal-dark-text opacity-50"
                            >
                                {name}
                            </th>
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {Array.from(
                        {length: weeksInMonth},
                        (_, weekIndex) => (
                            <tr key={weekIndex}>
                                {daysOfWeek.map((__, dayIndex) => {
                                    const day = getDateByMonthViewIndexes(start, weekIndex, dayIndex)
                                    const isCurrentView = viewStart <= day && day <= viewEnd
                                    const isOtherMonth = day.getMonth() !== monthDate.getMonth()
                                    const isPreviousMonth = isOtherMonth && day < monthDate
                                    const isNextMonth = isOtherMonth && day > monthDate
                                    const hideValue = isPreviousMonth && hidePreviousMonth || isNextMonth && hideNextMonth
                                    const hasEvents = !!eventsMap[getEventMapKey(day)]

                                    return (
                                        <td
                                            key={dayIndex}
                                            className={tcn('h-8 w-8', {
                                                'rounded-none bg-cal-light-bg': !hideValue && isCurrentView,
                                            })}
                                        >
                                            <div
                                                className={tcn(
                                                    'flex h-7 w-7 select-none items-center justify-center rounded-[16px]',
                                                    'text-center text-sm text-cal-dark-text',
                                                    {
                                                        'bg-primary-legacy font-bold text-white': !hideValue && isToday(day),
                                                        'opacity-50': isOtherMonth,
                                                        'font-bold': hasEvents,
                                                    })}
                                            >
                                                {hideValue ? ' ' : day.getDate()}
                                            </div>
                                        </td>
                                    )
                                })}
                            </tr>
                        ),
                    )}
                </tbody>
            </table>
        </div>
    )
})
