import type {CalendarEventDetails, CalendarEventItem} from '@bpinc/cal-request-urls'
import {type SelectionRange, EventCustomId} from '@bpinc/cal-state-types'

import {isWithinOneDay} from '../dates'


export const eventDetailsToCalendarEvent = (event: CalendarEventDetails): CalendarEventItem => {
    return {
        id: event.id || EventCustomId.NEW_EVENT,
        allDay: event.allDay || false,
        start: event.startTime,
        end: event.endTime,
        title: event.title,
    }
}

export const selectionToCalendarEvent = (
    selection: SelectionRange,
): CalendarEventItem => {
    const [start, end] = selection.map(date => Number(date))

    return {
        id: EventCustomId.SELECTION,
        allDay: !isWithinOneDay(start, end),
        start,
        end,
        title: '',
    }
}
