import React, {useMemo} from 'react'

import {formatEventTimes} from '@bpinc/cal-event-ui'
import {type NotEmptyEventMapItem} from '@bpinc/cal-state-types'
import {removeSpaces} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const useWeekEventTitle = (
    item: NotEmptyEventMapItem,
    isHeader?: boolean,
) => {
    const language = useLocalizationState('language')

    return useMemo(() => {
        const titleStr = item.data.title
        const {from} = formatEventTimes({
            allDay: item.data.allDay,
            startTime: item.data.start,
            endTime: item.data.end,
            locale: language,
        })

        return (
            <>
                {titleStr || ''}
                {!isHeader && (titleStr && ', ') + removeSpaces(from.time)}
            </>
        )
    }, [item, isHeader, language])
}
