import {View} from '@bpinc/cal-state-types'

import {getRangeFromView} from './dates'


type Options = {
    weekStartsFromMonday?: boolean
    withoutWeekend?: boolean
}

export const getViewTitle = (
    view: View,
    viewDateTs: number,
    monthsListLong: string[],
    monthsListShort: string[],
    options?: Options,
): string => {
    const viewDate = new Date(viewDateTs)
    const viewDateMonth = viewDate.getMonth()
    const viewDateYear = viewDate.getFullYear()

    switch (view) {
        case View.MONTH: {
            const month = monthsListLong[viewDateMonth]
            return `${month} ${viewDateYear}`
        }
        case View.WEEK: {
            const [startDate, endDate] = getRangeFromView(
                viewDateTs,
                View.WEEK,
                {
                    weekStartsFromMonday: options?.weekStartsFromMonday,
                    withoutWeekend: options?.withoutWeekend,
                },
            ).map(d => new Date(d))
            const [startDay, startMonth, startYear] = [
                startDate.getDate(),
                monthsListShort[startDate.getMonth()],
                startDate.getFullYear(),
            ]
            const [endDay, endMonth, endYear] = [
                endDate.getDate(),
                monthsListShort[endDate.getMonth()],
                endDate.getFullYear(),
            ]

            // two months intersection
            if (endDay < startDay) {
                // not two years intersection
                if (startYear === endYear) {
                    // Oct 25  Nov 2, 2023
                    return `${startMonth} ${startDay} - ${endMonth} ${endDay}, ${viewDateYear}`
                }
                // Dec 30, 2023  Jan 6, 2024
                return `${startMonth} ${startDay}, ${startYear} - ${endMonth} ${endDay}, ${endYear}`
            }
            // Nov 11 - 18, 2023
            return `${startMonth} ${startDay} - ${endDay}, ${viewDateYear}`
        }
        case View.DAY: {
            const month = monthsListLong[viewDateMonth]
            const day = viewDate.getDate()
            return `${month} ${day}, ${viewDateYear}`
        }
        case View.SCHEDULE: {
            const currentMonth = monthsListLong[viewDateMonth]

            const [, end] = getRangeFromView(viewDateTs, View.SCHEDULE)
            const endDate = new Date(end)
            const endMonthIndex = endDate.getMonth()
            const endMonth = monthsListLong[endMonthIndex]
            const endYear = endDate.getFullYear()

            return `${currentMonth} - ${endMonth}${viewDateYear !== endYear ? (`, ${endYear}`) : ''}`
        }
        default: {
            return ''
        }
    }
}
