import React from 'react'

import {EventType} from '@bpinc/cal-request-urls'

import type {DayEventBlockProps} from './DayEventBlock'
import {DayMeeting} from './DayMeeting'
import {DayRequest} from './DayRequest'
import {DayShift} from './DayShift'


export type DayEventProps = Omit<
    DayEventBlockProps,
    'title' | 'className'
>

export const DayEvent: React.FC<DayEventProps> = props => {
    switch (props.item.data.type) {
        case EventType.REQUEST:
            return <DayRequest {...props} />
        case EventType.SHIFT:
            return <DayShift {...props} />
        default:
            return <DayMeeting {...props} />
    }
}
