import React from 'react'

import {useBalancesLoader} from '../../hooks/useBalancesLoader'
import {useTimeoffTypesLoader} from '../../hooks/useTimeoffTypesLoader'
import {Balances} from './Balances'
import {Table} from './Table'


export const Requests: React.FC = React.memo(() => {
    useTimeoffTypesLoader()
    useBalancesLoader()

    return (
        <div className="z-0 flex flex-1 flex-col">
            <Balances />
            <Table />
        </div>
    )
})
