import React from 'react'

import {SemanticButton} from '@bpinc/lib-ui-elements'


type CalendarSidebarButtonProps = {
    onToggle: () => void
    tooltip?: string
}

export const CalendarSidebarButton: React.FC<CalendarSidebarButtonProps> = React.memo(({
    onToggle,
    tooltip,
}) => {
    return (
        <SemanticButton
            basic
            size="small"
            icon="calendar"
            onClick={onToggle}
            tooltip={tooltip}
        />
    )
})
