import React from 'react'

import {ScheduleGrid} from './ScheduleGrid'


export const ViewSchedule: React.FC = () => {
    return (
        <>
            <div className="h-5 border border-solid border-[#eaf0f4] bg-[#f5f6fa]" />
            <div className="flex flex-1 flex-col">
                <div className="relative flex-1">
                    <div className="absolute left-0 top-0 h-full w-full overflow-y-auto overflow-x-hidden pr-[3px]">
                        <ScheduleGrid />
                    </div>
                </div>
            </div>
        </>
    )
}
