import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface Params {
    moreQueue?: number
    eventDetails?: CalendarEventDetails
}
export function useTitle({moreQueue, eventDetails}: Params) {
    const {t} = useLocalizationState()

    return (
        [eventDetails?.title, moreQueue ? t.GLOBAL.heading_calendarAlert_morePrefix(String(moreQueue)) : '']
            .filter(Boolean)
            .join(' ')
    )
}
