import type {FC} from 'react'
import React, {memo, useCallback, useMemo} from 'react'

import {useTimezonesState, useTimezonesStateMethods} from '@bpinc/ad-timezones-state-context'
import {getGMT} from '@bpinc/cal-utils'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {TimezoneDropdown} from './TimezoneDropdown'


/** Timezone Dropdown Toggle is used on Calendar page to select a secondary timezone */
export const TimezoneDropdownToggle: FC = memo(() => {
    const {
        allTimezones,
        localTimezone,
        secondaryTimezone,
        recentSecondaryTimezones,
    } = useTimezonesState()

    const {setSecondaryTimezone} = useTimezonesStateMethods()

    const {
        t: {CALENDAR},
    } = useLocalizationState()

    /** Stores secondary timezone and last used timezones */
    const handleTimezoneSelect = useCallback(
        (timezone: string) => {
            let recentTimezones = [...recentSecondaryTimezones]

            if (timezone !== localTimezone) {
                const idx = recentTimezones.indexOf(timezone)

                recentTimezones =
                    idx === -1
                        ? [timezone].concat(recentTimezones).slice(0, 3)
                        : recentTimezones.splice(idx, 1).concat(recentTimezones)
            }
            setSecondaryTimezone({timezone, recentTimezones})
        },
        [recentSecondaryTimezones, localTimezone, setSecondaryTimezone],
    )

    const isSecondaryTimezone = secondaryTimezone && secondaryTimezone !== localTimezone

    const secondaryTimezoneGMT = useMemo(() => {
        return getGMT(allTimezones.find(timezone => timezone.id === secondaryTimezone)?.fullName)
    }, [allTimezones, secondaryTimezone])

    const title = isSecondaryTimezone
        ? CALENDAR.timezone_local_secondary_title(localTimezone, secondaryTimezone)
        : CALENDAR.timezone_local_title(localTimezone)

    const ariaLabel = `${CALENDAR.aria_timezone_dropdown()} ${title}`

    const labelText = isSecondaryTimezone && secondaryTimezoneGMT ? secondaryTimezoneGMT : CALENDAR.timezone_local_name()

    return (
        <TimezoneDropdown
            text={labelText}
            ariaLabel={ariaLabel}
            className={tcn(
                'focus:outline first:[&>div]:truncate first:[&>div]:text-center first:[&>div]:align-middle',
                'first:[&>div]:text-sm first:[&>div]:text-primary-legacy first:[&>div]:underline',
            )}
            allTimezones={allTimezones}
            localTimezoneId={localTimezone}
            onSelectTimezone={handleTimezoneSelect}
            recentTimezoneIds={recentSecondaryTimezones}
        />
    )
})
