import type {FC} from 'react'
import React, {useRef} from 'react'

import type {EventMapItem} from '@bpinc/cal-state-types'
import {useBoolean} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Popup} from '@bpinc/lib-ui-elements'

import type {ViewMonthEventsProps} from '..'
import {ViewMonthMorePopup} from './ViewMonthMorePopup'


interface MoreEventsPopupOpenerProps extends
    Pick<ViewMonthEventsProps, 'day' | 'dragInfo' | 'dayOfWeek' | 'onDragStart' | 'onEventFormClose' | 'selectedEvent'> {
    children: ({toggle}: {toggle: () => void}) => JSX.Element
    events: EventMapItem[]
}

export const MoreEventsPopupOpener: FC<MoreEventsPopupOpenerProps> = ({
    children,
    day,
    events,
    selectedEvent,
    dragInfo,
    onDragStart,
    onEventFormClose,
    dayOfWeek,
}) => {
    const mountRef = useRef<HTMLDivElement>(null)
    const {value: show, toggle, setFalse: handleClose} = useBoolean()

    return (
        <div
            ref={mountRef}
            className="w-full [&>div>div]:!transform-none"
        >
            <Popup
                show={show}
                mountNode={mountRef.current}
                placement="top"
                contentClassName={tcn('z-[3] mt-px min-w-full rounded border-[#ddd]', {
                    'pointer-events-none opacity-30': dragInfo,
                })}
                onClickOutside={handleClose}
                content={
                    <ViewMonthMorePopup
                        day={day}
                        events={events}
                        selectedEvent={selectedEvent}
                        onEventFormClose={onEventFormClose}
                        onClose={handleClose}
                        dragInfo={dragInfo}
                        dayOfWeek={dayOfWeek}
                        onDragStart={onDragStart}
                    />
                }
            >
                {children({toggle})}
            </Popup>
        </div>
    )
}
